(function (ng) {
    'use strict';

    ng.module('pinchZoom', [
    ]).directive('pinchZoom', ['$window', '$timeout', 'utilService',
        function ($window, $timeout, utilService) {
            return {
                scope: {
                    maxScale: '@',
                    contain: '@',
                    disable: '@',
                    fullscreen: '@'
                },
                restrict: 'A',
                link: function ($scope, el, attrs) {
                    var opts = {
                        minScale: 1,
                        transition: true
                    },
                    win = ng.element($window),
                            fullscreenClasses = 'ea-fs ng-pinch-zoom-fullscreen',
                            ctFullscreenClass = 'ea-fs-ct',
                            resize = function () {
                                if (el.hasClass(fullscreenClasses)) {
                                    setTimeout(function () {
                                        el.panzoom('resetDimensions');
                                        el.panzoom('reset');
                                    }, 100);
                                }
                            },
                            onScroll = function (e) {
                                e.preventDefault();
                                var delta = e.delta || e.originalEvent.wheelDelta,
                                        zoomOut = delta ? delta < 0 : e.originalEvent.deltaY > 0;

                                el.panzoom('zoom', zoomOut, {
                                    increment: 0.1,
                                    animate: false,
                                    focal: e
                                });
                            };

                    if ($scope.maxScale)
                        opts.maxScale = $scope.maxScale;
                    if ($scope.contain)
                        opts.contain = $scope.contain === 'invert' && $scope.contain || Boolean($scope.contain);

                    el.addClass('ng-pinch-zoom');

                    el.panzoom(opts);

                    if (Boolean($scope.disable))
                        el.panzoom('disable');

                    if (Boolean($scope.fullscreen)) {
                        el.on('click', function (e) {
                            if (el.panzoom('isDisabled')) {
                                utilService.toggleFullScreenMode(true, false);

                                el
                                        .addClass(fullscreenClasses)
                                        .parent()
                                        .addClass(ctFullscreenClass);

                                el.on('mousewheel.focal', onScroll);
                                el.panzoom('enable');
                                el.panzoom('resetDimensions');
                            }
                        });

                        el.on('panzoomend', function (e, p, m, changed, nonExecutable) {
                            if (!changed && !nonExecutable) {
                                utilService.toggleFullScreenMode(false, false);

                                el
                                        .removeClass(fullscreenClasses)
                                        .parent()
                                        .removeClass(ctFullscreenClass);

                                el.off('mousewheel.focal', onScroll);
                                el.panzoom('reset');
                                var timer = $timeout(function () {
                                    $timeout.cancel(timer);
                                    el.panzoom('disable');
                                }, 0, false);
                            }
                        });
                    }

                    win.on('resize', resize);

                    $scope.$on('$destroy', function () {
                        win.off('resize', resize);
                    });
                }
            };
        }
    ]);
})(angular);