(function (ng, $) {
    'use strict';

    ng.module('MobileSite.controllers.Office.Home', [
    ]).controller('homeCtrl', ['$scope', '$route', 'CONFIG', 'sliderService',
        function ($scope, $route, CONFIG, sliderService) {
            var route = $route.current.$$route.originalPath,
                    slides = [{
                            txt: 'PUTTING THE SERVICE INTO SERVICED OFFICES',
                            btn: 'VIEW OFFICES',
                            path: 'property-for-rent'
                        }],
                    unbind = $scope.$on('loginChanged', function () {
                        setTimeout(function() {
                            var instance = $('#grid-cont' + sliderService.getCtClass(route, '.') + ' .propertyGallery').data('flexslider');
                            instance && $.flexslider.custom.updateSliderSize(instance, 0, 1);
                        }, 0);
                    });

            if (CONFIG.ps.incl_news == 1)
                slides.push({
                    txt: 'KEEP UP-TO-DATE WITH BE OFFICES',
                    btn: 'READ THE LATEST',
                    path: 'news'
                });

            if (CONFIG.ps.incl_events == 1)
                slides.push({
                    txt: 'WHAT EVENTS ARE HAPPENING?',
                    btn: 'VIEW CALENDAR',
                    path: 'events'
                });

            $scope.slides = slides;

            setTimeout(function () {
                sliderService.init('propertyGallery', 'home-ct');
            }, 0);

            $scope.$on('$destroy', function () {
                unbind();
                sliderService.destroy($route.current.$$route.originalPath);
            });
        }
    ]);

})(angular, jQuery); 