(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.Contact', [
    ]).controller('contactCtrl', ['$scope', '$window', '$location', '$timeout', '$filter', 'CONFIG', 'EAAPI', 'utilService', 'searchService', 'ngToast',
        function ($scope, $window, $location, $timeout, $filter, CONFIG, EAAPI, utilService, searchService, ngToast) {
            ng.extend($scope, {
                hTitle: CONFIG.contactUsPageTitle || 'Contact Us',
                fullScreenMap: false,
                hideMAD: CONFIG.getAgentAppCfg('hideContactUsAddressDirections'),
                canAddContact: $scope.isMobile && !!$window.navigator.contacts
            });

            var getMapUrl = function (lat, lng, zoom, size, scale) {
                var opt = {
                    key: CONFIG.google.apiKey,
                    center: lat + ',' + lng,
                    zoom: zoom || 12,
                    size: size || '100x100',
                    scale: scale || 2,
                    sensor: false,
                    markers: lat + ',' + lng
                };
                return CONFIG.google.statisMapUrl + utilService.httpBuildQuery(opt);
            },
                    isSaleOrRent = function (type) {
                        var t = 'incl_property_';
                        return CONFIG.ps[t + type] == 1 || CONFIG.ps[t + 'commercial_' + type] == 1;
                    },
                    promises = [EAAPI.getBranches()],
                    setTime = function (date, time) {
                        var parsedTime = time.split(':');

                        date.setHours(~~parsedTime[0]);
                        date.setMinutes(~~parsedTime[1]);
                    };

            if (!CONFIG.isCalculatorApp && CONFIG.ps.incl_team == 1)
                promises.push(EAAPI.getTeam());

            utilService.loadAsyncData(promises)
                    .then(function (resps) {
                        var branches = resps[0],
                                i = 0,
                                l = branches.length,
                                branch;

                        for (; i < l; ++i) {
                            branch = branches[i];

                            branch.mapUrl = getMapUrl(branch.latitude, branch.longitude);

                            if (!CONFIG.isCalculatorApp) {
                                branch.isSale = branch.types.find(function (item) {
                                    // Sale or Commercial Sale
                                    return [1, 4].indexOf(parseInt(item.id, 10)) >= 0 && isSaleOrRent('sales');
                                });

                                branch.isRent = branch.types.find(function (item) {
                                    // Rent or Commercial Rent
                                    return [2, 5].indexOf(parseInt(item.id, 10)) >= 0 && isSaleOrRent('rent');
                                });

                                // Member
                                if (resps[1])
                                    branch.hasMember = resps[1].team.filter(function (item) {
                                        return item.branch_name && item.branch_name.toLowerCase() === branch.name.toLowerCase();
                                    }).length;
                            }

                            // Opening Times
                            var bots = branch.opening_times,
                                    l2 = bots.length;

                            if (l2) {
                                branch.ots = [];

                                var ots = [],
                                        j = 0;

                                for (; j < l2; ++j)
                                    if (bots[j].opening_time !== 'Closed') {
                                        var lastOts = ots[ots.length - 1];

                                        if (!lastOts || (lastOts[0].opening_time !== bots[j].opening_time || lastOts[0].closing_time !== bots[j].closing_time))
                                            ots.push([bots[j]]);
                                        else
                                            lastOts.push(bots[j]);
                                    }

                                l2 = ots.length;
                                for (j = 0; j < l2; ++j) {
                                    var start = new Date(),
                                            end = new Date();

                                    setTime(start, ots[j][0].opening_time);
                                    setTime(end, ots[j][0].closing_time);

                                    branch.ots.push({
                                        weekdays: ots[j][0].weekday.slice(0, 3) + (ots[j].length > 1 && (' - ' + ots[j][ots[j].length - 1].weekday.slice(0, 3)) || ''),
                                        times: ($filter('date')(start, 'h:mma') + ' - ' + $filter('date')(end, 'h:mma')).toLowerCase()
                                    });
                                }
                            }
                        }
                        
                        $scope.$evalAsync(function () {
                            $scope.branches = branches;
                        });
                    });

            ng.extend($scope, {
                getAddress: function (branch) {
                    return [branch.address1, branch.address2, branch.town, branch.postcode].filter(function (i) {
                        return !!i;
                    }).join(', ');
                },
                onMapClick: function (lat, lng) {
                    $scope.fullScreenMap = !$scope.fullScreenMap;
                    utilService.toggleFullScreenMode($scope.fullScreenMap);

                    var m = Math.max($window.innerWidth, $window.innerHeight);
                    $scope.fullScreenMapUrl = lat && lng ? getMapUrl(lat, lng, 15, m + 'x' + m, 1) : ' ';
                },
                search: function (type, l) {
                    var filter = searchService.getFilter({
                        location: l,
                        goal: utilService.getSalesTypes().filter(function (i) {
                            return i.type === type;
                        })[0].goal
                    });

                    searchService.setLastFilter(filter);

                    if (type === 'commercial_sales')
                        type = 'sale';
                    else if (type === 'commercial_rent')
                        type = 'rent';

                    $location.path('/property-for-' + type);
                }
            });

            if ($scope.canAddContact)
                $scope.onAddContact = function (i) {
                    var branch = $scope.branches[i];
                    if (branch) {
                        var contact = $window.navigator.contacts.create({"displayName": branch.name}),
                                name = new ContactName(),
                                phoneNumbers = [],
                                emails = [],
                                addresses = [],
                                urls = [],
                                onSuccess = function () {
                                    var timer = $timeout(function () {
                                        $timeout.cancel(timer);
                                        ngToast.create(CONFIG.MESSAGE.M0002);
                                    }, 1);
                                },
                                onError = function (error) {
                                    var msg = CONFIG.ERROR.E0009;

                                    switch (error.code) {
                                        case error.PERMISSION_DENIED_ERROR:
                                            msg = CONFIG.ERROR.E0010;
                                            break;
                                        case error.TIMEOUT_ERROR:
                                            msg = CONFIG.ERROR.E0011;
                                            break;
                                    }

                                    ngToast.create(msg);
                                };

                        if ($scope.isIOS)
                            contact.id = branch.name + 'eaApp';

                        name.givenName = branch.name;
                        contact.name = name;

                        if (branch.telephone) {
                            phoneNumbers[0] = new ContactField('work', branch.telephone, true);
                            contact.phoneNumbers = phoneNumbers;
                        }

                        if (branch.email) {
                            emails[0] = new ContactField('work', branch.email, true);
                            contact.emails = emails;
                        }

                        if (branch.address1) {
                            addresses[0] = new ContactAddress(true, 'work', $scope.getAddress(branch), branch.address1, branch.town, null, branch.postcode, branch.country);
                            contact.addresses = addresses;
                        }

                        if (branch.website) {
                            urls[0] = new ContactField('work', branch.website, true);
                            contact.urls = urls;
                        }

                        contact.save(onSuccess, onError);
                    }
                };
        }
    ]);
})(angular); 