(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.Share', [
    ]).controller('shareCtrl', ['$scope', '$routeParams', '$sessionStorage', 'CONFIG', 'formService',
        function ($scope, $routeParams, $sessionStorage, CONFIG, formService) {
            var eaLogin = $sessionStorage.eaLogin && $sessionStorage.eaLogin[CONFIG.ps.agent_id] || {};

            // Init scope variables
            ng.extend($scope, {
                hTitle: 'Share with a Friend',
                model: {
                    property_id: $routeParams.pid,
                    name: eaLogin.user_fullname || '',
                    email: eaLogin.user_email || ''
                },
                property: {}
            });

            // Init scope functions
            ng.extend($scope, {
                onChangeEmail: function () {
                    $scope.shareF.email.$errorMsg = null;
                },
                onSubmit: function (isValid) {
                    formService.onSubmit($scope, isValid, 'share', $scope.model, 'shareF', 'M0012', 'self');
                }
            });

            formService.AVS($scope, $routeParams.pid);
        }
    ]);

})(angular); 