(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.LandlordSettings', [])
            .controller('llSetCtrl', ['$scope', '$location', 'EAAPI',
                function ($scope, $location, EAAPI) {

                    // Init scope variables
                    $scope.model = {};

                    // Init scope functions
                    ng.extend($scope, {
                        onChangeEmail: function () {
                            $scope.llSetF.email.$errorMsg = null;
                        },
                        onSubmit: function (isValid) {
                            if (isValid) {
                                var model = $scope.model,
                                        args = {
                                            set: true,
                                            email: model.LL_Email,
                                            firstname: model.LL_FirstName,
                                            surname: model.LL_Surname,
                                            phone: model.LL_Telephone,
                                            mobile: model.LL_Mobile,
                                            address: model.LL_Address
                                        };

                                EAAPI.getLLSettings(args, true, model)
                                        .then(function (resp) {
                                            if (resp && resp.update)
                                                $location.path('landlord/properties');
                                        });
                            }
                        }
                    });

                    EAAPI.getLLSettings()
                            .then(function (resp) {
                                if (resp)
                                    $scope.model = resp;
                            });
                }
            ]);
})(angular);