(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.Notifications', [
    ]).controller('notiCtrl', ['$scope', 'EAAPI', 'CONFIG', 'notiService',
        function ($scope, EAAPI, CONFIG, notiService) {
            notiService.clear();

            EAAPI.getNotifications()
                    .then(function (resp) {
                        if (resp) {

                            var anchorEl = '<a href="{:href}" class="external-link">{:text}</a>',
                                    regex = CONFIG.regex,
                                    i = 0,
                                    l = resp.length,
                                    truncate = function (text, breakPoints) {
                                        if (text.length > breakPoints[0] + breakPoints[1]) {

                                            var firstPart = text.substr(0, breakPoints[0]),
                                                    secondPart = text.substr(text.length - breakPoints[1]),
                                                    breakers = ['/', '?', '&'],
                                                    l = breakers.length,
                                                    i,
                                                    index = breakPoints[0],
                                                    findFirstBreakerLastIndex = function (textPart, breaker, offset) {

                                                        var curIndex;

                                                        for (i = 0; i < l; ++i) {

                                                            if ((breaker === breakers[i] && offset || 0) < (curIndex = textPart.lastIndexOf(breakers[i]))) {
                                                                index = curIndex;
                                                                break;
                                                            }
                                                        }
                                                    };

                                            findFirstBreakerLastIndex(firstPart, '/', 8);
                                            firstPart = firstPart.substr(0, index + (i === 0));

                                            index = 0;

                                            findFirstBreakerLastIndex(secondPart);
                                            secondPart = secondPart.substr(index);

                                            text = firstPart + "..." + secondPart;
                                        }

                                        return text;
                                    };

                            for (; i < l; ++i) {

                                // Reset regex state
                                regex.emailUrl.lastIndex = 0;

                                // Convert notification message
                                resp[i].message = resp[i].message.replace(regex.emailUrl, function (match, isEmail, isUrl, matchIndex, matchText) {

                                    var containsSheme = regex.scheme.test(match),
                                            lastCharToJoin = "";

                                    if (isUrl) {

                                        // Skip ftp urls
                                        if (match.indexOf('ftp:') === 0) {
                                            return match;
                                        }

                                        // There is no valid scheme (http://, https://) in the match string,
                                        // check for invalid strings before the url
                                        if (!containsSheme) {

                                            var str = matchText.substr(matchIndex - 1, 1);

                                            if (
                                                    '/' === str ||
                                                    '//' === (str = matchText.substr(matchIndex - 2, 1)) ||
                                                    ':/' === str ||
                                                    '://' === matchText.substr(matchIndex - 3, 1)
                                                    ) {
                                                
                                                return match;
                                            }
                                        }
                                        
                                        // Check matched url last character is word character,
                                        // when not, then cut from the matched string
                                        // and join to the anchor
                                        var lastChar = match[match.length - 1];
                                        if (!/\w/.test(lastChar)) {
                                            match = match.slice(0, -1);
                                            lastCharToJoin = lastChar;
                                        }
                                    }

                                    return anchorEl
                                            .replace('{:href}', containsSheme && match || (isEmail && 'mailto:' || 'http://') + match)
                                            .replace('{:text}', (match = match.replace(regex.scheme, '')) && isEmail && match || truncate(match, [35, 15])) + lastCharToJoin;
                                });
                            }
                        }

                        ng.extend($scope, {
                            notifications: resp,
                            to: $scope.isOffice && 'home' || 'search'
                        });

                        $scope.toUcFirst = $scope.to.ucfirst();
                    });
        }
    ]);

})(angular); 