(function (ng) {
    'use strict';

    ng.module('angucomplete', [
    ]).directive('angucomplete', ['$http', '$sce', '$timeout',
        function ($http, $sce, $timeout) {
            return {
                restrict: 'EA',
                scope: {"id": "@id",
                    "placeholder": "@placeholder",
                    "selectedObject": "=selectedobject",
                    "initialValue": "=initialvalue",
                    "url": "@url",
                    "dataField": "@datafield",
                    "titleField": "@titlefield",
                    "titleSeparator": "@titleseparator",
                    "descriptionField": "@descriptionfield",
                    "imageField": "@imagefield",
                    "imageUri": "@imageuri",
                    "inputClass": "@inputclass",
                    "userPause": "@pause",
                    "localData": "=localdata",
                    "searchFields": "@searchfields",
                    "minLengthUser": "@minlength",
                    "matchClass": "@matchclass"
                }, template: '<div class="angucomplete-holder"><input id="{{id}}" ng-model="searchStr" type="text" placeholder="{{placeholder}}" class="{{inputClass}}" onmouseup="this.select();" ng-focus="resetHideResults()" ng-blur="hideResults()" autocomplete="off" /><div id="{{id}}_dropdown" class="angucomplete-dropdown" ng-if="showDropdown"><div class="angucomplete-searching" ng-show="searching">Searching...</div><div class="angucomplete-searching" ng-show="!searching && (!results || results.length == 0)">No results found</div><div class="angucomplete-row" ng-repeat="result in results" ng-mousedown="selectResult(result)" ng-mouseover="hoverRow()" ng-class="{\'angucomplete-selected-row\': $index == currentIndex}"><div ng-if="imageField" class="angucomplete-image-holder"><img ng-if="result.image && result.image != \'\'" ng-src="{{result.image}}" class="angucomplete-image"/><div ng-if="!result.image && result.image != \'\'" class="angucomplete-image-default"></div></div><div class="angucomplete-title" ng-if="matchClass" ng-bind-html="result.title"></div><div class="angucomplete-title" ng-if="!matchClass">{{ result.title }}</div><div ng-if="result.description && result.description != \'\'" class="angucomplete-description">{{result.description}}</div></div></div></div>',
                link: function ($scope, elem) {
                    /*var unbindInitialValue = $scope.$watch('initialValue', function(newval) {
                     if (newval) {
                     unbindInitialValue();
                     if (typeof newval === 'object' && newval && newval.title) {
                     $scope.searchStr = newval.title;
                     } else if (typeof newval === 'string' && newval.length > 0) {
                     $scope.searchStr = newval;
                     }
                     }
                     });*/
                    var inputField = elem.find('input'),
                            unbind = $scope.$watch('selectedObject', function (newval) {
                                if ($scope.pauseSelectedObjectWatcher)
                                    return $scope.pauseSelectedObjectWatcher = false;

                                if (ng.isDefined(newval)) {
                                    if (typeof newval === 'object' && newval && ng.isDefined(newval.title)) {
                                        $scope.searchStr = newval.title;
                                    } else if (typeof newval === 'string') {
                                        $scope.searchStr = newval;
                                    }
                                }
                            }),
                            isNewSearchNeeded = function (newTerm, oldTerm) {
                                return newTerm.length >= $scope.minLength && newTerm != oldTerm
                            },
                            processResults = function (responseData, str) {
                                if (responseData && responseData.length > 0) {
                                    $scope.results = [];
                                    var titleFields = [];
                                    if ($scope.titleField && $scope.titleField != "") {
                                        titleFields = $scope.titleField.split(",");
                                    }

                                    for (var i = 0; i < responseData.length; i++) {
                                        // Get title variables
                                        var titleCode = [];

                                        for (var t = 0; t < titleFields.length; t++) {
                                            titleCode.push(responseData[i][titleFields[t]]);
                                        }

                                        var description = "";
                                        if ($scope.descriptionField) {
                                            description = responseData[i][$scope.descriptionField];
                                        }

                                        var imageUri = "";
                                        if ($scope.imageUri) {
                                            imageUri = $scope.imageUri;
                                        }

                                        var image = "";
                                        if ($scope.imageField) {
                                            image = imageUri + responseData[i][$scope.imageField];
                                        }

                                        var text = titleCode.join(($scope.titleSeparator || '') + ' ');
                                        if ($scope.matchClass) {
                                            var re = new RegExp(str, 'i');
                                            var strPart = text.match(re)[0];
                                            text = $sce.trustAsHtml(text.replace(re, '<span class="' + $scope.matchClass + '">' + strPart + '</span>'));
                                        }

                                        var resultRow = {
                                            title: text,
                                            description: description,
                                            image: image,
                                            originalObject: responseData[i]
                                        }

                                        $scope.results[$scope.results.length] = resultRow;
                                    }

                                } else {
                                    $scope.results = [];
                                }
                            },
                            searchTimerComplete = function (str) {
                                // Begin the search

                                if (str.length >= $scope.minLength) {
                                    if ($scope.localData) {
                                        var searchFields = $scope.searchFields.split(",");

                                        var matches = [];
                                        for (var i = 0; i < $scope.localData.length; i++) {
                                            var match = false;

                                            for (var s = 0; s < searchFields.length; s++) {
                                                match = match || (typeof $scope.localData[i][searchFields[s]] === 'string' && typeof str === 'string' && $scope.localData[i][searchFields[s]].toLowerCase().indexOf(str.toLowerCase()) >= 0);
                                            }

                                            if (match) {
                                                matches[matches.length] = $scope.localData[i];
                                            }
                                        }

                                        $scope.searching = false;
                                        processResults(matches, str);

                                    } else {
                                        $http.get($scope.url + str, {}).
                                                success(function (responseData, status, headers, config) {
                                                    $scope.searching = false;
                                                    processResults((($scope.dataField) ? responseData[$scope.dataField] : responseData), str);
                                                }).
                                                error(function (data, status, headers, config) {
                                                    //console.log("error");
                                                });
                                    }
                                }
                            },
                            hideResults = function () {
                                $scope.hideTimer = $timeout(function () {
                                    $scope.showDropdown = false;
                                }, $scope.pause);
                            },
                            resetHideResults = function () {
                                if ($scope.hideTimer) {
                                    $timeout.cancel($scope.hideTimer);
                                }
                            },
                            hoverRow = function (index) {
                                $scope.currentIndex = index;
                            },
                            keyPressed = function (event) {
                                if (!(event.which == 38 || event.which == 40 || event.which == 13)) {
                                    if (!$scope.searchStr || $scope.searchStr == "") {
                                        $scope.showDropdown = false;
                                        $scope.lastSearchTerm = null
                                    } else if (isNewSearchNeeded($scope.searchStr, $scope.lastSearchTerm)) {
                                        $scope.lastSearchTerm = $scope.searchStr
                                        $scope.showDropdown = true;
                                        $scope.currentIndex = -1;
                                        $scope.results = [];

                                        if ($scope.searchTimer) {
                                            $timeout.cancel($scope.searchTimer);
                                        }

                                        $scope.searching = true;
                                        $scope.searchTimer = $timeout(function () {
                                            searchTimerComplete($scope.searchStr);
                                        }, $scope.pause);
                                    }
                                } else {
                                    event.preventDefault();
                                }
                            },
                            selectResult = function (result) {
                                if ($scope.matchClass) {
                                    result.title = result.title.toString().replace(/(<([^>]+)>)/ig, '');
                                }
                                $scope.searchStr = $scope.lastSearchTerm = result.title;
                                $scope.pauseSelectedObjectWatcher = true;
                                $scope.selectedObject = result;
                                $scope.showDropdown = false;
                                $scope.results = [];
                            },
                            onKeyUp = function (event) {
                                if (event.which === 40) {
                                    if ($scope.results && ($scope.currentIndex + 1) < $scope.results.length) {
                                        $scope.currentIndex++;
                                        $scope.$apply();
                                        event.preventDefault;
                                        event.stopPropagation();
                                    }

                                    $scope.$apply();
                                } else if (event.which == 38) {
                                    if ($scope.currentIndex >= 1) {
                                        $scope.currentIndex--;
                                        $scope.$apply();
                                        event.preventDefault;
                                        event.stopPropagation();
                                    }
                                } else if (event.which == 13) {
                                    if ($scope.results && $scope.currentIndex >= 0 && $scope.currentIndex < $scope.results.length) {
                                        $scope.selectResult($scope.results[$scope.currentIndex]);
                                        $scope.$apply();
                                        event.preventDefault;
                                        event.stopPropagation();
                                    } else {
                                        $scope.results = [];
                                        $scope.$apply();
                                        event.preventDefault;
                                        event.stopPropagation();
                                    }
                                } else if (event.which == 27) {
                                    $scope.results = [];
                                    $scope.showDropdown = false;
                                    $scope.$apply();
                                } else if (event.which == 8) {
                                    $scope.pauseSelectedObjectWatcher = true;
                                    $scope.selectedObject = null;
                                    $scope.$apply();
                                }
                            };

                    ng.extend($scope, {
                        lastSearchTerm: null,
                        currentIndex: null,
                        justChanged: false,
                        searchTimer: null,
                        hideTimer: null,
                        searching: false,
                        pause: 500,
                        minLength: 3,
                        searchStr: null,
                        pauseSelectedObjectWatcher: false
                    });

                    if ($scope.minLengthUser && $scope.minLengthUser != "")
                        $scope.minLength = $scope.minLengthUser;

                    if ($scope.userPause)
                        $scope.pause = $scope.userPause;

                    ng.extend($scope, {
                        hideResults: hideResults,
                        resetHideResults: resetHideResults,
                        hoverRow: hoverRow,
                        selectResult: selectResult
                    });


                    inputField.on('keyup', keyPressed);
                    elem.on("keyup", onKeyUp);

                    $scope.$on('$destroy', function () {
                        unbind();

                        inputField.off('keyup', keyPressed);
                        elem.off("keyup", onKeyUp);
                    });
                }
            };
        }
    ]);
})(angular);