(function (ng, $) {
    'use strict';

    ng.module('MobileSite.services.Slider', [
    ]).service('sliderService', ['$rootScope', '$window', '$timeout', '$location', 'utilService', 'platform',
        function ($rootScope, $window, $timeout, $location, utilService, platform) {
            var me = this;

            me.getCtClass = function (route, insertFirst) {
                var ct = route.indexOf('/:pid') >= 0 && ' ' ||
                        route.indexOf('landlord') >= 0 && 'landlord' ||
                        route.indexOf('favourites') >= 0 && 'favourites-ct' ||
                        route.indexOf('property-for-') >= 0 && 'results-ct' ||
                        route.indexOf('presentation') >= 0 && 'pres-ct' ||
                        route.indexOf('home') >= 0 && 'home-ct' ||
                        '';

                return ct.length > 1 && insertFirst && insertFirst + ct || ct;
            };

            me.init = function (id, ct, fs) {
                var sliderTarget = $((ct && '.' + ct + ' ' || '') + '#' + (id || 'propertyGallery')),
                        isPres = ct === 'pres-ct',
                        isHome = ct === 'home-ct',
                        slider = sliderTarget.flexslider({
                            selector: "ul.slides > li", //{NEW} Selector: Must match a simple pattern. '{container} > {slide}' -- Ignore pattern at your own peril
                            animation: "slide", //String: Select your animation type, "fade" or "slide"
                            slideshow: !id || isPres || isHome, //Boolean: Animate slider automatically

                            // Usability features
                            pauseOnAction: false, //Boolean: Pause the slideshow when interacting with control elements, highly recommended.

                            // Primary Controls
                            controlNav: isHome, //Boolean: Create navigation for paging control of each slide? Note: Leave true for manualControls usage
                            directionNav: !isPres && !isHome, //Boolean: Create navigation for previous/next navigation? (true/false)
                            prevText: "", //String: Set the text for the "previous" directionNav item
                            nextText: "", //String: Set the text for the "next" directionNav item

                            // Carousel Options
                            itemMargin: 10, //{NEW} Integer: Margin between carousel items.

                            //Custom
                            scaleRatio: ct === 'landlord' && (id && 0.3 || 0.35) || !isPres && !isHome && 0.5 || 0,
                            preloadCnt: (isPres || isHome) && 5 || 1,
                            removeItemOnFail: !isHome
                        }).data('flexslider');

                if (fs && slider)
                    slider.vars.slideClick = function () {
                        slider.fullscreen = !slider.fullscreen;
                        slider[(slider.fullscreen && 'add' || 'remove') + 'Class']('flex-fullscreen');
                        utilService.toggleFullScreenMode(slider.fullscreen, true);

                        var s = $window.screen;
                        if (!platform.ios && $window.screen.lockOrientation)
                            if (s.lockOrientation)
                                if (slider.fullscreen)
                                    s.lockOrientation('landscape');
                                else
                                    s.unlockOrientation();

                        var timer = $timeout(function () {
                            $timeout.cancel(timer);
                            $.flexslider.custom.updateSliderSize(slider, 0, 1);
                        }, 0);
                    };
            };

            me.initSlider = function (id, pid, route) {
                var timer = $timeout(function () {
                    $timeout.cancel(timer);
                    var ct = me.getCtClass(route);

                    me.init(id, ct);

                    var instance = $('#grid-cont' + (ct.length && '.' + ct || '')).find('#' + id).data('flexslider');

                    if (instance)
                        instance.vars.slideClick = function () {
                            if (!$window.scrolling)
                                $rootScope.$apply(function () {
                                    $location.search({}).path(route + '/' + pid);
                                });

                            $window.scrolling = false;
                        };
                }, 0, false);
            };

            me.destroy = function (route, id) {
                $('#grid-cont' + me.getCtClass(route, '.') + (id && ' .item[data-pid=' + id + ']' || '') + ' .propertyGallery').each(function (i, item) {
                    var instance = $(item).data('flexslider');
                    instance && instance.destroy();
                });
            };
        }]);
})(angular, jQuery);