(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.StampDuty', [
    ]).controller('stampDutyCtrl', ['$scope', '$location', 'CONFIG', 'EAAPI', 'searchService', 'utilService',
        function ($scope, $location, CONFIG, EAAPI, searchService, utilService) {
            var sdd = searchService.getStampDutyData(),
                    decodeSDInfo = function () {
                        $scope.info = utilService.decodeHtml(CONFIG.ps.text_stampdutyinfo);
                    },
                    unbind = $scope.$on('appPropertySettingsUpdated', decodeSDInfo),
                    getFilter = function (prices) {
                        var plusMinusVal = ng.isNumber(CONFIG.stampDutySaleAroudPlusMinusValue) ? CONFIG.stampDutySaleAroudPlusMinusValue : 100000,
                                pmin = $scope.model.price - plusMinusVal,
                                pmax = $scope.model.price + plusMinusVal;

                        return searchService.getFilter({
                            goal: 1,
                            pmin: pmin <= 0 ? '0' : prices.min.filter(function (i, index) {
                                return prices.min[index + 1] ? i.id <= pmin && pmin < prices.min[index + 1].id : true;
                            })[0].id,
                            pmax: pmax >= 100000000 ? '100000000' : prices.max.filter(function (i, index) {
                                return i.id >= pmax && pmax > prices.max[index - 1].id;
                            })[0].id
                        });
                    };

            // Init scope variables
            ng.extend($scope, {
                hTitle: 'Stamp Duty Calculator',
                model: {
                    price: sdd.price || '',
                    firstBuyer: false,
                    secHome: false,
                    duty: 0,
                    effective: 0,
                    duties: []
                }
            });

            // Init scope functions
            ng.extend($scope, {
                onChange: function () {
                    $scope.showProps = 0;

                    //40000 is a threshold, dont calculate below 40000
                    if (true === ($scope.show = $scope.model.price >= 40000)) {
                        ng.extend($scope.model, utilService.calcStampDuty($scope.model.price, $scope.model.firstBuyer, $scope.model.secHome));

                        if (!CONFIG.isCalculatorApp && $scope.model.duty) {
                            EAAPI.getPrices()
                                    .then(function (prices) {
                                        EAAPI.getProperties(getFilter(prices))
                                                .then(function (resp) {
                                                    $scope.showProps = resp.nrofprops > 0;
                                                });
                                    });
                        }
                    }
                },
                search: function () {
                    EAAPI.getPrices()
                            .then(function (prices) {
                                searchService.setLastFilter(getFilter(prices));
                                $location.path('/property-for-sale');
                            });
                }
            });

            decodeSDInfo();

            if (sdd.pid) {
                $scope.onChange();
                searchService.setStampDutyData({});

                $scope.hBackUrl = '#' + (utilService.backTo || ('/property-for-' + ([1, 4].indexOf(parseInt(sdd.sr, 10)) >= 0 ? 'sale' : 'rent') + '/' + sdd.pid));
                utilService.backTo = 0;
            }

            $scope.$on('$destroy', unbind);
        }
    ]);

})(angular); 