(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.Error', [
    ]).controller('errorCtrl', ['$scope', 'CONFIG', 'utilService',
        function ($scope, CONFIG, utilService) {
            var key = utilService.getErrorMsgKey() || 'E0004',
                    btnTxt = 'search',
                    path = '';
            
            utilService.setErrorMsgKey(null);

            if ($scope.isOffice)
                btnTxt = 'home';
            else if (CONFIG.isCalculatorApp) {
                path = 'calc-home';
                btnTxt = 'home';
            }
            else if (!$scope.isLoggedIn)
                path = btnTxt = 'login';

            ng.extend($scope, {
                hTitle: 'Ooops, we have a problem...',
                message: CONFIG.ERROR[key],
                btn: btnTxt,
                path: path
            });
        }
    ]);

})(angular); 