(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.Reviews', [
    ]).controller('reviewsCtrl', ['$scope', '$filter', 'EAAPI', 'cUtil',
        function ($scope, $filter, EAAPI, cUtil) {
            var RAAPI = $scope.cfg.raterAgentApi;

            $scope.hTitle = 'Reviews';

            //Get reviews
            EAAPI.get('parseXml', {
                url: RAAPI.url + $scope.cfg.ps.rateragent_apikey
            }).then(function (resp) {
                var reviews = [],
                        i = 0,
                        l = (resp && resp.branches && resp.branches.branch && resp.branches.branch.length) || 0,
                        sort = function (a, b) {
                            var a1 = a.review_date.replace(/-|:/g, ' ').split(' '),
                                    b1 = b.review_date.replace(/-|:/g, ' ').replace(':', ' ').split(' ');

                            return new Date(+b1[0], +b1[1] - 1, +b1[2], +b1[3], +b1[4], +b1[5]).getTime() - new Date(+a1[0], +a1[1] - 1, +a1[2], +a1[3], +a1[4], +a1[5]).getTime();
                        },
                        filter = function (i) {
                            return i.rating >= 4;
                        },
                        map = function (i) {
                            i.commentT = $filter('truncate')(i.comment);
                            i.revDateF = $filter('postDate')(i.review_date);
                            
                            return i;
                        };

                for (; i < l; ++i)
                    reviews.push.apply(reviews, resp.branches.branch[i].reviews.review);

                $scope.reviews = reviews
                        .filter(filter)
                        .sort(sort)
                        .slice(0, RAAPI.reviewCount || 10)
                        .map(map);
            });

            //Get/Download star images
            var starOnImgName = RAAPI.starOn && RAAPI.starOn.split('/').slice(-1)[0],
                    starOffImgName = RAAPI.starOff && RAAPI.starOff.split('/').slice(-1)[0],
                    ns1, ns2,
                    appUpdateImages = function () {
                        if (typeof starOnImgName === 'string') {
                            ns1 = 'reviews_on';
                            cUtil.getFile(starOnImgName, RAAPI.starOn, ns1, $scope.imagesDownloaded[ns1], function (img, downloaded) {
                                $scope.$evalAsync(function () {
                                    if (downloaded)
                                        $scope.imagesDownloaded[ns1] = downloaded;
                                    $scope.starOnImg = img;
                                });
                            });
                        }

                        if (typeof starOffImgName === 'string') {
                            ns2 = 'reviews_off';
                            cUtil.getFile(starOffImgName, RAAPI.starOff, ns2, $scope.imagesDownloaded[ns2], function (img, downloaded) {
                                $scope.$evalAsync(function () {
                                    if (downloaded)
                                        $scope.imagesDownloaded[ns2] = downloaded;
                                    $scope.starOffImg = img;
                                });

                            });
                        }
                    }, unbind = $scope.$on('appUpdateImages', appUpdateImages);

            appUpdateImages();
            $scope.$on('$destroy', unbind);
        }
    ]);

})(angular); 