(function (ng) {
    'use strict';

    ng.module('MobileSite.services.openFB', [
    ]).service('openFBService', ['$window', '$location', '$q', '$sessionStorage', 'CONFIG', 'authService',
        function ($window, $location, $q, $sessionStorage, CONFIG, authService) {
            var me = this,
                    aId = CONFIG.ps.agent_id;

            me.ready = function () {
                return $window.openFB.ready();
            };

            me.init = function (params) {
                return $window.openFB.init(params);
            };

            me.login = function (options) {
                var deferred = $q.defer();

                $window.openFB.login(function (result) {
                    deferred[result.status === "connected" && 'resolve' || 'reject'](result);
                }, options);

                return deferred.promise;
            };

            me.logout = function (accessToken) {
                var deferred = $q.defer();

                $window.openFB.logout(accessToken, function () {
                    deferred.resolve();
                });

                return deferred.promise;
            };

            me.api = function (obj, redirect) {
                var deferred = $q.defer();

                if (ng.isUndefined(redirect))
                    redirect = true;

                if (!obj.params)
                    obj.params = {};

                obj.params.access_token = obj.params.access_token || $sessionStorage.eaLogin[aId].accessToken;

                obj.success = function (result) {
                    deferred.resolve(result);
                };
                obj.error = function (error) {
                    // Error codes:
                    // 102 - Session error
                    // 190 - Access token expired
                    // see: https://developers.facebook.com/docs/graph-api/using-graph-api/#errors
                    if (error.code && (error.code == 102 || error.code == 190)) {
                        authService.onLogout();

                        if (redirect)
                            $location.path('/login');
                    }

                    deferred.reject(error);
                };

                $window.openFB.api(obj);

                return deferred.promise;
            };

            me.getLoginStatus = function (accessToken) {
                var deferred = $q.defer(),
                        ssEaLogin = $sessionStorage.eaLogin;

                if (!accessToken && ssEaLogin && ssEaLogin[aId] && ssEaLogin[aId].accessToken)
                    accessToken = ssEaLogin[aId].accessToken;

                if (accessToken) {
                    me.api({
                        path: '/debug_token',
                        params: {
                            input_token: accessToken,
                            access_token: accessToken
                        }
                    }, false).then(function (resp) {
                        deferred.resolve(resp);
                    }, function () {
                        deferred.reject();
                    });
                } else
                    deferred.reject();

                return deferred.promise;
            };
        }
    ]);
})(angular);