(function (ng) {
    'use strict';

    ng.module('MobileSite.services.Search', [
    ]).service('searchService', ['$rootScope', '$q', '$timeout', '$http', 'CONFIG', 'utilService', 'ngToast',
        function ($rootScope, $q, $timeout, $http, CONFIG, utilService, ngToast) {
            var me = this,
                    exclude = CONFIG.getAgentAppCfg('excludeMenuDefaultValue'),
                    defFilter = {
                        location: '',
                        multiple_locations: 0,
                        bd: 0,
                        goal: $rootScope.isCommercial ? 4 : 1,
                        type: '0',
                        exclude: ng.isNumber(exclude) && exclude <= 1 ? exclude : 1,
                        pmax: '100000000',
                        pmin: '0',
                        from: 0,
                        to: CONFIG.itemPerPage,
                        sortprice: 'desc',
                        sortaddress: 'asc'
                    },
                    lastFilter,
                    location,
                    nearMeLocation,
                    selectedLocations = [],
                    needMainPageFilter = false,
                    needAlertFilter = false,
                    isMapView = false,
                    stampDutyData = {},
                    page = 1;

            me.geoLocOpts = {
                maximumAge: 0,
                timeout: 10000,
                enableHighAccuracy: false
            };

            me.getFilter = function (filter, last) {
                filter = ng.isObject(filter) ? filter : {};
                last = last || false;

                var ma = [{}, defFilter, filter];

                if (last)
                    ma.push(me.getLastFilter() || {});

                return ng.merge.apply(null, ma);
            };

            me.setLastFilter = function (filter) {
                lastFilter = filter;
            };
            me.getLastFilter = function () {
                return lastFilter;
            };

            me.setPredLocation = function (loc) {
                location = loc;
            };
            me.getPredLocation = function () {
                return location;
            };

            me.setNearMeLocation = function (loc) {
                nearMeLocation = loc;
            };
            me.getNearMeLocation = function () {
                return nearMeLocation;
            };

            me.setSelectedLocations = function (locs) {
                selectedLocations = locs;
            };
            me.getSelectedLocations = function () {
                return selectedLocations;
            };

            me.setNeedAlertFilter = function (v) {
                needAlertFilter = v;
            };
            me.getNeedAlertFilter = function () {
                return needAlertFilter;
            };

            me.setNeedMainPageFilter = function (v) {
                needMainPageFilter = v;
            };
            me.getNeedMainPageFilter = function () {
                return needMainPageFilter;
            };

            me.setPage = function (v) {
                page = v;
            };
            me.getPage = function () {
                return page;
            };

            me.setIsMapView = function (v) {
                isMapView = v;
            };
            me.getIsMapView = function () {
                return isMapView;
            };

            me.setStampDutyData = function (v) {
                stampDutyData = v;
            };
            me.getStampDutyData = function () {
                return stampDutyData;
            };

            me.onGetPropsSuccess = function ($scope, resp, ipp, pageNr, append) {
                var maxP = Math.ceil(resp.nrofprops / ipp),
                        propsCntToPage, ppid = 0;

                pageNr = pageNr > maxP && maxP || pageNr || 1;
                propsCntToPage = ipp * pageNr;

                me.setPage(pageNr);
                me.nrofprops = $scope.nrofprops = resp.nrofprops;

                $scope.propCntLabel = $scope.label['listing' + ($scope.nrofprops > 1 && 's' || '')];

                $scope.needLoadMore = propsCntToPage < $scope.nrofprops;
                $scope.nextnumber = ipp;

                if ($scope.needLoadMore && $scope.nrofprops - propsCntToPage < ipp)
                    $scope.nextnumber = $scope.nrofprops - propsCntToPage;

                if (append) {
                    ppid = resp.entry[0].PID
                    $scope.props = $scope.props.concat(resp.entry);
                } else
                    $scope.props = resp.entry;

                me.currentProperties = $scope.props;

                return {
                    pageNr: pageNr,
                    ppid: ppid
                };
            };

            me.toCommaString = function (data, valueField) {
                return data.map(function (i) {
                    return i[valueField];
                }).join(',');
            };
            me.setModelAndHelpers = function (model, location, selectedLocations, selectedPropTypes, showNearMe, needMaxPrice, needMinPrice) {
                if (CONFIG.enableNearMe && showNearMe) {
                    me.setNearMeLocation(location || null);
                    if (!CONFIG.enablePredictiveLocationsMenu)
                        me.setSelectedLocations(me.toCommaString(selectedLocations, 'location'));
                } else if (CONFIG.enablePredictiveLocationsMenu) {
                    me.setPredLocation(location || null);
                    me.setNearMeLocation(null);
                } else {
                    model.location = selectedLocations.length > 0 ? me.toCommaString(selectedLocations, 'location') : model.location || '';

                    me.setNearMeLocation(null);
                    me.setPredLocation(null);
                }

                model.type = me.toCommaString(selectedPropTypes, 'id');

                if (!needMaxPrice)
                    model.pmax = defFilter.pmax;
                if (!needMinPrice)
                    model.pmin = defFilter.pmin;

                me.setLastFilter(model);
            };
            me.clearCoordsInfo = function (model) {
                delete model.lat;
                delete model.lon;
                delete model.radius;
            };

            //Predictive and Near me Locations
            me.onLocationChange = function (model, n, nearMe) {
                me.clearCoordsInfo(model);
                model.location = '';

                if (n) {
                    var l = n.originalObject || n.coords;
                    if (l && !utilService.isEmpty(l.latitude) && !utilService.isEmpty(l.longitude)) {
                        model.lat = l.latitude;
                        model.lon = l.longitude;
                        model.location = 'byCoordinates';
                        model.radius = nearMe ? 1.5 : 5;
                    }
                }
            };

            // Desktop Multi Select
            me.selectDesktopMultiItems = function (inputModel, selected, valueField) {
                var noneSelected,
                        i = 0,
                        l = inputModel.length

                if (!ng.isArray(selected))
                    selected = selected.split(',').map(Function.prototype.call, String.prototype.trim);

                for (; i < l; ++i) {
                    inputModel[i].ticked = selected.indexOf(inputModel[i][valueField]) >= 0;
                    noneSelected = noneSelected || !inputModel[i].ticked;
                }

                return noneSelected;
            };
            me.onDesktopMultiItemClick = function (data, inputModel, selected, valueField) {
                var first = inputModel[0][valueField];
                if (data[valueField] === first || !selected.length)
                    me.selectDesktopMultiItems(inputModel, first, valueField);
                else if (selected[0][valueField] === first)
                    inputModel[0].ticked = false;
            };

            // Near Me Location
            var setLocationName = function (latitude, longitude, location, pauseLocationWatcher) {
                var deferred = $q.defer(),
                        geocoder = new google.maps.Geocoder(),
                        latlng = new google.maps.LatLng(latitude, longitude),
                        errMsg = CONFIG.ERROR.E0016;

                geocoder.geocode({'latLng': latlng}, function (results, status) {
                    if (status === google.maps.GeocoderStatus.OK) {
                        if (results[0]) {
                            var addr = results[0].address_components,
                                    disp = [];


                            for (var i = 0, l = addr.length; i < l; ++i) {
                                if (addr[i].types.indexOf('street_number') >= 0)
                                    disp.push(addr[i].long_name);
                                if (addr[i].types.indexOf('route') >= 0)
                                    disp.push(addr[i].long_name);
                                if (addr[i].types.indexOf('locality') >= 0)
                                    disp.push(addr[i].long_name);
                                if (addr[i].types.indexOf('postal_code') >= 0)
                                    disp.push(addr[i].long_name);
                            }

                            if (disp.length) {
                                if (CONFIG.enablePredictiveLocationsMenu)
                                    pauseLocationWatcher = true;

                                location = disp.join(', ');

                                deferred.resolve({
                                    location: location,
                                    pauseLocationWatcher: pauseLocationWatcher
                                });
                            } else
                                deferred.reject(errMsg);
                        } else
                            deferred.reject(errMsg);
                    } else {
                        deferred.reject(CONFIG.ERROR.E0017 + status);
                    }
                });

                return deferred.promise;
            }, onSuccess = function (pos, model, location, showNearMe, pauseLocationWatcher) {
                return setLocationName(pos.coords.latitude, pos.coords.longitude, location, pauseLocationWatcher)
                        .then(function (resp) {
                            showNearMe = true;
                            me.onLocationChange(model, pos, true);

                            return {
                                location: resp.location,
                                showNearMe: showNearMe,
                                pauseLocationWatcher: resp.pauseLocationWatcher
                            };
                        }, function (error) {
                            ngToast.create(error);
                            $rootScope.$broadcast('loadingComplete');
                        });
            }, onCurPosSuccess = function (deferred, pos, fireLoadComplete) {
                deferred.resolve(pos);

                if (fireLoadComplete) {
                    $rootScope.$broadcast('loadingComplete');
                }
            };

            me.onError = function (error) {
                var msg = error.message;

                switch (error.code) {
                    case error.PERMISSION_DENIED:
                        msg = CONFIG.ERROR['E00' + (error.message.indexOf('Only secure origins are allowed') === 0 && 14 || 13)];
                        break;
                    case error.POSITION_UNAVAILABLE:
                        msg = CONFIG.ERROR.E0014;
                        break;
                    case error.TIMEOUT:
                        msg = CONFIG.ERROR.E0015;
                        break;
                    case error.UNKNOWN_ERROR:
                        msg = CONFIG.ERROR.E0009;
                        break;
                }

                ngToast.create(msg);
                var timer = $timeout(function () {
                    $timeout.cancel(timer);
                    $rootScope.$broadcast('loadingComplete');
                }, 0);
            };

            me.onNearMeClick = function (model, location, showNearMe, pauseLocationWatcher) {
                var deferred = $q.defer();
                if (navigator.geolocation) {
                    if (showNearMe) {
                        me.clearCoordsInfo(model);

                        deferred.resolve({
                            location: CONFIG.enablePredictiveLocationsMenu && me.getPredLocation() || '',
                            showNearMe: false,
                            pauseLocationWatcher: pauseLocationWatcher
                        });
                    } else {
                        return me.getCurrentPosition(false)
                                .then(function (pos) {
                                    return onSuccess(pos, model, location, showNearMe, pauseLocationWatcher).
                                            then(function (resp) {
                                                $rootScope.$broadcast('loadingComplete');
                                                return resp;
                                            });
                                });
                    }
                } else {
                    ngToast.create(CONFIG.ERROR.E0008);
                    deferred.reject();
                }

                return deferred.promise;
            };

            me.getCurrentPosition = function (fireLoadComplete) {
                var deferred = $q.defer();

                if (ng.isUndefined(fireLoadComplete)) {
                    fireLoadComplete = true;
                }

                $rootScope.$broadcast('loadingStarted');
                navigator.geolocation.getCurrentPosition(function (pos) {
                    onCurPosSuccess(deferred, pos, fireLoadComplete);
                }, function (error) {
                    $http.post(CONFIG.google.geolocationUrl)
                            .then(function (resp) {
                                var l = resp.data.location;

                                onCurPosSuccess(deferred, {
                                    coords: {
                                        latitude: l.lat,
                                        longitude: l.lng
                                    }
                                }, fireLoadComplete);
                            }, function () {
                                me.onError(error);
                                deferred.reject();
                            });
                }, me.geoLocOpts);

                return deferred.promise;
            };
        }]);
})(angular);