(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.Events', [
    ]).controller('eCtrl', ['$scope', '$location', 'CONFIG', 'EAAPI', 'utilService', 'favouritesService',
        function ($scope, $location, CONFIG, EAAPI, utilService, favouritesService) {
            var ipp = CONFIG.gcEventsItemPerPage,
                    args = {},
                    setTimeMinForNextPage = function (items, lastItem) {
                        // Parse RFC3339 timestamp
                        var lastStart = Date.parse(lastItem.start.dateTime),
                                lastEnd = Date.parse(lastItem.end.dateTime);

                        // Find in the next page items the last overlapping event in the chain
                        for (var i = 0; i < items.length; ++i) {
                            var item = items[i],
                                    end = Date.parse(item.end.dateTime);

                            // Check is an overlapping event
                            if (item.id !== lastItem.id && ((end < lastEnd && lastStart <= end) || (end <= lastEnd && lastStart < end))) {
                                $scope.model[$scope.eType].maxEndTime = item.start.dateTime;
                                break;
                            }
                        }
                    },
                    getEvents = function (append, force) {
                        // Wipe out arguments
                        if (!append) {
                            delete args.timeMin;
                            delete args.maxResults;
                        }

                        delete args.forceRequest;
                        if (force)
                            args.forceRequest = 1;

                        EAAPI.getEvents($scope.eType, args)
                                .then(function (resp) {
                                    if (!resp.error) {
                                        var curModel = $scope.model[$scope.eType],
                                                duplItemOffset = curModel.duplItemOffset || 0,
                                                items = ng.copy(resp.items.slice(0, ipp + duplItemOffset)),
                                                respLength = resp.items.length - duplItemOffset,
                                                nextNr = respLength % ipp,
                                                i = 0,
                                                l = items.length,
                                                lastScopeItem = items[l - 1];

                                        // Set event formatted fields like start/end date
                                        utilService.setEvent(items);

                                        // Set items to scope
                                        if (append) {
                                            var curModelIds = curModel.items.map(function (item) {
                                                return item.id;
                                            }),
                                                    // Wipe out the items from response, that exists 
                                                    // in the scope items, because when you get 
                                                    // overlapping events in the request, you get some
                                                    // items there already exists in the scope
                                                    withoutDuplItems = items.filter(function (item) {
                                                        return curModelIds.indexOf(item.id) < 0;
                                                    });


                                            curModel.items.push.apply(curModel.items, withoutDuplItems);

                                            utilService.scrollTo(withoutDuplItems[0].id, 100);
                                        } else {
                                            curModel.items = items;
                                            utilService.scrollTo(0);
                                        }

                                        if (l) {

                                            // Find out load more btn is needed and calculate next number on btn 
                                            curModel.needLoadMore = respLength > ipp;
                                            curModel.nextnumber = nextNr === 0 && ipp || nextNr;


                                            // TimeMin is the Lower bound (inclusive) for an event's end time to filter by
                                            // Set the max end time (as timeMin) to the last scope item end time
                                            curModel.maxEndTime = lastScopeItem.end.dateTime;
                                            curModel.duplItemOffset = 0;

                                            // Calculate recursivly the max end time from the next results set 
                                            // (from item per page (ipp) to items length), because of the existense 
                                            // of the overlapping events chain
                                            setTimeMinForNextPage(resp.items.slice(ipp, resp.items.length), lastScopeItem);

                                            // Check end time has changed
                                            var endTimeChanged = curModel.maxEndTime !== lastScopeItem.end.dateTime,
                                                    lastEnd = Date.parse(curModel.maxEndTime);

                                            // If end time changed, there is overlapping, so
                                            // calculate the overlapping events chain length (as duplItemOffset)
                                            // When not changed, then find out is there an event in the scope items,
                                            // which one the end time is greater, than the scope last item end time
                                            for (; i < l; ++i) {
                                                var end = items[i].end.dateTime,
                                                        curEnd = Date.parse(end);

                                                if (curEnd > lastEnd)
                                                    if (endTimeChanged)
                                                        ++curModel.duplItemOffset;
                                                    else {
                                                        curModel.maxEndTime = end;
                                                        lastEnd = curEnd;
                                                    }
                                            }
                                        }
                                    } else
                                        $location.path('/error');
                                });
                    };

            ng.extend($scope, {
                hTitle: 'Events',
                model: {
                    1: {},
                    2: {}
                }
            });

            ng.extend($scope, {
                toggleFavourites: favouritesService.toggleFavourites,
                onETypeClick: function (eType) {
                    var eq = $scope.eType === eType;

                    $scope.eType = eType;

                    // When clicked the same type like is the selected,
                    // then fire out a force request, to get the latest
                    // results from google (as eq === true),
                    // else get the first items if is the first request,
                    // and items not exists (as !items),
                    // otherwise just switching the two list and scroll to top
                    if (eq || (!$scope.model[eType].items))
                        getEvents(0, eq);
                    else
                        utilService.scrollTo(0);
                },
                loadMore: function () {
                    var curModel = $scope.model[$scope.eType];

                    args.timeMin = utilService.getIsoDate(new Date(curModel.maxEndTime));

                    // Get a min twice as much as many results, such as display,
                    // in order to calculate the next page item count
                    args.maxResults = 2 * ipp + curModel.duplItemOffset;

                    getEvents(1);
                }
            });

            $scope.onETypeClick(1);
        }
    ]);

})(angular); 