(function (ng) {
    'use strict';

    ng.module('MobileSite.services.Auth', [
    ]).service('authService', ['$location', '$sessionStorage', '$localStorage', 'CONFIG',
        function ($location, $sessionStorage, $localStorage, CONFIG) {
            var me = this,
                    aId = CONFIG.ps.agent_id;

            me.setSS = function (params) {
                if (!ng.isDefined($sessionStorage.eaLogin))
                    $sessionStorage.eaLogin = {};
                if (!ng.isDefined($sessionStorage.eaLogin[aId]))
                    $sessionStorage.eaLogin[aId] = {};

                ng.extend($sessionStorage.eaLogin[aId], params);
            };

            me.setLS = function (params) {
                if (!ng.isDefined($localStorage.eaLogin))
                    $localStorage.eaLogin = {};

                $localStorage.eaLogin[aId] = LZString.compressToUTF16(JSON.stringify(params));
            };

            me.isLoggedIn = function (isLL) {
                return !!($sessionStorage.eaLogin && $sessionStorage.eaLogin[aId] && $sessionStorage.eaLogin[aId][isLL === true && 'token' || 'user_id']);
            };


            me.onLogout = function () {
                if ($localStorage.eaLogin && $localStorage.eaLogin[aId])
                    delete $localStorage.eaLogin[aId];

                $sessionStorage.$reset();

                $location.path('/login');
            };

            me.onClickLLLogout = function () {
                delete $sessionStorage.eaLogin[aId].token;

                $location.path('/landlord/login');
            };

        }
    ]);
})(angular);