(function (ng, $) {
    'use strict';

    ng.module('MobileSite.services.App', [
    ]).service('utilService', ['$rootScope', 'CONFIG', '$window', '$document', '$injector', '$location', '$filter', '$sce', '$localStorage', 'favouritesService', 'fullScreenService', 'ngToast',
        function ($rootScope, CONFIG, $window, $document, $injector, $location, $filter, $sce, $localStorage, favouritesService, fullScreenService, ngToast) {
            var me = this,
                    errMsgKey = null;

            me.salesTypes = [];

            me.getErrorMsgKey = function () {
                return errMsgKey;
            };
            me.setErrorMsgKey = function (v) {
                errMsgKey = v;
            };

            me.onFlush = function () {
                //console.log('Clear cache');
                var EAAPI = $injector.get('EAAPI');
                $localStorage.$reset();
                EAAPI.initCache();
                favouritesService.initFavourites();
                $injector.get('notiService').initNoti();
                if (!$window.isOffline)
                    EAAPI.setPropertySettings(CONFIG.ps);
                //$sessionStorage.$reset();
            };

            me.getSalesTypes = function () {
                if (!me.salesTypes.length) {
                    var salesTypes = [{
                            type: 'sale',
                            goal: 1,
                            setting: 'incl_property_sales',
                            name: 'Sales',
                            period: ''
                        }, {
                            type: 'rent',
                            goal: 2,
                            setting: 'incl_property_rent',
                            name: 'Lettings',
                            period: CONFIG.ps.rent_frequency
                        }, {
                            type: 'student_rent',
                            goal: 3,
                            setting: 'incl_property_rent_student',
                            name: 'Students',
                            period: CONFIG.ps.rent_frequency_student
                        }
                    ];

                    if ($rootScope.isCommercial)
                        salesTypes = [{
                                type: 'commercial_sales',
                                goal: 4,
                                setting: 'incl_property_commercial_sales',
                                name: 'Sales',
                                period: ''
                            }, {
                                type: 'commercial_rent',
                                goal: 5,
                                setting: 'incl_property_commercial_rent',
                                name: 'Lettings',
                                period: CONFIG.ps.rent_frequency_commercial
                            }
                        ];
                    //@DEBUG
                    if ($rootScope.isOffice)
                        salesTypes = [{
                                type: 'commercial_rent',
                                goal: 5,
                                setting: 'incl_property_commercial_rent',
                                name: 'Lettings',
                                period: ''
                            }];

                    for (var i = salesTypes.length - 1; i >= 0; --i)
                        if (CONFIG.ps[salesTypes[i].setting] == 0 && salesTypes.length > 1)
                            salesTypes.splice(i, 1);

                    me.salesTypes = salesTypes;
                }
                return me.salesTypes;
            };
            me.getBeds = function (isStudentRent) {
                var beds = [],
                        i = 0;
                for (; i < 7; ++i)
                    beds.push({
                        id: i,
                        value: (i === 0 ? 'Studio' : i) + (isStudentRent ? '' : ' +')
                    });

                return beds;
            };
            me.formatPropertyType = function (pt, checkCfg) {
                return $injector.get('EAAPI').getPropTypes(checkCfg)
                        .then(function (resp) {
                            pt = resp && resp.filter(function (i) {
                                return i.id == pt;
                            })[0];

                            return pt ? pt.name : null;
                        });
            };
            me.formatAddress = function (prop) {
                return me.formatPropertyType(prop.PT, 1)
                        .then(function (pt) {
                            var bdText = '',
                                    sr = [1, 4].indexOf(parseInt(prop.SR, 10)) >= 0 ? 'for sale' : 'to rent',
                                    addr = '</span>',
                                    t = " in </span>",
                                    addrPieces = [],
                                    addrCfg = CONFIG.getAgentAppCfg('address'),
                                    i = 0,
                                    l = addrCfg && addrCfg.length || 0;

                            if (prop.BD >= 1)
                                bdText = prop.BD + " bed ";

                            // Agent specific address format
                            if (l) {
                                for (; i < l; ++i)
                                    if (prop[addrCfg[i]])
                                        addrPieces.push(prop[addrCfg[i]]);

                                if (addrPieces.length)
                                    addr = t + addrPieces.join(', ');
                            } else if (prop.A2)
                                addr = t + prop.A2;

                            return '<span class="salesdisplay">' + (bdText + (pt !== null && (pt + " ") || '') + sr).ucfirst() + addr;
                        });
            };

            me.httpBuildQuery = function (obj, num_prefix, temp_key, separator) {
                obj = obj || {};
                separator = separator || '&';
                var output_string = [],
                        keys = Object.keys(obj);

                if (!keys.length)
                    return '';

                keys.forEach(function (val) {
                    var key = val;
                    num_prefix && !isNaN(key) ? key = num_prefix + key : '';

                    var key = encodeURIComponent(key.replace(/[!'()*]/g, escape));
                    temp_key ? key = temp_key + '[' + key + ']' : '';

                    if (typeof obj[val] === 'object') {
                        var query = me.httpBuildQuery(obj[val], null, key);
                        output_string.push(query);
                    } else {
                        var value = encodeURIComponent(obj[val].toString().replace(/[!'()*]/g, escape));
                        output_string.push(key + '=' + value);
                    }

                });

                return output_string.join(separator);
            };
            me.loadAsyncData = $window.loadAsyncData;
            me.isJson = function (str) {
                try {
                    JSON.parse(str);
                } catch (e) {
                    return false;
                }
                return true;
            };
            me.isEmpty = function (v) {
                var emptyValues = [undefined, null, NaN, false, ''];
                for (var i = 0, l = emptyValues.length; i < l; ++i) {
                    if (v === emptyValues[i])
                        return true;
                }
                return false;
            };
            me.isUrl = function (url) {
                CONFIG.regex.url.lastIndex = 0;
                return CONFIG.regex.url.test(url);
            };
            me.decodeHtml = function (html) {
                var txt = $document[0].createElement("textarea");
                txt.innerHTML = html;
                return $sce.trustAsHtml(txt.value);
            };

            me.calcStampDuty = function (val, isFirstBuyer, isSecHome) {
                var units = isFirstBuyer && val <= 500000 ? [
                    {text: 'Up to 300,000', val: 0, percent: 0, plus: 0},
                    {text: 'Above 300,000.01 to 500,000', val: 300000, percent: 5, plus: 0}
                ] : [
                    {text: 'Up to 125,000', val: 0, percent: 0, plus: 0},
                    {text: 'Above 125,000 to 250,000', val: 125000, percent: 2, plus: 0},
                    {text: 'Above 250,000 to 925,000', val: 250000, percent: 5, plus: 2500},
                    {text: 'Above 925,000 to 1,500,000', val: 925000, percent: 10, plus: 36250},
                    {text: 'Above 1,500,000', val: 1500000, percent: 12, plus: 93750}
                ],
                        secHomePlus = [0, 3750, 10000, 64000, 138750],
                        l = units.length,
                        duty = 0,
                        taxable = 0,
                        duties = [],
                        i = 0;

                for (; i < l; ++i) {
                    if (isSecHome) {
                        units[i].percent += 3;
                        units[i].plus = secHomePlus[i];
                    }

                    if (val > units[i].val) {
                        duty = ((val - units[i].val) * units[i].percent / 100) + units[i].plus;
                        taxable = units[i + 1] && val > units[i + 1].val ? units[i + 1].val - units[i].val : val - units[i].val;
                        duties.push(taxable * units[i].percent / 100);
                    } else
                        duties.push(0);
                }

                return {
                    duty: duty,
                    effective: (duty / val * 100).toFixed(2),
                    duties: duties,
                    units: units
                };
            };

            me.toggleFullscreenClasses = function (hidePropertyContent) {
                hidePropertyContent = !ng.isDefined(hidePropertyContent) && true || hidePropertyContent;
                var ct = $('#main-ct'),
                        gridcont = $('#grid-cont'),
                        mainheader = ct.find('header.mainheader'),
                        fn = (mainheader.hasClass('hide') && 'remove' || 'add') + 'Class';

                mainheader[fn]('hide');
                ct.find('footer.callstoaction')[fn]('hide');
                if (hidePropertyContent)
                    gridcont.find('div.propertContent')[fn]('hide');
                gridcont[fn]('fullscreen');
            };

            me.toggleFullScreenMode = function (helperShow, toggleFSClasses) {
                var helperShowIsDefined = ng.isDefined(helperShow);

                if (ng.isDefined(toggleFSClasses))
                    me.toggleFullscreenClasses(toggleFSClasses);

                if (helperShowIsDefined && $window.StatusBar)
                    $window.StatusBar[helperShow && 'hide' || 'show']();
                else if (fullScreenService.isSupported())
                    if (fullScreenService.isEnabled())
                        fullScreenService.cancel();
                    else if (!helperShowIsDefined || helperShowIsDefined && helperShow)
                        fullScreenService.all();
            };

            me.embedVideo = function (url) {
                var type, id;//, url_check;

                url = url.replace(/\/$/, "");

                if (url.indexOf('youtu.be') >= 0) {
                    type = 'youtube';
                    id = url.substring(url.lastIndexOf("/") + 1, url.length);
                } else if (url.indexOf("youtube") >= 0) {
                    type = 'youtube'
                    if (url.indexOf("</iframe>") >= 0) {
                        var fin = url.substring(url.indexOf("embed/") + 6, url.length)
                        id = fin.substring(fin.indexOf('"'), 0);
                    } else {
                        if (url.indexOf("&") >= 0)
                            id = url.substring(url.indexOf("?v=") + 3, url.indexOf("&"));
                        else
                            id = url.substring(url.indexOf("?v=") + 3, url.length);
                    }
                    //url_check = "https://gdata.youtube.com/feeds/api/videos/" + id + "?v=2&alt=json";
                    //"https://gdata.youtube.com/feeds/api/videos/" + id + "?v=2&alt=json"
                } else if (url.indexOf("vimeo") >= 0) {
                    type = 'vimeo'
                    if (url.indexOf("</iframe>") >= 0) {
                        var fin = url.substring(url.lastIndexOf('vimeo.com/"') + 6, url.indexOf('>'))
                        id = fin.substring(fin.lastIndexOf('/') + 1, fin.indexOf('"', fin.lastIndexOf('/') + 1))
                    } else {
                        id = url.substring(url.lastIndexOf("/") + 1, url.length)
                    }
                    //url_check = 'http://vimeo.com/api/v2/video/' + id + '.json';
                    //'http://vimeo.com/api/v2/video/' + video_id + '.json';
                }

                if (id)
                    if (type === "youtube")
                        url = "https://www.youtube.com/embed/" + id + "?autohide=1&controls=1&showinfo=0";
                    else if (type === "vimeo")
                        url = "https://player.vimeo.com/video/" + id + "?portrait=0";

                return $sce.trustAsResourceUrl(url);
            };

            me.isLightColor = function (color) {
                var rgb = parseInt(color, 16);
                return (0.2126 * ((rgb >> 16) & 255) + 0.7152 * ((rgb >> 8) & 255) + 0.0722 * (rgb & 255)) >= 128;
            };

            me.scrollTo = function (selector, delay, notPid, toParent) {
                var counter = 0;

                if (selector === 0)
                    return $('#grid-cont').scrollTop(0);

                selector = !notPid && "[data-pid=" + selector + "]" || selector;

                function tryScroll() {
                    if (counter > 5)
                        return;

                    if (!$(selector).length) {
                        $window.requestAnimationFrame(tryScroll);
                    } else {
                        setTimeout(function () {
                            var pEl = $(selector),
                                    gEl = $('#grid-cont');

                            if (toParent) {
                                pEl = pEl.parents('article');
                            }

                            if (pEl.length) {
                                gEl.animate({
                                    scrollTop: pEl.position().top + parseFloat(pEl.css('marginTop')) + gEl.scrollTop()
                                }, 1000);
                            }

//                            $rootScope.$evalAsync(function () {
//                                $location.search('pid', null);
//                            });
                        }, delay || 500);
                    }
                    ++counter;
                }
                tryScroll();

                $rootScope.$evalAsync(function () {
                    $location.search('pid', null);
                });
            };

            me.calcDim = function (iw, ih, isImgPortrait, reverse) {
                var cw = $window.innerWidth,
                        ch = $window.innerHeight,
                        isPortrait = cw <= ch,
                        nw, nh;

                if (isImgPortrait && !isPortrait || !isImgPortrait && isPortrait) {
                    cw = $window.innerHeight;
                    ch = $window.innerWidth;
                }

                var iAspectRatio = iw / ih,
                        cAspectRatio = cw / ch;

                if (reverse ? cAspectRatio > iAspectRatio : cAspectRatio < iAspectRatio) {
                    nw = cw;
                    nh = cw / iAspectRatio;
                } else {
                    nh = ch;
                    nw = ch * iAspectRatio;
                }

                return (nw < cw || nh < ch) && me.calcDim(iw, ih, isImgPortrait, true) || {
                    width: nw,
                    height: nh
                };
            };

            me.encodeUri = function (url, backUrl, a) {
                return LZString.compressToEncodedURIComponent(url + '@@@BACKURL@@@' + backUrl)
            };

            me.showLoading = function () {
                $rootScope.$evalAsync(function () {
                    $rootScope.loader.externalCall = true;
                    $rootScope.$broadcast('loadingStarted');
                });
            };

            me.hideLoading = function () {
                $rootScope.$evalAsync(function () {
                    $rootScope.loader.externalCall = false;
                    $rootScope.$broadcast('loadingComplete');
                });
            };

            me.setPost = function (resp, logo) {
                if (resp.news[0] && !resp.news[0].fid)
                    for (var i = 0, l = resp.news.length; i < l; ++i) {
                        var post = resp.news[i],
                                author = resp.author || post.author;

                        ng.extend(post, {
                            fid: 'n' + post.id,
                            dateF: $filter('postDate')(post.date),
                            timeF: $filter('humanTime')(post.date),
                            descF: post.meta_page_description && $filter('truncate')(post.meta_page_description),
                            authorPath: author && $filter('formatUrlFriendlyString')(author.firstname + '-' + author.surname),
                            src: post.image && (resp.image_url + $filter('format')(CONFIG.api.feedImgQuery, post.image, 600, 600)) || logo,
                            authorSrc: author && author.image && (resp.author_image_url + $filter('format')(CONFIG.api.feedImgQuery, author.image, 100, 100))
                        });
                    }
            };

            me.setEvent = function (items) {
                if (items[0] && !items[0].fid) {
                    var format = 'd MMM yy, HH:mm',
                            i = 0,
                            l = items.length;

                    for (; i < l; ++i) {
                        var start = Date.parse(items[i].start.dateTime),
                                end = Date.parse(items[i].end.dateTime),
                                created = Date.parse(items[i].created),
                                updated = Date.parse(items[i].updated);

                        // Format updated date, when the update older than 1 min (60 * 1000 ms)
                        ng.extend(items[i], {
                            fid: 'e' + items[i].id,
                            startD: start && $filter('date')(start, format),
                            endD: end && $filter('date')(end, format),
                            updateD: updated && (updated - created > 60 * 1000) && $filter('date')(updated, format)
                        });
                    }
                }
            };

            me.getIsoDate = function (date, withoutTimeZone) {
                var pad = function (n) {
                    return n < 10 && ('0' + n) || n;
                }, offset = date.getTimezoneOffset();

                date = date || new Date();

                return date.getFullYear()
                        + "-" + pad(date.getMonth() + 1)
                        + "-" + pad(date.getDate())
                        + "T" + pad(date.getHours())
                        + ":" + pad(date.getMinutes())
                        + ":" + pad(date.getSeconds())
                        + (!withoutTimeZone && ((offset > 0 ? "-" : "+")
                                + pad(Math.floor(Math.abs(offset) / 60), 2)
                                + ":" + pad(Math.abs(offset) % 60, 2)) || '');
            };

            me.toast = function (msg) {
                ngToast.create({
                    content: msg,
                    additionalClasses: 'ea-slide ea-toast-ng',
                    timeout: 5000
                });
            };
        }
    ]);
})(angular, jQuery);