(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.MortgageCalculator', [
    ]).controller('mortgageCtrl', ['$scope', '$location', 'CONFIG', 'ngToast', 'formService',
        function ($scope, $location, CONFIG, ngToast, formService) {
            ng.extend($scope, {
                hTitle: 'Mortgage Calculator',
                isText: false,
                enquiry: false,
                model: {
                    purchasePrice: 0,
                    payment_period: 25,
                    interest_rate: parseFloat(CONFIG.ps.app_interest_rate) || 0.5,
                    interest_only: 0,
                    repayments: 0,
                    firstname: '',
                    surname: '',
                    email: '',
                    phone: '',
                    message: ''
                }
            });

            ng.extend($scope, {
                onChange: function () {
                    var A = $scope.model.purchasePrice,
                            R = $scope.model.interest_rate / 100;

                    if (A > 1 && R >= 0.0001 && $scope.model.payment_period > 1) {
                        $scope.model.repayments = Math.floor(((A * R) / 12) * (1 / (1 - (Math.pow(1 / (1 + R), $scope.model.payment_period)))));
                        $scope.model.interest_only = Math.floor((A * R) / 12);
                    } else {
                        $scope.model.repayments = 0;
                        $scope.model.interest_only = 0;
                    }
                },
                onChangeEmail: function () {
                    $scope.mcF.email.$errorMsg = null;
                },
                onSubmit: function (isValid) {
                    formService.onSubmit($scope, isValid, 'mortgageCalculator', $scope.model, 'mcF', 'M0004', 'self');
                }
            });
        }
    ]);

})(angular); 