(function (ng, $) {
    'use strict';

    var infoBubble,
            initInfoBubble = function () {
                if (!infoBubble)
                    infoBubble = new InfoBubble({
                        shadowStyle: 0,
                        padding: 5,
                        backgroundColor: 'rgba(57,57,57,0.7)',
                        borderRadius: 5,
                        arrowSize: 15,
                        borderWidth: 1,
                        borderColor: '#2c2c2c',
                        disableAutoPan: true,
                        hideCloseButton: true,
                        arrowPosition: 50,
                        backgroundClassName: 'info-bubble',
                        arrowStyle: 0,
                        maxWidth: 310,
                        minHeight: 48
                    });
            };

    ng.module('MobileSite.directives.Map', [
    ]).directive("map", ['$rootScope', '$window', '$interpolate', '$templateCache', 'CONFIG', 'platform',
        function ($rootScope, $window, $interpolate, $templateCache, CONFIG, platform) {
            return {
                restrict: "A",
                scope: {
                    control: '='
                },
                link: function ($scope, $element, $attrs) {
                    var $win = ng.element($window),
                            google = $window.google,
                            map,
                            mapOptions = {
                                zoom: 15,
                                disableDefaultUI: true,
                                draggable: !platform.device,
                                scrollwheel: false,
                                zoomControl: true,
                                zoomControlOptions: {
                                    position: google.maps.ControlPosition.RIGHT_BOTTOM,
                                    style: google.maps.ZoomControlStyle.SMALL
                                }
                            }, latitude = $attrs.latitude && parseFloat($attrs.latitude),
                            longitude = $attrs.longitude && parseFloat($attrs.longitude),
                            address = $attrs.address,
                            location,
                            markers = [],
                            marker,
                            lastClickedMarker,
                            timers = [],
                            places = CONFIG.showPlacesOnMap,
                            icon = {
                                path: 'm0,-48',
                                strokeWeight: 0
                            }, interpolationFn = $interpolate($templateCache.get('components/map/pin.html')),
                            createMarker = function (pos, iconOpts, name, zIndex) {
                                markers.push(marker = new Marker({
                                    position: pos,
                                    map: map,
                                    icon: icon,
                                    map_icon_label: interpolationFn(iconOpts),
                                    name: name,
                                    zIndex: zIndex
                                }));

                                setMarkerClick(marker);
                            },
                            setMarkerClick = function (marker) {
                                // If exists the name, that need to show in the window
                                if (marker.name)
                                    google.maps.event.addListener(marker, 'click', function (event) {
                                        // On desktop click event called twice, so we hack this bug
                                        if (!marker.listened) {
                                            marker.listened = true;

                                            //Set zIndex for markers
                                            if (marker.MarkerLabel) {
                                                // Set zIndex to default or empty for last clicked marker
                                                if (lastClickedMarker)
                                                    lastClickedMarker.MarkerLabel.div.style.zIndex = lastClickedMarker.zIndex || '';

                                                lastClickedMarker = this;

                                                // Set clicked marker zIndex to max value (bring marker to front)
                                                lastClickedMarker.MarkerLabel.div.style.zIndex = google.maps.Marker.MAX_ZINDEX + 1;
                                            }

                                            // Set the content of the window and show it
                                            infoBubble.setContent(this.name);
                                            infoBubble.open(map, this);
                                        }

                                        timers[1] = setTimeout(function () {
                                            marker.listened = false;
                                        }, 500);
                                    });
                            },
                            resize = function () {
                                if (map)
                                    $scope.$evalAsync(function () {
                                        map.panTo(markers[0].getPosition());

                                        if (infoBubble.isOpen()) {
                                            infoBubble.close();
                                            infoBubble.open();
                                        }
                                    });
                            };

                    // Map init fn
                    $scope.control.init = function (show) {
                        // Delay init, because of the slide down animation
                        timers[0] = setTimeout(function () {
                            // If map exists then return from function
                            if (map) {
                                if (show) {
                                    $window.google.maps.event.trigger(map, 'resize');
                                    map.panTo(markers[0].getPosition());
                                }
                                return;
                            }

                            $window.markerOverride();
                            initInfoBubble();

                            if (latitude && longitude) {
                                location = new google.maps.LatLng(latitude, longitude);

                                // Create map with map options
                                if (!$rootScope.dMap)
                                    $rootScope.dMap = map = new google.maps.Map($element[0], $.extend(mapOptions, {
                                        center: location
                                    }));
                                else {
//                                    $('#prop-d-map').appendTo('#property-map').show();
                                    $('#prop-d-map').replaceWith($rootScope.$dMap);
                                    map = $rootScope.dMap;
                                    map.setCenter(location);
                                    google.maps.event.trigger(map, 'resize');
                                }

                                // Create property location marker
                                createMarker(location, {
                                    id: 1,
                                    color: '#' + CONFIG.ps.buttons_color.replace(/\/+$/, '').split('/').pop(),
                                    fa: 'circle'
                                }, address, 500);

                                map.listeners = [
                                    // On resize window center map
                                    google.maps.event.addListener(map, 'zoom_changed', resize),
                                    // On map click close the infoBubble
                                    google.maps.event.addDomListener(map, 'click', function () {
                                        infoBubble.close();
                                    })
                                ];

                                // If needed to find places around property
                                if (places && ng.isArray(places) && places.length) {
                                    var defaultTypes = ['train_station', 'subway_station', 'school'],
                                            types = defaultTypes.filter(function (item, i) {
                                                return places.indexOf(i) !== -1;
                                            }),
                                            typeMarkerIcons = {
                                                train_station: 'train',
                                                subway_station: 'subway',
                                                school: 'graduation-cap'
                                            },
                                    // Create new places service and get places
                                    service = new google.maps.places.PlacesService($('#google-node')[0]);
                                    
                                    types.forEach(function (item) {
                                        service.nearbySearch({
                                            location: location,
                                            radius: 500,
                                            type: item
                                        }, function (results, status) {
                                            if (status === google.maps.places.PlacesServiceStatus.OK)
                                                for (var i = 0, l = results.length; i < l; i++) {
                                                    var coords = results[i].geometry.location;
                                                    for (var j = 0, ll = types.length; j < ll; j++)
                                                        if (results[i].types.indexOf(types[j]) !== -1)
                                                            createMarker(coords, {
                                                                id: 2,
                                                                color: '#666',
                                                                fa: typeMarkerIcons[types[j]]
                                                            }, results[i].name);
                                                }
                                        });

                                    });
                                }

                                //Window resize
                                $win.on('resize', resize);

                                $scope.$on('$destroy', function () {
                                    var i = 0,
                                            l = timers.length;

                                    for (; i < l; ++i)
                                        clearTimeout(timers[i]);

                                    $win.off('resize', resize);

                                    if (map) {
                                        for (i = 0, l = map.listeners.length; i < l; ++i)
                                            google.maps.event.removeListener(map.listeners[i]);

                                        map.listeners = null;

                                        for (i = 0, l = markers.length; i < l; ++i) {
                                            google.maps.event.clearInstanceListeners(markers[i]);
                                            markers[i].destroy();
                                            markers[i].setMap(null);

                                            for (var k in markers[i].MarkerLabel)
                                                delete markers[i].MarkerLabel[k];
                                            for (var k in markers[i])
                                                delete markers[i][k];

                                            delete markers[i];
                                        }

                                        lastClickedMarker = null;

                                        if (!$rootScope.$dMap)
                                            $rootScope.$dMap = $('#property-map').children().detach();
//                                        $('#property-map').children().prop('id', 'prop-d-map').appendTo('body').hide();
                                    }
                                });
                            }
                        }, 1000, false);
                    };
                }
            };
        }
    ]);
})(angular, jQuery);