(function (ng, $) {
    'use strict';

    ng.module('MobileSite.controllers.Main', [
        'MobileSite.services.Cordova'
    ]).controller('mainCtrl', ['$scope', '$window', '$localStorage', '$sessionStorage', 'CONFIG', 'utilService', 'authService', 'openFBService', 'cUtil', 'platform',
        function ($scope, $window, $localStorage, $sessionStorage, CONFIG, utilService, authService, openFBService, cUtil, platform) {
            // Init local variables
            var isResizeEventListened = false,
                    evalAsync = false,
                    firstImgFitOrientationLoaded = false,
                    // Init local functions
                    loadBgImg = function (type, img, singleBg) {
                        var dImg = $('<img\>')
                                .on('load', function () {
                                    var me = this,
                                            isImgPortrait = type === 'port',
                                            dim = utilService.calcDim(me.width, me.height, singleBg && ((!isImgPortrait && $scope.isPortrait) || (isImgPortrait && !$scope.isPortrait)) ? !isImgPortrait : isImgPortrait),
                                            setBg = function () {
                                                $scope[type + 'BgImg'] = {
                                                    bg: img,
                                                    widthOri: me.width,
                                                    heightOri: me.height,
                                                    width: dim.width,
                                                    height: dim.height
                                                };
                                            }, isImgFitOrientation = (isImgPortrait && $scope.isPortrait) || (!isImgPortrait && !$scope.isPortrait)

                                    // "if" expression first part for apps, second part for the site
                                    if (isImgFitOrientation || evalAsync ||
                                            (!$scope.isCordova && firstImgFitOrientationLoaded)) {

                                        evalAsync = true;
                                        $scope[(firstImgFitOrientationLoaded && '$apply' || '$eval') + 'Async'](setBg);
                                    } else
                                        setBg();

                                    // Flag containes that need to run digest cycle for the site
                                    if (isImgFitOrientation)
                                        firstImgFitOrientationLoaded = true;

                                    // When there is just one bg image (landscape or portratit) resize on orientation change
                                    if (singleBg && !isResizeEventListened)
                                        $($window).on('resize.' + type, function () {
                                            var dim = utilService.calcDim($scope[type + 'BgImg'].widthOri, $scope[type + 'BgImg'].heightOri, $scope.isPortrait);

                                            $scope[type + 'BgImg'].width = dim.width;
                                            $scope[type + 'BgImg'].height = dim.height;
                                        });

                                    dImg = null;
                                }).prop('src', img);
                    },
                    getImages = function (notDownload) {
                        var ns,
                                n1 = CONFIG.ps.logo_img,
                                n2 = CONFIG.ps.hp_ipad_portrait_img,
                                n3 = CONFIG.ps.hp_ipad_landscape_img,
                                t1 = $scope.imagesDownloaded;

                        if (n1) {
                            ns = 'logo_img';
                            cUtil.getFile(n1, CONFIG.ps.logo_img_url + '&file=' + n1 + '&width=1280&height=200', ns, notDownload || t1[ns], function (img, downloaded) {
                                // When isTablet and ipad bg images exists, don't run digest cycle here, dont mess bg images rendering.
                                // Digest cycle run after the bg images loaded
                                if ($scope.isTablet && (n2 || n3))
                                    $scope.logo = img;
                                else
                                    $scope.$applyAsync(function () {
                                        $scope.logo = img;
                                    });
                            });
                        }

                        if ($scope.isTablet) {
                            if (n2) {
                                ns = 'hp_ipad_portrait_img';

                                cUtil.getFile(n2, CONFIG.ps.hp_ipad_portrait_url + '&file=' + n2, ns, notDownload || t1[ns], function (img) {
                                    loadBgImg('port', img, !n3);
                                });
                            }

                            if (n3) {
                                ns = 'hp_ipad_landscape_img';

                                cUtil.getFile(n3, CONFIG.ps.hp_ipad_landscape_url + '&file=' + n3, ns, notDownload || t1[ns], function (img) {
                                    loadBgImg('land', img, !n2);
                                });
                            }
                        }
                    },
                    initImageFlags = function () {
                        $scope.imagesDownloaded = {
                            about_us_image: false,
                            reviews_on: false,
                            reviews_off: false
                        };
                    },
                    onLoginChanged = function (v, ov, isLL) {
                        isLL = isLL === true;

                        var varName = 'is' + (isLL && 'LL' || '') + 'LoggedIn',
                                isDef = ng.isDefined($scope[varName]),
                                inclCfg = CONFIG.ps['incl_' + (isLL && 'landlords' || 'loginform')] == 1;

                        $scope[varName] = v || !isLL && !inclCfg;

                        // If not defined $scope.isLoggedIn, then the app is started now,
                        // and don't need to fire out the event.
                        // This event is listened in the toggleWatchers directive and in home controller
                        if (($scope.isOffice || isLL) && inclCfg && isDef)
                            $scope.$broadcast('loginChanged', v);
                    };

            // Init scope variables
            ng.extend($scope, {
                isPortrait: $window.innerWidth <= $window.innerHeight,
                isTablet: platform.tablet,
                isMobile: platform.mobile,
                isIOS: platform.ios,
                isAndroid: platform.android,
                year: new Date().getFullYear(),
                ls: $localStorage,
                signal: {
                    showRateDialog: false,
                    showRepairDialog: false,
                    showFavLabel: CONFIG.ps.incl_news == 1 || CONFIG.ps.incl_events == 1
                },
                isRequestValuationEnabledBySalesTypes: !$scope.isOffice && 3 !== Math.min.apply(null, utilService.getSalesTypes().map(function (i) {
                    return i.goal;
                }))
            });

            var labelObj = {
                listing: 'property',
                listings: 'properties'
            };

            if ($scope.isCommercial)
                labelObj = {
                    listing: 'business',
                    listings: 'businesses'
                };
            else if ($scope.isOffice)
                labelObj = {
                    listing: 'space',
                    listings: 'spaces'
                };

            $scope.label = labelObj;

            // Init scope functions
            ng.extend($scope, {
                onClickLogout: function () {
                    var aId = CONFIG.ps.agent_id,
                            ssEaLogin = $sessionStorage.eaLogin;

                    if (!$scope.isOffline && ssEaLogin && ssEaLogin[aId] && ssEaLogin[aId].accessToken)
                        openFBService.logout(ssEaLogin[aId].accessToken);

                    authService.onLogout();
                },
                onClickLLLogout: authService.onClickLLLogout,
                onClickFlush: utilService.onFlush
            });

            // Load logo and bg images
            initImageFlags();
            getImages(1);

            // Event listeners
            $scope.$onMany(['appPropertySettingsUpToDate', 'appPropertySettingsUpdated'], function () {
                initImageFlags();
                getImages();
            });

            // Change statusbar background and text color 
            // by config bgColor on iOS devices for every page change
            $scope.$on('$routeChangeSuccess', function (e, current) {
                var tUrl = current.loadedTemplateUrl, color,
                        oriPath = current.$$route && current.$$route.originalPath,
                        pages = ['login', 'register', 'forgot-password'];

                $scope.pageName = tUrl && tUrl.split('/').pop().replace('.html', '') + '-page' || '';

                $scope.isSearch = $scope.pageName === 'search-page';
                $scope.isResults = $scope.pageName === 'results-page';
                $scope.isLoginPages = oriPath && pages.indexOf(oriPath, 0, 1, 1) >= 0;

                $scope.isLL = oriPath && oriPath.indexOf('landlord') >= 0;

                if (!platform.android && $window.StatusBar) {
                    $window.StatusBar.backgroundColorByHexString('#' + (color = ($scope.isSearch || $scope.isLoginPages) && CONFIG.ps.bg_color || CONFIG.ps.header_color || '000'));
                    $window.StatusBar['style' + (utilService.isLightColor(color) && 'Default' || 'LightContent')]();
                }
            });

            $scope.$on("loadingStarted", function () {
                $scope.loader.liLoading = true;
            });
            $scope.$on("loadingComplete", function () {
                $scope.$evalAsync(function () {
                    $scope.loader.liLoading = false;
                });
            });

            // Watchers
            // Watch login changed
            $scope.$watch(authService.isLoggedIn, onLoginChanged);

            // Watch landlord login changed
            $scope.$watch(function () {
                return authService.isLoggedIn(true);
            }, function (v) {
                onLoginChanged(v, 0, true);
            });

            // Watch favourites changed and update cnt
            $scope.$watch(function () {
                return $localStorage.eaFavourites && ng.toJson($localStorage.eaFavourites[CONFIG.ps.agent_id]);
            }, function (v) {
                if (v)
                    $scope.favCnt = Object.keys(JSON.parse(v)).length;
            });
        }
    ]);

})(angular, jQuery);