(function (ng, $) {
    'use strict';

    ng.module('MobileSite.services.API', [
    ]).service('EAAPI', ['$rootScope', '$document', '$window', '$http', '$q', '$location', '$localStorage', '$sessionStorage', '$injector', 'CONFIG', 'utilService', 'searchService', 'authService', 'ngToast',
        function ($rootScope, $document, $window, $http, $q, $location, $localStorage, $sessionStorage, $injector, CONFIG, utilService, searchService, authService, ngToast) {
            var me = this, ls,
                    _get = function (method, args, noCache) {
                        _checkLocalStorageSize();

                        method = CONFIG.api.feedMap[method] || method;
                        args = args || {};
                        var cacheName = args.cacheKey || method;

                        return CONFIG.api.cache.enabled && !noCache && !args.forceRequest && ng.isDefined(ls[cacheName]) && (ls[cacheName].lastUpdate || ls[cacheName].expire >= new Date().getTime()) ? _getCached(cacheName) : _getRaw(method, args, noCache);
                    },
                    _getRaw = function (method, args, noCache) {
                        var justUrl = args.justUrl || '';
                        delete args.justUrl;

                        // When no internet connection redirect to offline page
                        if ($rootScope.isOffline && !justUrl) {
                            if (!args.runInBg)
                                $location.path('/offline');

                            return _promise(false);
                        }


                        // Save config args to variables
                        var cacheName = args.cacheKey || method,
                                cacheDuration = args.cacheDuration || args.shortDuration && CONFIG.api.cache.shortDuration || 0,
                                runInBg = args.runInBg || 0,
                                url = args.extUrl;

                        // Delete config args
                        delete args.cacheKey;
                        delete args.cacheDuration;
                        delete args.shortDuration;
                        delete args.runInBg;
                        delete args.forceRequest;
                        delete args.extUrl;

                        // Set default url parameters
                        if (method) {
                            args = ng.merge({
                                datatype: 'json',
                                source: CONFIG.api.source,
                                agent_id: CONFIG.ps.agent_id
                            }, args);

                            // Build url
                            url = CONFIG.api.endpoint + method + '.php';
                        } else if (!url) {
                            _setError();
                            return _promise(false);
                        }

                        url += (url.indexOf('?') >= 0 && '&' || '?') + utilService.httpBuildQuery(args);

                        if (justUrl)
                            return url;

                        // Load url
                        return $http.get(url, {data: {runInBg: runInBg}}).then(function (resp) {
                            resp = resp.data;

                            if (!resp)
                                _setError(runInBg);
                            else if (ng.isDefined(resp.success)) {
                                if (resp.success) {
                                    var data = resp.data || resp.response;
                                    _setCache(cacheName, data, noCache, cacheDuration);
                                    return data;
                                } else
                                    return resp.response || resp.error || resp.msg || resp;
                            } else if (ng.isObject(resp)) {
                                _setCache(cacheName, resp, noCache, cacheDuration);
                                return resp;
                            } else
                                _setError(runInBg);
                        }, function () {
                            _setError(runInBg);
                        });
                    },
                    _getMethodMap = function (method) {
                        var excMethods = ['news'];

                        return excMethods[excMethods.indexOf(method, 0, 1, 1)] || method;
                    },
                    _getCached = function (method) {
                        var methodMap = _getMethodMap(method);

                        if (utilService.FEED_UPDATES && utilService.FEED_UPDATES[methodMap] && !ls[method].lastUpdate) {
                            ls[method].lastUpdate = utilService.FEED_UPDATES[methodMap];
                            delete ls[method].expire;
                        }

                        return _promise(JSON.parse(LZString.decompressFromUTF16(ls[method].data)));
                    },
                    _setCache = function (method, data, noCache, cacheDuration) {
                        if (!noCache) {
                            if (!ls[method])
                                ls[method] = {};

                            ls[method].data = LZString.compressToUTF16(JSON.stringify(data));

                            var methodMap = _getMethodMap(method);

                            if (utilService.FEED_UPDATES && utilService.FEED_UPDATES[methodMap])
                                ls[method].lastUpdate = utilService.FEED_UPDATES[methodMap];
                            else
                                ls[method].expire = new Date().getTime() + (cacheDuration || CONFIG.api.cache.duration);
                        }
                    },
                    _delLsGce = function (type) {
                        for (var k in ls)
                            if (k.indexOf('gce' + (type && ('-' + type) || '')) >= 0)
                                delete ls[k];
                    },
                    _getLocalStorageSize = function () {
                        var t = 0;
                        for (var k in localStorage)
                            t += localStorage[k].length * 2;

                        return t;
                    },
                    _checkLocalStorageSize = function () {
                        var k,
                                cfgCache = CONFIG.api.cache,
                                cacheMaxAllowedSize = cfgCache.maxSize - cfgCache.offsetSize;

                        // Clean all expired caches, that not containe main data
                        if (cacheMaxAllowedSize <= _getLocalStorageSize())
                            for (k in ls)
                                if (ls[k].expire < new Date().getTime() && !ls[k].lastUpdate)
                                    delete ls[k];

                        // Clean all probably unused caches
                        if (cacheMaxAllowedSize <= _getLocalStorageSize())
                            for (k in ls)
                                if (k.indexOf('lp-') >= 0)
                                    delete ls[k];

                        if (cacheMaxAllowedSize <= _getLocalStorageSize())
                            _delLsGce();

                        if (cacheMaxAllowedSize <= _getLocalStorageSize())
                            for (k in ls)
                                if (['tags-', 'authors-'].indexOf(k, 0, 1, 1))
                                    delete ls[k];

                        if (cacheMaxAllowedSize <= _getLocalStorageSize())
                            for (k in ls)
                                if (['getProperties', 'getfile-'].indexOf(k, 0, 1, 1))
                                    delete ls[k];

                        if (cacheMaxAllowedSize <= _getLocalStorageSize())
                            // Keep the first news
                            for (k in ls)
                                if (k.indexOf('news-') >= 0 && k !== cfgCache.firstNewsCacheKey)
                                    delete ls[k];
                    },
                    _setError = function (runInBg) {
                        if (!runInBg)
                            if ($rootScope.isCordova)
                                // Connection was interrupted during the request
                                if ($rootScope.isOffline)
                                    $location.path('/offline');
                                else
                                    _toError();
                            else
                                _toError();
                        //<debug>
                        else
                            console.log('Background request failed!');
                        //</debug>
                    },
                    _promise = function (data) {
                        return $q(function (resolve, reject) {
                            if (data === false)
                                reject();
                            else
                                resolve(data);
                        });
                    };

            me.initCache = function () {
                if (!$localStorage.eaCache)
                    $localStorage.eaCache = {};
                if (!$localStorage.eaCache[CONFIG.ps.agent_id])
                    $localStorage.eaCache[CONFIG.ps.agent_id] = {};

                ls = $localStorage.eaCache[CONFIG.ps.agent_id];
            };

            me.initCache();

            me.setPropertySettings = function (ps) {
                if (!ls.app_property_settings)
                    ls.app_property_settings = {};

                ls.app_property_settings.data = LZString.compressToUTF16(JSON.stringify(ps));
            };

            me.get = function (method, args, noCache) {
                return _get(method, args, noCache);
            };
            me.getProperty = function (pid) {
                var p = searchService.currentProperties;
                if (p && p.length)
                    for (var i = 0, l = p.length; i < l; ++i)
                        if (p[i].PID && p[i].PID === pid) {
                            var data = {
                                'entry': [
                                    p[i]
                                ]
                            };
                            _setCache(pid, data);
                            return _promise(data);
                        }

                return _get('properties', {prop_id: pid, cacheKey: pid});
            };
            me.getProperties = function (args, backFromDetails, isLL) {
                var p = searchService.currentProperties;
                args = ng.copy(args) || {};

                if ($rootScope.isOffice)
                    delete args.goal;

                if (backFromDetails && args.from > 0 && p && p.length)
                    return _promise({
                        nrofprops: searchService.nrofprops,
                        entry: p
                    });

                if (isLL)
                    args.landlord_token = $sessionStorage.eaLogin[CONFIG.ps.agent_id].token;

                args.cacheKey = 'get' + (isLL && 'LL' || '') + 'Properties-' + XXH(JSON.stringify(Object.sort(args)), 99999).toString(36);
                args.shortDuration = true;

                return _get('properties', args)
                        .then(function (resp) {
                            var propStatusRepl = CONFIG.getAgentAppCfg('propStatusRepl');

                            if (resp && propStatusRepl)
                                for (var i = 0, l = resp.entry.length; i < l; ++i)
                                    if (propStatusRepl[resp.entry[i].status])
                                        resp.entry[i].status = propStatusRepl[resp.entry[i].status];

                            return resp;
                        });
            };
            me.getLLSettings = function (args, noCache, data) {
                args = args || {};

                var method = 'llSettings',
                        cacheKey = args.cacheKey = 'getLLSettings';

                args.token = $sessionStorage.eaLogin[CONFIG.ps.agent_id].token;
                args.shortDuration = true;

                return _get(method, args, noCache)
                        .then(function (resp) {
                            if (!resp || (args.set && !resp.update || !args.set && !resp.LL_Email)) {
                                authService.onClickLLLogout();
                                ngToast.create(CONFIG.ERROR.E0019);
                            } else {
                                if (args.set) {
                                    ngToast.create(CONFIG.MESSAGE.M0007);

                                    if (data)
                                        ls[cacheKey].data = LZString.compressToUTF16(JSON.stringify(data));
                                }
                                return resp;
                            }
                        });
            };
            me.getLLDocument = function (pid) {
                var args = {};

                args.order = 'asc';
                args.allfields = true;

                return _get('document_types', args)
                        .then(function (documents) {
                            if (documents && documents[0]) {
                                var promises = [],
                                        i = 0,
                                        l = documents.length;

                                for (; i < l; ++i) {
                                    documents[i].type = ng.lowercase(documents[i].name.replace(/ /g, '_'));
                                    documents[i].files = {};

                                    promises.push(_get('landlords_property', {
                                        property_id: pid,
                                        document_type_id: documents[i].id,
                                        shortDuration: true,
                                        cacheKey: 'lp-' + pid + '-' + documents[i].id
                                    }));
                                }

                                return utilService.loadAsyncData(promises)
                                        .then(function (resps) {
                                            for (i = 0; i < l; ++i)
                                                documents[i].files = resps[i];

                                            return documents;
                                        });
                            } else
                                return documents;
                        });
            };
            me.getLocations = function () {
                return _get('locations')
                        .then(function (locations) {
                            if (locations) {
                                var txt = $document[0].createElement("textarea"),
                                        i = 0,
                                        l = locations.length;

                                for (; i < l; ++i) {
                                    txt.innerHTML = locations[i].display_name;
                                    locations[i].display_name = txt.value;
                                }
                            } else {
                                locations = [];
                            }

                            return locations;
                        });
            };
            me.getPrices = function (extra, args) {
                extra = extra || {};
                args = args || {};

                var type = extra.type || 'sale',
                        minMax = extra.minMax || '',
                        types = utilService.getSalesTypes();

                if (ng.isNumber(type)) {
                    type = types.filter(function (i) {
                        return i.goal === type;
                    })[0].type;
                }

                var map = 'prices' + type + minMax;
                if (!extra.all && ng.isDefined(me[map])) {
                    return _promise(me[map]);
                }

                args.all = true;

                return _get('prices', args)
                        .then(function (allPrices) {
                            var firstVal = allPrices[Object.keys(allPrices)[0]];
                            firstVal = firstVal[Object.keys(firstVal)[0]][0];

                            if (!ng.isObject(firstVal)) {
                                for (var k in allPrices) {
                                    for (var kk in allPrices[k]) {
                                        for (var i = 0, l = allPrices[k][kk].length; i < l; ++i) {
                                            allPrices[k][kk][i] = {
                                                id: allPrices[k][kk][i],
                                                value: (CONFIG.ps.app_currency || '') + parseInt(allPrices[k][kk][i], 10).toLocaleString()
                                            };
                                        }
                                        allPrices[k][kk][kk === 'min' ? 'unshift' : 'push']({id: kk === 'min' ? '0' : '100000000', value: 'No ' + kk.ucfirst()});
                                    }
                                }
                                _setCache('prices', allPrices);
                            }

                            return extra.all ? allPrices : me[map] = (minMax ? allPrices[type][minMax] : allPrices[type]);
                        });


            };

            me.getPropTypes = function (checkCfg) {
                return checkCfg && CONFIG.getAgentAppCfg('hidePropertyAddressPropertyType') ? _promise(null) : _get('property_types')
                        .then(function (pts) {
                            var txt = $document[0].createElement("textarea"),
                                    i = 0,
                                    l = pts.length;

                            for (; i < l; ++i) {
                                txt.innerHTML = pts[i].label || pts[i].name;
                                pts[i].name = txt.value;
                                pts[i].id = pts[i].id + '';
                            }

                            return pts;
                        });
            };

            me.getNotifications = function (forceRequest, refreshNotiCounter) {
                return _get('notifications', {shortDuration: true, forceRequest: forceRequest, runInBg: forceRequest})
                        .then(function (resp) {
                            var notiService = $injector.get('notiService');

                            if (forceRequest) {
                                notiService.refreshNewest();

                                if (refreshNotiCounter) {
                                    notiService.refreshNotiCnt(resp);
                                }
                            }

                            $rootScope.showNotifications = notiService.showNotifications();

                            return resp;
                        });
            };

            me.getContent = function (args) {
                args = args || {};
                ng.extend(args, CONFIG.api.feedArgs['content'] || {});
                return _get('content', args);
            };

            me.getTestimonials = function () {
                return me.getContent()
                        .then(function (resp) {
                            return {
                                testimonials: resp.testimonials,
                                image_url: resp.testimonials_image_url
                            };
                        });
            };

            me.getAbout = function () {
                return me.getContent()
                        .then(function (resp) {
                            return {
                                about_us: resp.about_us,
                                about_us_image_url: resp.about_us_image_url,
                                about_us_image: resp.about_us_image
                            };
                        });
            };

            me.getTeam = function () {
                return me.getContent()
                        .then(function (resp) {
                            return resp.teams;
                        });
            };

            me.getNews = function (args, stored, cacheType) {
                var curN = cacheType && utilService[cacheType];

                if (stored && curN && curN.news && curN.news.length)
                    return _promise(curN);

                var cfgNewsFeedArgs = CONFIG.api.feedArgs.news,
                        cacheKey = CONFIG.api.cache.firstNewsCacheKey;

                args = args || {};

                if (!ng.equals(cfgNewsFeedArgs, args)) {
                    args = ng.extend({}, cfgNewsFeedArgs, args);
                    cacheKey = 'news-' + XXH(JSON.stringify(Object.sort(args)), 99999).toString(36);
                }

                args.cacheKey = cacheKey;

                return _get('news', args);
            };

            me.getBranches = function (name) {
                return _get('branches_details')
                        .then(function (resp) {
                            return name ? resp.filter(function (i) {
                                return i.name.toLowerCase().replace(' ', '-') === name.toLowerCase().replace(' ', '-');
                            })[0] || false : resp;
                        });
            };
            me.getAdverts = function () {
                return _get('adverts'
//                        , {
//                            source: CONFIG.api.source,
//                        }
                        );
            };
            me.getImage = function (args) {
                return _get('image', ng.merge({
                    justUrl: true,
                    type: 'image',
                    imagecount: 1
                }, args), true);
            };
            me.getImages = function (args, imgCnt) {
                args = args || {};
                var images = [],
                        l = args.imagecount || imgCnt || 0;

                args.width = Math.max($window.innerWidth, $window.innerHeight);

                for (var i = 1; i <= l; ++i)
                    images.push(me.getImage(ng.merge(args, {
                        imagecount: i
                    })));

                return imgCnt === 1 && images[0] || images;
            };

            me.getPropImages = function (prop, imgCnt) {
//                var img = [CONFIG.ps.logo_img_url + '&file=' + CONFIG.ps.logo_img];
                var img = [CONFIG.blankPropImg];

                if (prop.images && prop.images.image)
                    img = me.getImages({
                        pid: prop.PID,
                        imagecount: prop.images.image.length
                    }, imgCnt);

                return img;
            };

            me.getFiles = function (args) {
                args = args || {};
                args.shortDuration = true;
                args.cacheKey = 'getfile-' + args.pid;

                return _get('getfile', args)
                        .then(function (resp) {
                            var files = [];

                            if (resp && resp.files && resp.files.length > 0) {
                                var f = resp.files;
                                for (var i = 0, l = f.length; i < l; ++i)
                                    if (!$.isEmptyObject(f[i])) {
                                        files.push({
                                            file: CONFIG.api.endpoint + f[i].path + f[i].filename,
                                            ext: f[i].extension
                                        });
                                    }
                            }

                            return files;
                        });
            };

            me.getEvents = function (type, args) {
                args = args || {};

                delete args.timeMax;

                args = ng.extend({
                    key: CONFIG.google.secondApiKey,
                    maxResults: CONFIG.gcEventsItemPerPage * 2,
                    orderBy: 'startTime',
                    singleEvents: true,
                    cacheKey: 'gce',
                    shortDuration: true,
                    extUrl: CONFIG.google.calendarUrl
                }, args);

                // type === 1 - Today events
                // type === 2 - Upcoming events
                if (type) {
                    var d = new Date();
                    d.setHours(24, 0, 0, 0, 0);

                    if (type === 1) {
                        args.timeMax = utilService.getIsoDate(d);
                        d.setHours(-24, 0, 0, 0, 0);
                    }

                    if (!args.timeMin)
                        args.timeMin = utilService.getIsoDate(d);

                    args.cacheKey += '-' + type + '-' + XXH(args.timeMin, 99999).toString(36);

                    if (args.forceRequest)
                        _delLsGce(type);
                }

                return _get(0, args);
            };

            me.getEvent = function (id) {
                var args = {
                    key: CONFIG.google.secondApiKey,
                    extUrl: CONFIG.google.calendarUrl + '/' + id,
                    fields: 'creator/displayName,description,end/dateTime,etag,id,location,start/dateTime,summary',
                    cacheKey: 'gce-' + id
                };

                return _get(0, args);
            };
        }
    ]);

})(angular, jQuery);