(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.RentalYieldCalculator', [
    ]).controller('rentalYieldCtrl', ['$scope', 'utilService',
        function ($scope, utilService) {

            // Init scope variables
            ng.extend($scope, {
                hTitle: 'Rental Yield Calculator',
                max: 9999999999999,
                model: {
                    price: 10000,
                    duty: 0,
                    secHome: false,
                    legalFees: 0,
                    lenderFees: 0,
                    furnishFees: 0,
                    totalCost: 0,
                    monthlyRent: 0,
                    timeUnoccupied: '',
                    annualIncome: 0,
                    annualIncome2: 0,
                    totalCost2: 0,
                    grossYield: ''
                }
            });

            // Init scope functions
            ng.extend($scope, {
                onChangePrice: function () {
                    var model = $scope.model;

                    // 40000 is a threshold, dont calculate below 40000
                    ng.extend(model, model.price >= 40000
                            ? utilService.calcStampDuty(model.price, false, model.secHome)
                            : {duty: 0});

                    $scope.calcTotalCost();
                },
                calcTotalCost: function () {
                    var model = $scope.model;
                    model.totalCost = model.totalCost2 = model.price + model.duty + model.legalFees + model.lenderFees + model.furnishFees;
                    $scope.calcGrossYield();
                },
                calcAnnualIncome: function () {
                    var model = $scope.model,
                            annualRent = 12 * model.monthlyRent;

                    model.annualIncome = model.annualIncome2 = annualRent - (annualRent * +model.timeUnoccupied / 100);
                    $scope.calcGrossYield();
                },
                calcGrossYield: function () {
                    var model = $scope.model;
                    model.grossYield = model.totalCost2 && model.annualIncome2 && (model.annualIncome2 * 100 / model.totalCost2).toFixed(2) || '';
                }
            });
        }
    ]);

})(angular); 