(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.ArrangeViewing', [
    ]).controller('avCtrl', ['$scope', '$routeParams', '$filter', '$sessionStorage', 'CONFIG', 'EAAPI', 'formService',
        function ($scope, $routeParams, $filter, $sessionStorage, CONFIG, EAAPI, formService) {
            var eaLogin = $sessionStorage.eaLogin && $sessionStorage.eaLogin[CONFIG.ps.agent_id] || {};

            ng.extend($scope, {
                hTitle: 'Arrange a Viewing',
                isReqTel: CONFIG.telephoneFieldRequired.arrangeViewing === true,
                model: {
                    property_id: $routeParams.pid,
                    firstname: eaLogin.user_fullname || '',
                    email: eaLogin.user_email || '',
                    phone: eaLogin.user_mobile || '',
                    viewing_date: ''
                },
                property: {}
            });

            ng.extend($scope, {
                onChangeEmail: function () {
                    $scope.avF.email.$errorMsg = null;
                },
                onSubmit: function (isValid) {
                    var vd = $scope.model.viewing_date;
                    formService.onSubmit($scope, isValid, 'arrangeViewing', ng.extend({}, $scope.model, {
                        viewing_date: vd && $filter('date')(vd, 'yyyy-MM-dd HH:mm:ss') || ''
                    }), 'avF', 'M0009', 'self');
                }
            });

            formService.AVS($scope, $routeParams.pid)
                    .then(function () {
                        EAAPI.getBranches(!$scope.isOffice && $scope.property.office)
                                .then(function (resp) {
                                    resp = resp[0] || resp;

                                    if (resp)
                                        $scope.tel = '+44' + resp.telephone.replace(/\s/g, '').substring(1);
                                });
                    });
        }
    ]);
})(angular); 