(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.Team', [
    ]).controller('teamCtrl', ['$scope', '$routeParams', 'EAAPI',
        function ($scope, $routeParams, EAAPI) {
            $scope.hTitle = 'Our Team';

            EAAPI.getTeam()
                    .then(function (resp) {
                        ng.extend($scope, {
                            team: $routeParams.branch && resp.team.filter(function (item) {
                                return item.branch_name && item.branch_name.toLowerCase() === $routeParams.branch.toLowerCase();
                            }) || resp.team,
                            imgUrl: resp.image_url
                        });
                    });
        }
    ]);

})(angular); 