(function (ng, $) {
    'use strict';

    ng.module('MobileSite.controllers.Search', ['angucomplete', 'isteven-multi-select'
    ]).controller('searchCtrl', ['$scope', '$window', '$routeParams', '$location', 'EAAPI', 'CONFIG', 'utilService', 'searchService', 'formService',
        function ($scope, $window, $routeParams, $location, EAAPI, CONFIG, utilService, searchService, formService) {
            var isMobile = $scope.isMobile,
                    needMainPageFilter = searchService.getNeedMainPageFilter(),
                    locationValueField = 'location',
                    propTypeValueField = 'id',
                    emptyFn = ng.noop,
                    setActiveSalesTypeIndex = function () {
                        $scope.activeSalesTypeIndex = $scope.salesTypes.findIndex(function (st) {
                            return st.goal === $scope.model.goal;
                        });
                    },
                    unbind;

            // Initialize fixed positioned multiselect layer function and resize event
            if (!isMobile) {
                var fixPosMulSelLayer = function (type) {
                    var $ct = $('#prop-' + type),
                            $layer = $ct.find('.checkboxLayer.show');

                    if ($layer.length) {
                        var $ctCt = $('#grid-cont'),
                                $form = $ctCt.find('form'),
                                ctCtPos = $ctCt.position(),
                                formPos = $form.position(),
                                layerTop = ctCtPos.top + formPos.top + (type === 'type' && $form || $ct).height() + parseInt($ctCt.css('marginTop'), 10),
                                layerMaxH = Math.ceil($window.innerHeight - layerTop - 10);

                        $layer
                                .css({
                                    position: 'fixed',
                                    top: Math.ceil(layerTop),
                                    left: ctCtPos.left + formPos.left,
                                    width: $scope.isTablet && $form.width() || $ct.width()
                                })
                                .children()
                                .css('maxHeight', layerMaxH < parseInt($ct.find('.checkBoxContainer').css('maxHeight'), 10) && layerMaxH || '');
                    }
                },
                        resize = function () {
                            fixPosMulSelLayer('type');

                            // Location menu can predictive menu,
                            // in this case the function not exists
                            if ($scope.onToggle)
                                fixPosMulSelLayer('location');
                        };

                $($window).on('resize', resize);
            }

            $scope.GETParam = $routeParams;
            $scope.showModal = $scope.GETParam.appurl && $scope.GETParam.device;

            //Clear get params
            $location.search({});

            $scope.salesTypes = utilService.getSalesTypes();

            $scope.beds = [utilService.getBeds(), utilService.getBeds(true)];
            $scope.exclude = ['No', 'Yes'];

            if (needMainPageFilter) {
                searchService.setNeedMainPageFilter(false);
                $scope.model = searchService.getLastFilter();
                delete $scope.model.from;
                delete $scope.model.to;

                setActiveSalesTypeIndex();
                $scope.defSalesType = $scope.salesTypes[$scope.activeSalesTypeIndex];
            } else {
                var isValidDefSel = ng.isNumber(CONFIG.searchTypeDefaultSelection) && CONFIG.searchTypeDefaultSelection <= $scope.salesTypes.length - 1,
                        exclude = CONFIG.getAgentAppCfg('excludeMenuDefaultValue');
                $scope.defSalesType = isValidDefSel ? $scope.salesTypes[CONFIG.searchTypeDefaultSelection] : $scope.salesTypes[0];
                $scope.model = {
                    goal: $scope.defSalesType.goal,
                    bd: $scope.beds[0][0].id,
//                    exclude: ng.isNumber(CONFIG.excludeMenuDefaultValue) && CONFIG.excludeMenuDefaultValue <= $scope.exclude.length - 1 ? CONFIG.excludeMenuDefaultValue : 1
                    exclude: ng.isNumber(exclude) && exclude <= $scope.exclude.length - 1 ? exclude : 1
                };
//                $scope.activeSalesTypeIndex = ($scope.model.goal - 1) % 3;
                setActiveSalesTypeIndex();
            }

            /************* Locations *************/

            if (!CONFIG.enablePredictiveLocationsMenu) {
                $scope.locations = [{location: '', display_name: 'All'}];
                $scope.selectedLocations = [];

                if (!needMainPageFilter)
                    $scope.model.location = $scope.locations[0].location;

                formService.getPTL($scope, 'locations', locationValueField, '', false);

                if (!isMobile)
                    $scope.onToggle = function () {
                        fixPosMulSelLayer('location');
                    };

                EAAPI.getLocations()
                        .then(function (resp) {
                            $scope.locations = $scope.locations.concat(ng.copy(resp));

                            var nearMeLoc = searchService.getNearMeLocation();
                            if (CONFIG.enableNearMe && nearMeLoc && needMainPageFilter) {
                                $scope.showNearMe = true;
                                $scope.location = nearMeLoc;
                                if (!isMobile)
                                    searchService.selectDesktopMultiItems($scope.locations, searchService.getSelectedLocations(), locationValueField);
                                else
                                    $scope.selectedLocations = searchService.getSelectedLocations();
                            } else if (!isMobile)
                                searchService.selectDesktopMultiItems($scope.locations, $scope.model.location, locationValueField);
                        });
            } else {
                $scope.pauseLocationWatcher = true;

                if (needMainPageFilter) {
                    var nearMeLoc = searchService.getNearMeLocation();
                    if (CONFIG.enableNearMe && nearMeLoc) {
                        $scope.showNearMe = true;
                        $scope.location = nearMeLoc;
                    } else
                        $scope.location = searchService.getPredLocation();
                }

                unbind = $scope.$watch('location', function (n) {
                    if ($scope.pauseLocationWatcher)
                        return $scope.pauseLocationWatcher = false;

                    searchService.onLocationChange($scope.model, n);
                });
            }

            /************* Near Me *************/

            if (CONFIG.enableNearMe) {
                if (!ng.isDefined($scope.showNearMe))
                    $scope.showNearMe = false;

                if (!ng.isDefined($scope.location))
                    $scope.location = false;

                $scope.onNearMeClick = function () {
                    if ($scope.isOffline)
                        return $scope.showNearMe = false;

                    searchService.onNearMeClick($scope.model, $scope.location, $scope.showNearMe, $scope.pauseLocationWatcher)
                            .then(function (resp) {
                                if (resp) {
                                    $scope.location = resp.location;
                                    $scope.showNearMe = resp.showNearMe;
                                    $scope.pauseLocationWatcher = resp.pauseLocationWatcher;
                                }
                            });
                };
            }

            /************* Prices *************/

            EAAPI.getPrices({all: true})
                    .then(function (resp) {
                        $scope.prices = resp;
                        $scope.onGoalChange($scope.defSalesType, needMainPageFilter);
                    });

            $scope.needMaxPrice = function () {
                return !(ng.isArray(CONFIG.hideMaxPriceMenuInSearchTypes) && CONFIG.hideMaxPriceMenuInSearchTypes.indexOf($scope.model.goal - 1) >= 0);
            };

            $scope.onGoalChange = function (goal, dontChangePrice) {
                $scope.model.goal = goal.goal;
                if (!dontChangePrice)
                    $scope.model.pmax = $scope.prices[goal.type].max.slice(-1).pop().id;
                $scope.goal = goal.type;
                $scope.freq = goal.period;
                $scope.exclType = $scope.freq ? 'Let' : 'Sold';
                setActiveSalesTypeIndex();
                $scope.isStudentRent = +($scope.model.goal === 3);
            };

            /************* Property Types *************/

            $scope.propTypes = [{id: '0', name: 'Any'}];
            $scope.selectedPropTypes = [];

            if (!needMainPageFilter)
                $scope.model.type = $scope.propTypes[0].id;

            formService.getPTL($scope, 'propTypes', propTypeValueField, 'type');

            if (!isMobile)
                $scope.onPropTypeOpen = function () {
                    fixPosMulSelLayer('type');
                };

            $scope.onSubmit = function (isValid) {
                if (isValid) {
                    $scope.hideSearch = $scope.isTablet;
                    searchService.setModelAndHelpers($scope.model, $scope.location, $scope.selectedLocations, $scope.selectedPropTypes, $scope.showNearMe, $scope.needMaxPrice());
                    searchService.setIsMapView(false);
                    $location.path('/property-for-' + ([1, 4].indexOf($scope.model.goal) >= 0 ? 'sale' : 'rent'));
                }
            };

            //iOS hack - Click event doesn't bubble to document and isteven-multi-select plugin lisening on document
            $('.main-ct').on('click', emptyFn);

            $scope.$on('$destroy', function () {
                $('.main-ct').off('click', emptyFn);
                if (!isMobile)
                    $($window).off('resize', resize);

                if (unbind)
                    unbind();
            });
        }
    ]).controller('resultsCtrl', ['$rootScope', '$scope', '$window', '$document', '$route', '$routeParams', '$timeout', '$location', '$compile', '$interpolate', '$templateCache', 'EAAPI', 'CONFIG', 'utilService', 'searchService', 'formService', 'favouritesService', 'sliderService', 'ngToast',
        function ($rootScope, $scope, $window, $document, $route, $routeParams, $timeout, $location, $compile, $interpolate, $templateCache, EAAPI, CONFIG, utilService, searchService, formService, favouritesService, sliderService, ngToast) {
            var doc = $document[0],
                    google = $window.google,
                    isMobile = $scope.isMobile,
                    pageNr = parseInt($routeParams.p, 10),
                    ipp = CONFIG.itemPerPage,
                    locationValueField = 'location',
                    propTypeValueField = 'id',
                    initedPropTypes = false,
                    ppid = $routeParams.pid,
                    append = 0,
                    getPropsOnFail = function () {
                        pageNr = 1;
                        $scope.nrofprops = 0;
                        $scope.props = [];
                        $scope.needLoadMore = false;
                    },
                    getProps = function () {
                        var promises = [EAAPI.getProperties($scope.model, !!ppid && !append)];
                        if (!initedPropTypes)
                            promises.push(EAAPI.getPropTypes());
                        if (!$scope.props)
                            promises.push(EAAPI.getAdverts());

                        utilService.loadAsyncData(promises)
                                .then(function (resps) {
                                    /************* Property Types *************/

                                    var resp = resps[1];
                                    if (!initedPropTypes) {

                                        initedPropTypes = true;
                                        $scope.propTypes = $scope.propTypes.concat(ng.copy(resp));

                                        if (isMobile)
                                            $scope.ptApi.reInit(true);
                                        else
                                            searchService.selectDesktopMultiItems($scope.propTypes, $scope.model.type, propTypeValueField);

                                        //Load adverts to resp if exists
                                        resp = resps[2];

                                    }

                                    /************* Adverts *************/

                                    if (!$scope.props && resp && resp.sc_include_adverts == 1) {
                                        $scope.adverts = resp.advert;
                                        $scope.advertImgUrl = resp.images_url;

                                    }

                                    /************* Properties *************/

                                    resp = resps[0];

                                    if (resp && ng.isDefined(resp.nrofprops)) {
                                        var vars = searchService.onGetPropsSuccess($scope, resp, ipp, pageNr, append);

                                        pageNr = vars.pageNr;
                                        ppid = vars.ppid || ppid;

//                                        if (!map)
//                                            initMap().then(function () {
//                                                loadMapProperties(resp.entry);
//                                            });
//                                        else
//                                            loadMapProperties(resp.entry);

                                        if (!append)
                                            clearMarkers();

                                        if (!map)
                                            initMap();

                                        loadMapProperties(resp.entry);

//                                        if (ppid && !$scope.isMapView)
//                                            utilService.scrollTo(ppid);

                                        $scope.$digest();
                                    } else
                                        getPropsOnFail();
                                }, function () {
                                    $scope.adverts = [];
                                    getPropsOnFail();
                                });
                    },
                    setPageLimits = function () {
                        $scope.model.to = pageNr * ipp;
                        $scope.model.from = $scope.model.to - ipp;
                    },
                    clear = function () {
                        pageNr = 1;
                        setPageLimits();
                        $location.search('p', null);
                        searchService.setPage(pageNr);
                        $('#grid-cont').scrollTop(0);
                    },
                    onBackBtnPressed = function () {
                        // InAppBrowser loading
                        if ($rootScope.loader.iab)
                            $rootScope.loader.iab.close();
                        // Filters menu is opened
                        else if ($('#filters-menu').hasClass('visible'))
                            hideMask();
                        else if ($scope.props.length === 0 && $scope.showNearMe && !$scope.isMapView)
                            $scope.onNearMeClick();
                        // Back to prev page
                        else
                            $scope.onBackBtn();
                    },
                    onOrientationChange = function () {
                        if ($scope.isMapView)
                            var timer = $timeout(function () {
                                $timeout.cancel(timer);
                                map.panTo(infoBubble.marker.getPosition());

                                if (infoBubble.isOpen()) {
                                    infoBubble.close();
                                    infoBubble.open();
                                }
                            }, 400);
                    },
                    unbind;

            $scope.route = $route.current.$$route.originalPath;
            $scope.enableResultsSlider = CONFIG.getAgentAppCfg('enableResultsSlider');

            $scope.exclude = ['No', 'Yes'];
            if (CONFIG.showSortByPriceMenu)
                $scope.sortprices = {
                    desc: 'Highest Price First (Price)',
                    asc: 'Lowest Price First (Price)',
                    'd-asc': 'Nearest First (Distance)',
                    'd-desc': 'Nearest Last (Distance)'
                };

            $scope.tenantFeesInfo = utilService.decodeHtml(CONFIG.ps.text_tenantfeesinfo);

            $scope.model = searchService.getFilter({goal: $scope.route.indexOf('sale') >= 0 ? ($scope.isCommercial ? 4 : 1) : ($scope.isCommercial ? 5 : 2)}, true);

            //@DEBUG
            if ($scope.isOffice)
                $scope.model.goal = 5;

            pageNr = ng.isNumber(pageNr) && !isNaN(pageNr) && pageNr || 1;

            if (pageNr > 1)
                setPageLimits();

            $scope.beds = utilService.getBeds($scope.model.goal === 3);

            /************* Map View *************/

            var showMeMarker,
                    markers = [],
                    latlngbounds,
                    coords = [],
                    infoBubble,
                    tplPath = 'components/map/',
                    lastClickedMarker,
                    pauseZoomChange = false,
                    map,
                    icon = {
                        path: 'm0,-48',
                        strokeWeight: 3
                    }, interpolationFn = $interpolate($templateCache.get(tplPath + 'pin.html')),
                    setCenterMap = function (pid, marker) {
                        if ($scope.isMapView) {
                            if ($scope.props.length) {
                                if (!marker) {
                                    pid = $routeParams.pid || pid;
                                    marker = markers.filter(function (i) {
                                        return i.prop.PID == pid;
                                    })[0] || markers[0];
                                }

                                pauseZoomChange = true;

                                var timer = $timeout(function () {
                                    $timeout.cancel(timer);

                                    google.maps.event.trigger(map, 'resize');
                                    map.fitBounds(latlngbounds);

                                    var timer2 = setTimeout(function () {
                                        clearTimeout(timer2);
                                        pauseZoomChange = false;
                                    }, 100);
                                }, 200, false);
                                google.maps.event.trigger(marker, 'click', {dontPan: 1});
                            } else
                                google.maps.event.trigger(map, 'resize');
                        }
                    },
                    clearMarkers = function (all) {
                        if (!map)
                            return;

                        for (var i = 0, l = markers.length; i < l; ++i) {
                            google.maps.event.clearInstanceListeners(markers[i]);
                            markers[i].destroy();
                            markers[i].setMap(null);

                            for (var k in markers[i].MarkerLabel)
                                delete markers[i].MarkerLabel[k];
                            for (var k in markers[i])
                                delete markers[i][k];

                            delete markers[i];
                        }

                        if (all && showMeMarker) {
                            google.maps.event.clearInstanceListeners(showMeMarker);
                            showMeMarker.setMap(null);
                            showMeMarker = null;
                        }

                        markers = [];
                        coords = [];
                        latlngbounds = new google.maps.LatLngBounds();
                        infoBubble.close();
                    },
                    loadMapProperties = function (props) {
                        if (!map)
                            return;

                        var currentMarkers = [],
                                marker,
                                selectedMarker,
                                coord,
                                prop,
                                i = 0,
                                l = props.length;

                        for (; i < l; ++i) {
                            prop = props[i];
                            coord = prop.latitude + ',' + prop.longitude;

                            if ($.isNumeric(prop.latitude) && $.isNumeric(prop.longitude) && coords.indexOf(coord) < 0) {
                                coords.push(coord);

                                marker = new Marker({
                                    position: new google.maps.LatLng(prop.latitude, prop.longitude),
                                    icon: icon,
                                    map_icon_label: interpolationFn({
                                        id: 1,
                                        color: '#666',
                                        fa: 'circle'
                                    }),
                                    map: map
                                });

                                marker.prop = {
                                    PID: prop.PID,
                                    PT: prop.PT,
                                    SR: prop.SR,
                                    BD: prop.BD,
                                    A2: prop.A2,
                                    PRICE_TEXT: prop.PRICE_TEXT,
                                    PR: prop.PR,
                                    CUR: prop.CUR,
                                    LET_RENT_FREQUENCY: prop.LET_RENT_FREQUENCY
                                };
                                latlngbounds.extend(marker.position);

                                google.maps.event.addListener(marker, 'click', function (extraOpt) {
                                    //Set zIndex for markers
                                    if (this.MarkerLabel) {
                                        // Set zIndex to default or empty for last clicked marker
                                        if (lastClickedMarker)
                                            lastClickedMarker.MarkerLabel.div.style.zIndex = lastClickedMarker.zIndex || '';

                                        lastClickedMarker = this;

                                        // Set clicked marker zIndex to max value (bring marker to front)
                                        lastClickedMarker.MarkerLabel.div.style.zIndex = google.maps.Marker.MAX_ZINDEX + 1;
                                    }

                                    // Set clicked property
                                    $scope.cProp = this.prop;

                                    infoBubble.marker = this;
                                    infoBubble.setContent($compile($templateCache.get(tplPath + 'info-win.html'))($scope)[0]);
                                    infoBubble.open(map, this);

                                    if (!extraOpt || extraOpt && !extraOpt.dontPan)
                                        var timer = $timeout(function () {
                                            $timeout.cancel(timer);
                                            infoBubble.panToView();
                                        }, 0, false);
                                });

                                currentMarkers.push(marker);

                                if (ppid && prop.PID === ppid)
                                    selectedMarker = marker;
                            }
                        }

                        if (l === 0 && markers.length === 0)
                            clearMarkers();
                        else {
                            markers = markers.concat(currentMarkers);
                            setCenterMap(null, selectedMarker || currentMarkers[0]);
                        }
                    },
                    initCustomMapCtrls = function () {
                        var cPos = google.maps.ControlPosition;

                        // Set Load Ctrl
                        map.controls[cPos.TOP_RIGHT].push($compile($templateCache.get(tplPath + 'load-btn.html'))($scope)[0]);

                        // View Ctrl
                        $scope.isNormalView = true;
                        $scope.onClickMapView = function () {
                            $scope.isNormalView = !$scope.isNormalView;
                            map.setMapTypeId(google.maps.MapTypeId[$scope.isNormalView ? 'ROADMAP' : 'SATELLITE']);
                        };

                        // Set View Ctrl
                        map.controls[cPos.LEFT_BOTTOM].push($compile($templateCache.get(tplPath + 'view-btn.html'))($scope)[0]);

                        // Show Me Ctrl
                        var onSuccess = function (pos) {
                            pos = new google.maps.LatLng(pos.coords.latitude, pos.coords.longitude);

                            if (!showMeMarker) {
                                showMeMarker = new Marker({
                                    position: pos,
                                    icon: icon,
                                    map_icon_label: interpolationFn({
                                        id: 2,
                                        color: '#' + CONFIG.ps.buttons_color.replace(/\/+$/, '').split('/').pop(),
                                        fa: 'user'
                                    })
                                });

                                google.maps.event.addListener(showMeMarker, 'click', function () {
                                    infoBubble.setContent('<span>' + CONFIG.MESSAGE.M0003 + '</span>');
                                    infoBubble.open(map, this);
                                });

                            } else {
                                showMeMarker.setPosition(pos);
                                showMeMarker.setVisible(true);
                            }

                            showMeMarker.setMap(map);

                            map.panTo(showMeMarker.getPosition());
                            google.maps.event.trigger(showMeMarker, 'click');

                            var timer = $timeout(function () {
                                $timeout.cancel(timer);
                                $rootScope.$broadcast('loadingComplete');
                            }, 1);
                        }, onError = function (error) {
                            $scope.isHiddenShowMe = true;
                            searchService.onError(error);
                        };

                        $scope.isHiddenShowMe = true;
                        $scope.onClickShowMe = function () {
                            if ($scope.isHiddenShowMe) {
                                if ($window.navigator.geolocation) {
                                    $scope.isHiddenShowMe = false;
                                    $rootScope.$broadcast('loadingStarted');
                                    $window.navigator.geolocation.getCurrentPosition(onSuccess, onError, searchService.geoLocOpts);
                                } else
                                    ngToast.create(CONFIG.ERROR.E0008);
                            } else {
                                $scope.isHiddenShowMe = true;
                                showMeMarker.setVisible(false);
                                if (markers.length) {
//                                    map.fitBounds(latlngbounds);
                                    google.maps.event.trigger(infoBubble.marker || markers[0], 'click');
                                } else
                                    infoBubble.close();
                            }
                        };

                        // Set Show Me Ctrl
                        map.controls[cPos.LEFT_BOTTOM].push($compile($templateCache.get(tplPath + 'show-me-btn.html'))($scope)[0]);
                    },
                    initMap = function () {
//                        var deferred = $q.defer();

                        if (google && google.maps) {
                            $window.markerOverride();

                            latlngbounds = new google.maps.LatLngBounds();
                            infoBubble = new InfoBubble({
                                shadowStyle: 0,
                                padding: 5,
                                backgroundColor: 'rgba(57,57,57,0.7)',
                                borderRadius: 5,
                                arrowSize: 15,
                                borderWidth: 1,
                                borderColor: '#2c2c2c',
                                disableAutoPan: true,
                                hideCloseButton: true,
                                arrowPosition: 50,
                                backgroundClassName: 'info-bubble',
                                arrowStyle: 0,
                                maxWidth: 360
                            });

                            // Default London coordinates
                            var lat = 51.507351,
                                    lng = -0.127758;

                            if ($scope.props[0]) {
                                lat = $scope.props[0].latitude;
                                lng = $scope.props[0].longitude;
                            }

                            // Create map
                            if (!$rootScope.map) {
//                                $('#grid-cont.results-ct').append('<div id="googleMap" class="ea-map"></div>');
                                $rootScope.map = map = new google.maps.Map($('#googleMap')[0], {
                                    zoom: 14,
                                    disableDefaultUI: true,
                                    scrollwheel: false,
                                    zoomControl: true,
                                    zoomControlOptions: {
                                        position: google.maps.ControlPosition.RIGHT_BOTTOM,
                                        style: google.maps.ZoomControlStyle.SMALL
                                    },
                                    center: new google.maps.LatLng(lat, lng)
                                });
//                                deferred.resolve();
                            } else {
//                                setTimeout(function () {
                                $('#googleMap').replaceWith($rootScope.$map);
//                                    $('#googleMap').appendTo('#grid-cont.results-ct');
                                map = $rootScope.map;
                                map.setCenter(new google.maps.LatLng(lat, lng));
                                google.maps.event.trigger(map, 'resize');

//                                    deferred.resolve();
//                                }, 500);
                            }

                            // Show/hide map
                            $scope.isMapView = searchService.getIsMapView();
                            $('#googleMap')[$scope.isMapView && 'show' || 'hide']();

                            // Init Custom Map Controls
                            initCustomMapCtrls();

                            map.listeners = [
                                // On resize window, center map
                                google.maps.event.addListener(map, 'zoom_changed', function (e) {
                                    if (!pauseZoomChange && infoBubble.marker)
                                        map.panTo(infoBubble.marker.getPosition());
                                }),
                                // On map click close the infoBubble
                                google.maps.event.addDomListener(map, 'click', function () {
                                    infoBubble.close();
                                })
                            ];
                        }// else
//                            deferred.reject();

//                        return deferred.promise;
                    };

            $scope.toggleMapView = function () {
                if (!map)
                    return;

                if ($scope.isOffline) {
                    $scope.isMapView = false;
                    $('#googleMap')[$scope.isMapView && 'show' || 'hide']();
                    return searchService.setIsMapView($scope.isMapView);
                }

                $scope.isMapView = !$scope.isMapView;
                $('#googleMap')[$scope.isMapView && 'show' || 'hide']();
                searchService.setIsMapView($scope.isMapView);

                var timer = $timeout(function () {
                    $timeout.cancel(timer);

                    if ($scope.isMapView)
                        setCenterMap();
                    else {
                        $('#grid-cont div.flexslider').each(function () {
                            $(this).data('flexslider').resize();
                        });

                        if (infoBubble.marker && infoBubble.marker.prop)
                            utilService.scrollTo(infoBubble.marker.prop.PID);
                    }
                }, 100);
            };

            /************* Load Properties *************/

            getProps();

            /************* Locations *************/

            if (!CONFIG.enablePredictiveLocationsMenu) {
                $scope.locations = [{location: '', display_name: 'Show all locations'}];
                $scope.selectedLocations = [];

                //Desktop Location
                if (!isMobile) {
                    searchService.selectDesktopMultiItems($scope.locations, $scope.model.location, locationValueField);

                    $scope.onToggle = function () {
                        $('#location + .checkboxLayer.show').length ? showMask(35) : hideMask();
                    };

                    $scope.onLocationItemClick = function (data) {
                        searchService.onDesktopMultiItemClick(data, $scope.locations, $scope.selectedLocations, locationValueField);
                        var timer = $timeout(function () {
                            $timeout.cancel(timer);
                            $scope.onSubmit($scope.searchF.$valid);
                        }, 50);
                    };
                } else //Mobile Location
                    $scope.onLocationChange = function () {
                        $scope.onSubmit($scope.searchF.$valid);
                    };

                EAAPI.getLocations()
                        .then(function (resp) {
                            $scope.locations = $scope.locations.concat(ng.copy(resp));

                            var nearMeLoc = searchService.getNearMeLocation();
                            if (CONFIG.enableNearMe && nearMeLoc) {
                                $scope.showNearMe = true;
                                $scope.location = nearMeLoc;
                                if (!isMobile)
                                    searchService.selectDesktopMultiItems($scope.locations, searchService.getSelectedLocations(), locationValueField);
                                else
                                    $scope.selectedLocations = searchService.getSelectedLocations();
                            } else if (!isMobile) {
                                var noneSelected = searchService.selectDesktopMultiItems($scope.locations, $scope.model.location, locationValueField);

                                // On init, when not ticked anyithing and the button display value is "None Selected",
                                // then set the button text to the model location value if exists.
                                // This is for displaying the branch name when jumped from the contact us page property filter by branch link
                                if (noneSelected && $scope.model.location) {
                                    $timeout(function () {
                                        $('#location').text($scope.model.location);
                                    }, 0, false);
                                }
                            }
                        });
            } else {
                $scope.pauseLocationWatcher = true;

                var nearMeLoc = searchService.getNearMeLocation();
                if (CONFIG.enableNearMe && nearMeLoc) {
                    $scope.showNearMe = true;
                    $scope.location = nearMeLoc;
                } else
                    $scope.location = searchService.getPredLocation();

                unbind = $scope.$watch('location', function (n) {
                    if ($scope.pauseLocationWatcher)
                        return $scope.pauseLocationWatcher = false;

                    searchService.onLocationChange($scope.model, n);
                    $scope.onSubmit($scope.searchF.$valid);
                });
            }

            /************* Near Me *************/

            if (CONFIG.enableNearMe) {
                if (!ng.isDefined($scope.showNearMe))
                    $scope.showNearMe = false;

                if (!ng.isDefined($scope.location))
                    $scope.location = false;

                $scope.onNearMeClick = function () {
                    if ($scope.isOffline)
                        return $scope.showNearMe = false;

                    searchService.onNearMeClick($scope.model, $scope.location, $scope.showNearMe, $scope.pauseLocationWatcher)
                            .then(function (resp) {
                                if (resp) {
                                    $scope.location = resp.location;
                                    $scope.showNearMe = resp.showNearMe;
                                    $scope.pauseLocationWatcher = resp.pauseLocationWatcher;

                                    if (!CONFIG.enablePredictiveLocationsMenu || CONFIG.enablePredictiveLocationsMenu && $scope.showNearMe)
                                        $scope.onSubmit($scope.searchF.$valid);
                                }
                            });
                };
            }

            /************* Prices *************/

            $scope.needMaxPrice = !(ng.isArray(CONFIG.hideMaxPriceMenuInSearchTypes) && CONFIG.hideMaxPriceMenuInSearchTypes.indexOf($scope.model.goal - 1) >= 0);
            $scope.needMinPrice = !(ng.isArray(CONFIG.hideMinPriceMenuInSearchTypes) && CONFIG.hideMinPriceMenuInSearchTypes.indexOf($scope.model.goal - 1) >= 0);
            var salesType = utilService.getSalesTypes().filter(function (i) {
                return i.goal === $scope.model.goal;
            })[0];

            if ($scope.needMaxPrice || $scope.needMinPrice) {
                EAAPI.getPrices({all: true})
                        .then(function (resp) {
                            $scope.minPrices = resp[salesType.type].min;
                            $scope.maxPrices = resp[salesType.type].max;
                            $scope.freq = salesType.period;
                        });

            }
            $scope.exclType = salesType.period ? 'Let' : 'Sold';

            /************* Property Types *************/

            $scope.ptApi = {};
            $scope.propTypes = [{id: '0', name: 'Any'}];
            $scope.selectedPropTypes = [];

            if (!isMobile) {
                $scope.onPropTypeOpen = function () {
                    var $ct = $('#prop-type'),
                            $layer = $ct.find('.checkboxLayer.show');

                    if ($layer.length) {
                        var $table = $ct.parents('table').first(),
                                tableWidth = $table.width(),
                                layerWidth = $ct.width(),
                                itemMaxWidth = 15 + Math.max.apply(null, $layer.find('.acol').map(function () {
                                    return $(this).width();
                                })),
                                cssObj = {
                                    position: 'fixed'
                                };

                        if (layerWidth < itemMaxWidth) {
                            layerWidth = itemMaxWidth;
                        }

                        cssObj.width = layerWidth > tableWidth && tableWidth || layerWidth;
                        cssObj[CONFIG.showSortByPriceMenu && 'right' || 'left'] = $table.position().left;

                        $layer.css(cssObj);
                    }
                };

                $($window).on('resize', $scope.onPropTypeOpen);

                formService.getPTL($scope, 'propTypes', propTypeValueField, 'type', false);
            }

            ng.extend($scope, {
                toggleFavourites: favouritesService.toggleFavourites,
                getPropImages: EAAPI.getPropImages,
                scrollTo: function (last) {
                    if (last && ppid && !$scope.isMapView) {
                        var delay = $scope.props.length * 15;
                        utilService.scrollTo(ppid, append && 50 || (delay < 1000 && 1000 || delay));
                    }

                    if (last) {
                        append = 0;
                        ppid = 0;
                    }
                },
                initSlider: sliderService.initSlider,
                onAlertClick: function () {
                    searchService.setNeedAlertFilter(true);
                    $location.path('/alerts');
                },
                loadMore: function () {
                    pageNr += 1;
                    $location.search('p', pageNr);
                    searchService.setPage(pageNr);
                    setPageLimits();

                    $scope.onSubmit($scope.searchF.$valid, true);
                },
                onSubmit: function (isValid, isAppend) {
                    if (isValid) {
                        append = isAppend;

                        if (!append)
                            clear();

                        searchService.setModelAndHelpers($scope.model, $scope.location, $scope.selectedLocations, $scope.selectedPropTypes, $scope.showNearMe, $scope.needMaxPrice, $scope.needMinPrice);

                        var model = $scope.model;

                        // Distance sort
                        if (model.sortprice.indexOf('d-') === 0) {
                            searchService.getCurrentPosition()
                                    .then(function (pos) {
                                        ng.extend(model, {
                                            sortdistance: model.sortprice.replace('d-', ''),
                                            sortlat: pos.coords.latitude,
                                            sortlng: pos.coords.longitude
                                        })

                                        getProps(append);
                                    });
                        } else {
                            delete model.sortdistance;
                            delete model.sortlat;
                            delete model.sortlng;

                            getProps(append);
                        }
                    }
                }
            });

            /*$scope.getPropRange = function(p) {
             var a = p * ipp,
             l = $scope.props.length,
             b = l % ipp;
             
             return [(p - 1) * ipp - (a - l - (b !== 0 ? ipp - b : 0)), a - (a - l)];
             };*/

//            $scope.onReturnBtnClick = function () {
//                searchService.setNeedMainPageFilter(true);
//                $location.path($scope.mainPath);
//                $scope.onNearMeClick();
//            };

            if ($window.screen)
                $window.addEventListener("orientationchange", onOrientationChange, false);

            //Device back button pressed
            doc.addEventListener("backbutton", onBackBtnPressed, false);

            $scope.$on('$destroy', function () {
                if (map) {
                    var cPos = google.maps.ControlPosition,
                            i = 0,
                            l = map.listeners && map.listeners.length || 0;

                    clearMarkers(1);

                    for (; i < l; ++i)
                        google.maps.event.removeListener(map.listeners[i]);

                    map.listeners = null;

                    map.controls[cPos.TOP_RIGHT].pop();
                    map.controls[cPos.LEFT_BOTTOM].pop();
                    map.controls[cPos.LEFT_BOTTOM].pop();

                    lastClickedMarker = null;

                    if (!$rootScope.$map)
                        $rootScope.$map = $('#googleMap').detach();
//                    $('#googleMap').appendTo('body').hide();
                }

                if ($scope.onPropTypeOpen)
                    $($window).off('resize', $scope.onPropTypeOpen);

                $window.removeEventListener('orientationchange', onOrientationChange, false);
                doc.removeEventListener('backbutton', onBackBtnPressed, false);

                sliderService.destroy($scope.route);

                if (unbind)
                    unbind();
            });
        }
    ]);
})(angular, jQuery); 