(function (ng, $) {
    'use strict';

    ng.module('MobileSite.controllers.Presentation', [
    ]).controller('presCtrl', ['$rootScope', '$scope', '$timeout', '$route', 'EAAPI', 'sliderService',
        function ($rootScope, $scope, $timeout, $route, EAAPI, sliderService) {
            if (!$rootScope.loader.loading)
                $('section.pres-ct').removeClass('loading');
            
            $scope.getPropImages = EAAPI.getPropImages;
            
            EAAPI.get('presentation')
                    .then(function (resp) {
                        $('#propertyGallery .loader').show();
                        $scope.properties = resp.entry
                        var timer1 = $timeout(function () {
                            $timeout.cancel(timer1);
                            sliderService.init('propertyGallery', 'pres-ct');
                            $('section.pres-ct').removeClass('loading');
                        }, 0, false);
                    });

            $scope.$on('$destroy', function () {
                sliderService.destroy($route.current.$$route.originalPath);
            });
        }
    ]);

})(angular, jQuery);