(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.ForgotPassword', [
    ]).controller('forgotPasswordCtrl', ['$scope', 'formService',
        function ($scope, formService) {
            var args,
                    fn = 'app_forgot_password',
                    emailArgName = 'user',
                    nsLLPath = 'landlord/';

            // Init scope variables
            $scope.email = '';

            if ($scope.isLL) {
                $scope.loginPath = '#/' + nsLLPath + 'login';

                fn = nsLLPath + 'forgot-password';
                emailArgName = 'email';
            } else
                $scope.loginPath = '#/login';

            // Init scope functions
            ng.extend($scope, {
                onChangeEmail: function () {
                    $scope.forgotF.email.$errorMsg = null;
                },
                onSubmit: function (isValid) {
                    if (isValid) {
                        args = {};
                        args[emailArgName] = $scope.email;

                        formService.onSubmit($scope, isValid, fn, args, 'forgotF', 'M0010', 'login', 'E0003');
                    }
                }
            });
        }
    ]);

})(angular);