(function (ng) {
    'use strict';

    ng.module('MobileSite.services.Cordova', [
    ]).factory('deviceReady', ['$window', '$document',
        function ($window, $document) {
            return function (done) {
                if (typeof $window.cordova === 'object') {
                    $document[0].addEventListener('deviceready', function () {
                        done();
                    }, false);
                } else {
                    done();
                }
            };
        }
    ]).service('cUtil', ['$window', '$document',
        function ($window, $document) {
            //Private variables
            var me = this,
                    dir = 'EA',
                    isCordova = ng.isDefined($window.cordova),
                    platform = isCordova && $window.device ? $window.device.platform.toLowerCase() : null;

            //Public variables
            //Public functions
//            me.alert = function (a) {
//                setTimeout(function () {
//                    alert(a);
//                }, 0);
//            };

            //Download a file form given url and save it to the app directory
            me.download = function (fileUrl, fileName, ns, done) {
                var doneIsFn = typeof done === 'function';

                if (isCordova) {
                    ns = ns || '';
                    fileName = ns + fileName;

                    var deleteOldFiles = function (dirEntry) {
                        var directoryReader = dirEntry.createReader();

                        directoryReader.readEntries(function (entries) {
                            for (var i = 0, l = entries.length; i < l; ++i)
                                if (entries[i].name !== fileName && entries[i].name.indexOf(ns) >= 0)
                                    entries[i].remove();
                        });
                    },
                            downloadSuccess = function (fileEntry, dirEntry) {
                                if (doneIsFn)
                                    done(fileEntry.toURI());

                                if (ns.length)
                                    deleteOldFiles(dirEntry);
                            },
                            downloadFail = function () {
                                if (doneIsFn)
                                    done(false);
                            },
                            downloadFile = function (dirEntry) {
                                new FileTransfer().download(fileUrl, dirEntry.toURL() + fileName, function (fileEntry) {
                                    downloadSuccess(fileEntry, dirEntry);
                                }, downloadFail);
                            },
                            requestFileSystemSuccess = function (fileSystem) {
                                fileSystem.root.getDirectory(dir, {
                                    create: true
                                }, downloadFile);
                            };

                    $document[0].addEventListener('deviceready', function () {
                        $window.requestFileSystem(LocalFileSystem.PERSISTENT, 0, requestFileSystemSuccess);
                    }, false);
                } else if (doneIsFn)
                    done(false);
            };

            //Get file form app directory (downloaded file)
            me.findFile = function (fileName, ns, done) {
                var doneIsFn = typeof done === 'function';

                if (isCordova && doneIsFn) {
                    ns = ns || '';

                    var filePath = (platform === 'ios' ? $window.cordova.file.documentsDirectory : $window.cordova.file.dataDirectory + '/files/') + dir + '/' + ns + fileName,
                            resolveLocalFileSystemURLSuccess = function (fileEntry) {
                                done(fileEntry.toURI());
                            },
                            resolveLocalFileSystemURLFail = function () {
                                done(false);
                            };

                    $document[0].addEventListener('deviceready', function () {
                        $window.resolveLocalFileSystemURL(filePath, resolveLocalFileSystemURLSuccess, resolveLocalFileSystemURLFail);
                    }, false);
                } else if (doneIsFn)
                    done(false);
            };

            //Find and download file
            me.getFile = function (fileName, fileUrl, ns, imageDownloaded, done) {
                if (typeof done === 'function') {
                    if (isCordova) {
                        me.findFile(fileName, ns, function (img) {
                            if (img)
                                done(img);
                            else if (ng.isDefined($window.localFeeds))
                                done('images/dynamic/' + ns + fileName);

                            if (!imageDownloaded)
                                me.download(encodeURI(fileUrl), fileName, ns, function (img) {
                                    if (img)
                                        done(img, true);
                                });
                        });
                    } else
                        done(fileUrl);
                }
            };
        }
    ]);
})(angular);