(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.Favourites', [
    ]).controller('favCtrl', ['$scope', '$localStorage', '$location', '$route', '$routeParams', 'ngToast', 'CONFIG', 'EAAPI', 'utilService', 'searchService', 'favouritesService', 'sliderService',
        function ($scope, $localStorage, $location, $route, $routeParams, ngToast, CONFIG, EAAPI, utilService, searchService, favouritesService, sliderService) {
            ng.extend($scope, {
                hTitle: 'My Favourites',
                enableResultsSlider: CONFIG.getAgentAppCfg('enableResultsSlider'),
                route: $route.current.$$route.originalPath,
                postPath: '#/news/post/',
                tenantFeesInfo: utilService.decodeHtml(CONFIG.ps.text_tenantfeesinfo),
                isFav: true
            });

            ng.extend($scope, {
                toggleFavourites: function (id) {
                    var res = favouritesService.toggleFavourites(id);

                    if (res === 0) {
                        var type = id[0];
                        id = id.slice(1);

                        var index = $scope.items.indexOf($scope.items.filter(function (i) {
                            return i.rId === id;
                        })[0] || {});

                        if (index >= 0) {
                            if (type === 'p')
                                sliderService.destroy($scope.route, id);

                            $scope.items.splice(index, 1);

                            removedItemCnt++;

                            setScope($scope.items, $scope.nrOfItems - 1);
                        }
                    }
                    ngToast.create(CONFIG.MESSAGE.M0001);
                },
                getPropImages: EAAPI.getPropImages,
                scrollTo: function (last) {
                    if (last) {
                        if (ppid) {
                            var delay = $scope.items.length * 25;
                            utilService.scrollTo(ppid, append && 50 || (delay < 1000 && 1000 || delay), 0, 1);
                        }
                        append = 0;
                        ppid = 0;
                    }
                },
                initSlider: sliderService.initSlider,
                loadMore: function () {
                    pageNr += 1;

                    setPageLimits();

                    append = 1;
                    getItems();
                }
            });

            //@DEBUG
//            $localStorage.eaFavourites[CONFIG.ps.agent_id] = {"e5s75bq1vpkslcosad12mjcff0o":1,"n7612":1,"e48pk5qqnak3f6v49j4dqprmrfs":1, "pTestOID":1, "ebp204tuq57bhhiqof3qfas34jg":1,"n7368":1,"n7601":1,"egtqimrkiakkovr9u8te69dcem4":1,"n7367":1,"n7362":1,"n7363":1,"pTEST121":1,"e8oa72ae5h0lip0nurhbjstp5og":1,"p6359940":1,"p6359010":1,"p6359687":1,"p6359282":1,"p6359281":1,"n7366":1,"n7064":1,"n7067":1,"n7068":1,"n7069":1,"n7070":1, "e0i1smkncad4oqqjdj0nvquftq4_20161212T100000Z": 1};
//            $localStorage.eaFavourites[CONFIG.ps.agent_id] = {"n1593": 1, "pTEST121": 1, "pCITYIP_2080645": 1, "n1583": 1, "pCWARFIP_2083752": 1, "pCWARFIP_105470": 1, "n7600": 1, "pCWARFIP_105476": 1, "n7346": 1, "n4648": 1, "pCWARFIP_117956": 1, "n6261": 1, "n4649": 1, "pCWARFIP_113104": 1, "pCWARFIP_105464": 1, "pCWARFIP_413948": 1, "pCWARFIP_2031815": 1, "pCWARFIP_235070": 1, "pCWARFIP_1831464": 1, "pCWARFIP_439545": 1, "n4031": 1, "n1728": 1, "n1740": 1, "n1725": 1, "pCWARFIP_241704": 1};
//            $localStorage.eaFavourites[CONFIG.ps.agent_id] = {"pTEST121": 1, "pCITYIP_2080645": 1, "pCWARFIP_2083752": 1, "pCWARFIP_105470": 1, "pCWARFIP_105476": 1, "pCWARFIP_117956": 1, "pCWARFIP_113104": 1, "pCWARFIP_105464": 1, "pCWARFIP_413948": 1, "pCWARFIP_2031815": 1, "pCWARFIP_235070": 1, "pCWARFIP_1831464": 1, "pCWARFIP_439545": 1, "pCWARFIP_241704": 1};

            var k, type, id,
                    ipp = CONFIG.itemPerPage,
                    from = 0,
                    to = ipp,
                    removedItemCnt = favouritesService.removedItemCnt || 0,
                    pageNr = +$routeParams.p || favouritesService.curPage,
                    ppid = $routeParams.pid,
                    append = 0,
                    ids = [],
                    lsItems = $localStorage.eaFavourites[CONFIG.ps.agent_id],
                    setItems = function (items, recs, idField) {
                        for (var i = 0, l = recs.length; i < l; ++i) {
                            recs[i].rId = recs[i][idField];
                            items[ids.indexOf(recs[i][idField])] = recs[i];
                        }
                    },
                    setPageLimits = function () {
                        to = pageNr * ipp - removedItemCnt;
                        from = to - ipp;
                    },
                    setScope = function (items, index) {
                        // Find out load more btn is needed and calculate next number on btn 
                        ng.extend($scope, {
                            needLoadMore: ipp * pageNr - removedItemCnt < index,
                            nextnumber: ipp,
                            nrOfItems: index
                        });

                        if ($scope.needLoadMore && (index - (ipp * pageNr - removedItemCnt)) < ipp)
                            $scope.nextnumber = index - (ipp * pageNr - removedItemCnt);

                        $scope.$evalAsync(function () {
                            if (append) {
                                $scope.items.push.apply($scope.items, items);
                                ppid = items[0].rId;
                            } else {
                                $scope.items = items;
                            }

                            favouritesService.curFavs = ng.copy($scope.items);
                            favouritesService.curPage = pageNr;

//                            if (ppid)
//                                utilService.scrollTo(ppid);
//
//                            ppid = 0;
                        });
                    },
                    getItems = function () {
                        var items = favouritesService.curFavs || [],
                                promises = [];

                        if (append || !items || !items.length) {
                            var pids = [], nids = [], eids = [],
                                    index = 0;

                            if (from === 0)
                                removedItemCnt = favouritesService.removedItemCnt = 0;

                            items = [];

                            if (!append)
                                ids.length = 0;

                            // Sort ids into separate arrays by type
                            for (k in lsItems) {
                                if (index >= from && index < to) {
                                    type = k[0];
                                    id = k.slice(1);

                                    ids.push(id);

                                    // Property record
                                    if (type === 'p')
                                        pids.push(id);
                                    // News record
                                    else if (type === 'n')
                                        nids.push(id);
                                    // Google calendar event record
                                    else if (type === 'e')
                                        eids.push(id);
                                }

                                ++index;
                            }

                            // Get property records
                            if (pids.length)
                                promises.push(EAAPI.getProperties({
                                    property_ids: pids.join(','),
                                    sortprice: 'desc'
                                }));

                            // Get news records
                            if (nids.length)
                                promises.push(EAAPI.getNews({
                                    nids: nids.join(',')
                                }));

                            // Get Google Calendar event records
                            if (eids.length)
                                promises.push(utilService.loadAsyncData(eids.map(function (eid) {
                                    return EAAPI.getEvent(eid);
                                })));
                        }

                        if (promises.length)
                            utilService.loadAsyncData(promises)
                                    .then(function (resps) {
                                        var resp = ng.copy(resps),
                                                props = [], news = [], events = [],
                                                i = 0,
                                                l = resps.length,
                                                parseResp = function (respItem) {
                                                    if (ng.isArray(respItem)) {
                                                        respItem.forEach(function (eItem) {
                                                            if (!eItem.error)
                                                                events.push(eItem);
                                                        });

                                                        utilService.setEvent(events);
                                                    } else if (respItem.entry)
                                                        props.push.apply(props, respItem.entry);
                                                    else if (respItem.news) {
                                                        utilService.setPost(respItem, $scope.logo);
                                                        news.push.apply(news, respItem.news);
                                                    }
                                                };

                                        resp.forEach(parseResp);

                                        setItems(items, props, 'PID');
                                        setItems(items, news, 'id');
                                        setItems(items, events, 'id');

                                        // Clear empty values and set to scope
                                        items = items.filter(Boolean);

                                        setScope(items, index);
                                    });
                        else if (items.length)
                            setScope(items, Object.keys(lsItems).length);
                        else
                            $scope.items = items;
                    },
                    unbind = $scope.$on('$routeChangeStart', function (e, next) {
                        favouritesService.removedItemCnt = removedItemCnt;
                        if (next.$$route.originalPath.indexOf('news') >= 0)
                            utilService.backTo = '/favourites';
                        else
                            searchService.setPage(pageNr);
                    });

            $location.search('p', null);

            pageNr = ng.isNumber(pageNr) && !isNaN(pageNr) && pageNr || 1;

            if (pageNr > 1)
                setPageLimits();

            getItems();

            $scope.$on('$destroy', function () {
                unbind();
                sliderService.destroy($scope.route);
            });
        }
    ]);

})(angular); 