(function (ng) {
    'use strict';

    ng.module('MobileSite.services.Favourites', [
    ]).service('favouritesService', ['$localStorage', 'CONFIG',
        function ($localStorage, CONFIG) {
            var me = this;

            me.curFavs = [];

            me.initFavourites = function () {
                if (!$localStorage.eaFavourites)
                    $localStorage.eaFavourites = {};
                if (!$localStorage.eaFavourites[CONFIG.ps.agent_id])
                    $localStorage.eaFavourites[CONFIG.ps.agent_id] = {};
            };

            me.toggleFavourites = function (id) {
                if (!id)
                    return;

                var ret = 0;
                if ($localStorage.eaFavourites[CONFIG.ps.agent_id][id])
                    delete $localStorage.eaFavourites[CONFIG.ps.agent_id][id];
                else
                    ret = $localStorage.eaFavourites[CONFIG.ps.agent_id][id] = 1;

                me.curFavs.length = 0;
                me.curPage = 1;

                return ret;
            };

            me.initFavourites();
        }
    ]);
})(angular);