(function (ng, $) {
    'use strict';

    ng.module('MobileSite.controllers.News', [
    ]).controller('newsCtrl', ['$scope', '$routeParams', '$location', '$filter', '$pageTitle', 'EAAPI', 'CONFIG', 'utilService', 'favouritesService',
        function ($scope, $routeParams, $location, $filter, $pageTitle, EAAPI, CONFIG, utilService, favouritesService) {
            var ipp = CONFIG.newsItemPerPage,
                    args = {
                        from: 0,
                        to: ipp
                    }, tag = $routeParams.tag,
                    page,
                    tagPath = '/news' + (tag && ('/tags/' + tag) || ''),
                    cacheType = 'cur' + (tag && 'Tag' || '') + 'News',
                    findPost = function (news, backToItem) {
                        var post, i = 0, l = news.length;
                        for (; i < l; ++i)
                            if (news[i].slug === $routeParams.postTitle) {
                                post = news[i];
                                utilService[cacheType].backToNews = backToItem && i + 1 || -1;
                                break;
                            }

                        return post;
                    },
                    setDetailsPage = function (resp) {
                        var post = $scope.post;

                        if (post) {
                            // For favourites page set pid in the back url
                            ng.extend($scope, {
                                hTitle: post.title,
                                hBackUrl: '#' + (utilService.backTo && (utilService.backTo + '?pid=' + post.id) || tagPath),
                                hasVideoUrl: $scope.post.video_url && !$scope.isOffline,
                                src: post.image && (resp.image_url + $filter('format')(CONFIG.api.feedImgQuery, post.image, 600, 600))
                            });
                            
                            post.content = utilService.decodeHtml(post.content);
                            
                            utilService.backTo = 0;
                            $pageTitle.set(post.meta_page_title, true);

                            $scope.news.splice($scope.news.indexOf(post), 1);
                            $scope.news.splice(4);
                        }
                    },
                    getNews = function (append) {
                        EAAPI.getNews(args, !append && (utilService[cacheType].backToNews || $routeParams.postTitle), cacheType)
                                .then(function (data) {
                                    var resp = ng.copy(data),
                                            nrOfItems = resp.nrofitems;

                                    // Format posts variables
                                    utilService.setPost(resp, $scope.logo);

                                    // Set scope variables
                                    if (append) {
                                        $scope.news.push.apply($scope.news, resp.news);
                                        utilService.scrollTo('#grid-cont article.post-item:eq(' + ((page - 1) * ipp) + ')', 50, 1);
                                    } else {
                                        $scope.news = resp.news;
                                        if (utilService[cacheType].backToNews > 0)
                                            utilService.scrollTo('#grid-cont.blog-list article.post-item:eq(' + (utilService[cacheType].backToNews - 1) + ')', 1000, 1);
                                    }

                                    page = !append && utilService[cacheType].backToNews && utilService[cacheType].newsPage || page || 1;

                                    if (utilService.curNews)
                                        utilService.curNews.backToNews = 0;
                                    if (utilService.curTagNews)
                                        utilService.curTagNews.backToNews = 0;

                                    // Details page
                                    if ($routeParams.postTitle) {
                                        // Find post in current news (news locale cache)
                                        $scope.post = findPost($scope.news, 1);

                                        // If this post opened from author page, there is a chance the post is not in the news locale cache
                                        // Find post in author news
                                        if (!ng.isDefined($scope.post) && utilService.curAuthorNews)
                                            $scope.post = findPost(utilService.curAuthorNews);

                                        // If post not in news locale cache and not in author news, then get by slug from server
                                        if (!ng.isDefined($scope.post))
                                            EAAPI.getNews(ng.extend(args, {
                                                slug: $routeParams.postTitle
                                            })).then(function (resp) {
                                                if (resp.nrofitems > 0) {
                                                    utilService.setPost(resp, $scope.logo);
                                                    $scope.post = resp.news[0];
                                                    setDetailsPage(resp);
                                                } else {
                                                    utilService.setErrorMsgKey('E0006');
                                                    return $location.path('/error');
                                                }
                                            });
                                        else
                                            setDetailsPage(resp);
                                    } else {
                                        // Find out load more btn is needed and calculate next number on btn 
                                        var itemsCntToPage = ipp * page;
                                        
                                        $scope.needLoadMore = itemsCntToPage < nrOfItems;
                                        $scope.nextnumber = ipp;

                                        if ($scope.needLoadMore && (nrOfItems - itemsCntToPage) < ipp)
                                            $scope.nextnumber = nrOfItems - itemsCntToPage;

                                        // Author page
                                        if ($routeParams.authorName) {
                                            utilService[cacheType].backToNews = -1;

                                            if ($.isEmptyObject(resp.author)) {
                                                utilService.setErrorMsgKey('E0007');
                                                return $location.path('/error');
                                            }

                                            $scope.src = $scope.news[0] && $scope.news[0].authorSrc || (resp.author.image && (resp.author_image_url + $filter('format')(CONFIG.api.feedImgQuery, resp.author.image, 100, 100)));

                                            utilService.curAuthorNews = $scope.news;
                                            utilService[cacheType].author = $scope.author = resp.author;
                                        }
                                        // Main news page and tag news page
                                        else {
                                            // Locally cache current news (main and tag in separate variable)
                                            // So when come back from details page, all loaded news so far can be displayed
                                            $scope.isList = 1;

                                            ng.extend(utilService[cacheType], resp, {
                                                news: $scope.news,
                                                newsPage: page
                                            });
                                        }
                                    }
                                });
                    };

            // Init scope variables
            if (tag)
                $scope.tag = tag.ucfirst();

            if (!$routeParams.postTitle) {
                $scope.hBackUrl = '#' + (utilService.backTo || tagPath);
                utilService.backTo = 0;
            }

            ng.extend($scope, {
                postPath: '#' + tagPath + '/post/',
                appName: CONFIG.ps.app_name.replace(' ', ''),
                twitterAppName: CONFIG.ps.twitter_link.split('/').pop(),
                hideAuthor: CONFIG.getAgentAppCfg('hideNewsAuthor')
            });

            // Init scope functions
            ng.extend($scope, {
                toggleFavourites: favouritesService.toggleFavourites,
                embedVideo: utilService.embedVideo,
                loadMore: function () {
                    page += 1;

                    args.to = page * ipp;
                    args.from = args.to - ipp;

                    getNews(1);
                }
            });

            if (!utilService[cacheType])
                utilService[cacheType] = {};

            if (tag && !$routeParams.postTitle) {
                // Clicked to another tag, not back to current "tagnews"
                if (utilService[cacheType].tag !== tag)
                    utilService[cacheType].backToNews = 0;

                utilService[cacheType].tag = tag;
                $pageTitle.set('Tag: ' + tag + ' - News', true);
                args.tags = tag;
            } else if ($routeParams.authorName) {
                utilService[cacheType].backToNews = 0;
                $scope.hTitle = $scope.authorName = $routeParams.authorName.replace(/-/g, ' ').ucwords();
                $pageTitle.set($scope.authorName);
                args.authors = $scope.authorName;
            } else
                $pageTitle.set('News');

            getNews();
        }
    ]);

})(angular, jQuery); 