(function (ng, $) {
    'use strict';

    ng.module('MobileSite.controllers.Details', [
        'MobileSite.directives.Map'
    ]).controller('detailsCtrl', ['$scope', '$window', '$document', '$route', '$routeParams', '$location', '$timeout', '$sessionStorage', '$filter', '$pageTitle', 'CONFIG', 'EAAPI', 'searchService', 'utilService', 'favouritesService', 'sliderService', '$rootScope',
        function ($scope, $window, $document, $route, $routeParams, $location, $timeout, $sessionStorage, $filter, $pageTitle, CONFIG, EAAPI, searchService, utilService, favouritesService, sliderService, $rootScope) {
            // Init scope variables that needed on page load
            ng.extend($scope, {
                mapControl: {},
                pid: $routeParams.pid
            });

            // Get property by property id
            EAAPI.getProperty($scope.pid)
                    .then(function (resp) {

                        // On error redirect to error page
                        if (!resp || !resp.entry[0])
                            return $location.path('/error');

                        // Init local variables and functions
                        var page = searchService.getPage(),
                                p = $scope.property = ng.copy(resp.entry[0]),
                                pid = $scope.pid,
                                route = $route.current.$$route.originalPath,
                                isLL = $scope.isLL,
                                isFav = route.indexOf('favourites') >= 0,
                                curPath = $location.path(),
                                backPath = isLL && '/landlord/properties' || isFav && '/favourites' || '/property-for-' + (!$scope.isOffice && [1, 4].indexOf(parseInt(p.SR, 10)) >= 0 ? 'sale' : 'rent'),
                                setAddr = function (addr) {
                                    $scope.displayAddress = addr || '';
                                    $pageTitle.set((isLL && 'Landlord - ' || '') + $scope.displayAddress.replace(/<.*?>/g, ''), true);
                                },
                                setTitle = function () {
                                    var road = p.A2,
                                            index = road.indexOf(',');

                                    if (index >= 0)
                                        road = road.substring(0, index);

                                    if (!road && p.A3)
                                        road = p.A3;

                                    $scope.hTitle = road + (p.PC_Short && road.indexOf(p.PC_Short) < 0 && ', ' + p.PC_Short || '');
                                },
                                setDate = function () {
                                    if ((p.SR == 2 || p.SR == 3) && p.date_available) {
                                        d = p.date_available.split('/');

                                        if (d.length === 3) {
                                            var d = new Date(d[2], d[1] - 1, d[0]);

                                            $scope.dateAvailable = $filter('date')(d, 'dd MMM');

                                            if (d.getTime() <= new Date().getTime())
                                                $scope.dateAvailable = 'Now';
                                        }
                                    }
                                },
                                resize = function () {
                                    var timer = $timeout(function () {
                                        $timeout.cancel(timer);
                                        $scope.mapHeight = Math.min(640, Math.min($window.innerWidth, $window.innerHeight) - (!$scope.isPortrait && 150 || 50));
                                    }, 0);
                                },
                                onBackBtnPressed = function () {
                                    // InAppBrowser loading
                                    if ($rootScope.loader.iab)
                                        $rootScope.loader.iab.close();
                                    // If fullscreen view active, leave fullscreen
                                    else if ($('#propertyGallery').hasClass('flex-fullscreen'))
                                        $('#propertyGallery').data('flexslider').vars.slideClick();
                                    // Floorplan pinch-zoom close
                                    else if ($('.ng-pinch-zoom').hasClass('ng-pinch-zoom-fullscreen'))
                                        $('.ng-pinch-zoom').trigger('panzoomend');
                                    // Back to previous page
                                    else {
                                        var search = {
                                            pid: pid
                                        };
                                        if (page > 1)
                                            search.p = page;

                                        $scope.$evalAsync(function () {
                                            $location.search(search).path(backPath);
                                        });
                                    }
                                };

                        // Init scope variables
                        if (p.SR == 1)
                            $scope.duty = $filter('currency')(utilService.calcStampDuty(p.PR).duty, p.CUR, 0);

                        ng.extend($scope, {
                            fid: 'p' + pid,
                            hBackUrl: '#' + backPath + '?pid=' + pid + (page > 1 ? '&p=' + page : '')
                        });

                        // Init scope functions
                        ng.extend($scope, {
                            toggleFavourites: favouritesService.toggleFavourites,
                            onClickToStampDutyPage: function () {
                                utilService.backTo = curPath;

                                searchService.setStampDutyData({
                                    pid: pid,
                                    price: p.PR,
                                    sr: p.SR
                                });
                                $location.path('/stamp-duty-calculator');
                            },
                            onFooterLinkClick: function (method, pid) {
                                utilService.backTo = curPath;

                                $location.path('/' + method + '/' + pid);
                            }
                        });

                        // Page logic

                        // Set header title
                        setTitle();

                        // Get image urls and init slider
                        $scope.images = EAAPI.getPropImages(p);
                        $scope.noImg = $scope.images[0] === CONFIG.blankPropImg;

                        var timer1 = $timeout(function () {
                            $timeout.cancel(timer1);
                            sliderService.init(0, isLL && 'landlord' || '', !$scope.noImg);
                        }, 1);

                        // Set display address and page title
                        if ($scope.isOffice)
                            setAddr(p.A2);
                        else
                            utilService
                                    .formatAddress(p)
                                    .then(setAddr);

                        // Parse description
                        p.DS = utilService.decodeHtml(p.DS);

                        // Parse date available
                        setDate();

                        if (isLL) {
                            // On landlord page get landlord documents
                            EAAPI.getLLDocument(pid)
                                    .then(function (data) {
                                        var resp = ng.copy(data);

                                        $scope.lLDocuments = [];

                                        if (resp) {
                                            for (var i = 0, l = resp.length; i < l; ++i) {
                                                var docs = resp[i].files.documents,
                                                        dl = docs.length,
                                                        j = 0;

                                                if (dl > 0) {
                                                    for (; j < dl; ++j) {
                                                        docs[j].uplFDate = $filter('date')(Date.parse(docs[j].modified), 'dd/MM/yyyy');
                                                        docs[j].uplHTime = $filter('humanTime')(docs[j].modified);
                                                    }

                                                    $scope.lLDocuments.push(resp[i]);
                                                }
                                            }

                                            $scope.fileUrl = resp[0].files.file_url;
                                        }
                                    });

                            $scope.onClickStatus = function () {
                                var instance = $('#grid-cont.landlord .propertyGallery').data('flexslider');
                                instance && instance.vars.slideClick();
                            };
                        }
                        // When not landlord page, get pdfs,
                        // calculate map height and init resize event
                        else {
                            // Parse tenant fees info
                            $scope.tenantFeesInfo = utilService.decodeHtml(CONFIG.ps.text_tenantfeesinfo);

                            if (parseInt(p.media_document, 10) > 0) {
                                // Don't need to check for "media_document" type, because the feed will check for us
                                $scope.pdfs = EAAPI.getImages({
                                    pid: pid,
                                    imagecount: p.media_document,
                                    type: 'brochurelow'
                                });
                            } else if (utilService.isUrl(p.media_document))
                                $scope.mediaPdf = p.media_document;

                            resize();

                            // Resize event listener
                            $($window).on('resize', resize);
                        }

                        if (parseInt(p.brochure_picture, 10) > 0)
                            $scope.pdfsHigh = isLL && 1 || EAAPI.getImages({
                                pid: pid,
                                imagecount: p.brochure_picture,
                                type: 'brochurehigh'
                            });
                        // Init virtual walkthrough
                        else if ([
                            '360-grad-immobilien.com', // 870
                            'api.inspiredlabs.com', // 2
                            'my.matterport.com', // 859 839 36 2
                            'propertytours.tv', // 891 876 859 837 824 572
                            'storage.net-fs.com' // 456
                        ].indexOf(p.brochure_picture, 0, 1, 1) >= 0)
                            $scope.virtWalk = 1;
                        // Embed video
                        else if (p.brochure_picture) {
                            p.brochure_picture = utilService.embedVideo(p.brochure_picture);

                            // On android open video in a seperate page and not in InAppBrowser
                            if ($scope.isAndroid)
                                p.brochure_picture = utilService.encodeUri(p.brochure_picture, '#' + curPath);
                        }

                        // Get floorplans
                        if (p.floorplans > 0)
                            $scope.floorplans = EAAPI.getImages({
                                pid: pid,
                                imagecount: p.floorplans,
                                type: 'floorplan'
                            });

                        // Get epcs
                        if (p.epc > 0)
                            EAAPI.getFiles({
                                pid: pid,
                                imagecount: p.epc,
                                type: 'epc'
                            }).then(function (resp) {
                                $scope.epcs = resp;
                            });

                        // When branch exists or is a Serviced Offices Client,
                        // then get branch
                        if (p.office || $scope.isOffice)
                            EAAPI.getBranches(!$scope.isOffice && p.office)
                                    .then(function (resp) {
                                        $scope.branch = resp[0] || resp;
                                    });

                        // When is a Serviced Offices Client get the team
                        if ($scope.isOffice)
                            EAAPI.getTeam()
                                    .then(function (resp) {
                                        ng.extend($scope, {
                                            team: resp.team.filter(function (i) {
                                                return i.branch_id === pid;
                                            }) || false,
                                            imgUrl: resp.image_url
                                        });
                                    });

                        // Event listeners
                        // Device back button pressed
                        $document[0].addEventListener("backbutton", onBackBtnPressed, false);

                        $scope.$on('$destroy', function () {
                            if (!isLL)
                                $($window).off('resize', resize);

                            $document[0].removeEventListener('backbutton', onBackBtnPressed, false);
                            sliderService.destroy(route);
                        });

                        // When property loaded log property detail page access
                        var h = $scope.history,
                                prevHItemPath = h[h.length - 2] && h[h.length - 2].originalPath;

                        if (CONFIG.ps.incl_log_property_detail == 1 && !isFav && (prevHItemPath === backPath || !prevHItemPath)) {
                            var args = {
                                runInBg: 1,
                                property_id: pid
                            }, eaLogin = $sessionStorage.eaLogin && $sessionStorage.eaLogin[CONFIG.ps.agent_id];

                            if (eaLogin && eaLogin.user_id)
                                args.user_id = eaLogin.user_id;

                            EAAPI.get('logDetail', args, true);
                        }

                    });
        }
    ]);
})(angular, jQuery); 