(function (ng) {
    'use strict';
    ng.module('MobileSite.services.PushNotification', [
    ]).service('notiService', ['$rootScope', '$window', '$http', '$localStorage', '$location', '$route', 'CONFIG', 'EAAPI', 'ngToast', 'platform',
        function ($rootScope, $window, $http, $localStorage, $location, $route, CONFIG, EAAPI, ngToast, platform) {
            var me = this,
                    push,
                    registrationId,
                    callReg,
                    reInitPushPlugin,
                    cache = $localStorage.eaCache[CONFIG.ps.agent_id],
                    notiCacheKey = CONFIG.api.feedMap.notifications,
                    initFn = function (cb) {
                        return CONFIG.notification.enabled && (platform.android || platform.ios) && $window.PushNotification ? cb : ng.noop;
                    },
                    initPushPlugin = function () {

                        // TODO: check hasPermision aka push not is enabled or not - it is difficult - worth it?

                        push = $window.PushNotification.init({
                            android: {
                                senderID: CONFIG.google.senderId
                            },
                            ios: {
                                alert: true,
                                badge: true,
                                sound: true
                            }
                        });
                        push.on('registration', function (data) {
                            reInitPushPlugin = false;
                            registrationId = data.registrationId;

                            if (callReg) {
                                me.reg();
                            }
                        });
                        push.on('notification', function (data) {
                            // data.message,
                            // data.title,
                            // data.count,
                            // data.sound,
                            // data.image,
                            // data.additionalData
                            if (data.count)
                                push.setApplicationIconBadgeNumber(ng.noop, ng.noop, data.count);
                            var extra = data.additionalData;
                            // App started with noti from notification center
                            // On iOS just from phonegap-plugin-push 1.6.0 version
                            if (extra.coldstart)
                                processNoti(1);
                            else {
                                // extra.foreground = true - Notification arrived while using the app
                                // extra.foreground = false - App resumed with noti from notification center (app was in background)
                                if (extra.foreground)
                                    ngToast.create(CONFIG.MESSAGE.M0006);
                                else
                                    $window.resumedWithNoti = true;
                                processNoti();
                            }
                        });
                        push.on('error', function (e) {

                            // Newly installed app, tried to register the device,
                            // and probably failed, because there is no internet
                            // connection yet, or some push server error, but
                            // it is not verifiable
                            // When the device once already registered, the
                            // registration id can retrieved without connection
                            // (aka the app is in offline state), so don't throw
                            // this error
                            if (
                                    e &&
                                    (
                                            e.message === 'SERVICE_NOT_AVAILABLE' ||
                                            e.message === 'TIMEOUT'
                                            )
                                    ) {
                                reInitPushPlugin = true;
                            }
                        });
                    },
                    processNoti = function (helper) {
                        EAAPI.getNotifications(true)
                                .then(function (resp) {
                                    if ($location.path() === '/notifications') {
                                        $route.reload();
                                    } else if (helper === 1) {
                                        $location.path('/notifications');
                                    } else {
                                        me.refreshNotiCnt(resp);
                                    }
                                });
                    };
            // Init push plugin
            initFn(initPushPlugin)();
            // Function register the device token into the DB
            me.reg = initFn(function (rnCnt) {

                if (rnCnt) {
                    me.refreshNotiCnt();
                }
                // When registration called before the push registration event
                // handler (i think this is almost impossible, but may occur 
                // in rare cases, when the app is fully up to date), set a flag
                // (callReg) to the push plugin registration event handler call
                // the registration method, or push plugin initialization failed
                // (@see the push plugin error event handler for explanation)
                // and need to reinit the push plugin (reInitPushPlugin), in order
                // to the push plugin can register the device
                if (reInitPushPlugin || !registrationId) {
                    callReg = true;
                    return reInitPushPlugin && initPushPlugin();
                }

                if (!me.registered && !$rootScope.isOffline) {
                    if (CONFIG.ps.registration_id !== registrationId) {

                        me.regInProgress = true;
                        $http.get('https://123app.co.uk/pushnotifications_admin/register_multi_platform.php', {
                            params: {
                                platform: $window.device && $window.device.platform.toLowerCase() || null,
                                source: CONFIG.api.source,
                                agentid: CONFIG.ps.agent_id,
                                uuid: $window.device.uuid,
                                devicetoken: registrationId
                            }
                        }).then(function (resp) {
                            if (resp && resp.data && resp.data.success) {
                                var ps = cache.app_property_settings;
                                ps.data = LZString.compressToUTF16(JSON.stringify(ng.extend(JSON.parse(LZString.decompressFromUTF16(ps.data)), {registration_id: registrationId})));
                                me.registered = true;
                            }

                            me.regInProgress = false;
                            //ngToast.create(CONFIG.MESSAGE.M0005);
                        }, function () {
                            me.regInProgress = false;
                        });
                    } else {
                        me.registered = true;
                    }
                }
            });

//            me.unReg = initFn(function () {
//                if (push)
//                    push.unregister(ng.noop, ng.noop);
//            });
            // Function clear the app icon badge number and notifications from noti center.

            me.clear = initFn(function () {
                push.setApplicationIconBadgeNumber(ng.noop, ng.noop, 0);
                if (push.clearAllNotifications)
                    push.clearAllNotifications();
            });

            me.processNoti = initFn(processNoti);

            me.showNotifications = initFn(function (a) {
                // Show notification link if is Android or iOS device and there are notifications
                return cache && cache[notiCacheKey] && JSON.parse(LZString.decompressFromUTF16(cache[notiCacheKey].data)).length;
            });

            me.refreshNewest = initFn(function () {
                var d = 0;
                if (
                        cache &&
                        cache[notiCacheKey] &&
                        (d = JSON.parse(LZString.decompressFromUTF16(cache[notiCacheKey].data))[0] || 0)
                        ) {
                    d = (d.date + " " + d.time).match(/^(\d{2})\/(\d{2})\/(\d{4}) (\d{2}):(\d{2}):(\d{2})/);
                    d = new Date(d[3], d[2] - 1, d[1], d[4], d[5], d[6]).getTime();
                }
                $localStorage.eaNotifications[CONFIG.ps.agent_id].newest = d;
            });

            me.refreshNotiCnt = initFn(function (notis) {
                var noti = $localStorage.eaNotifications[CONFIG.ps.agent_id];
                notis = ng.isDefined(notis) && notis || cache && cache[notiCacheKey] && JSON.parse(LZString.decompressFromUTF16(cache[notiCacheKey].data));
                noti.counter = notis && notis.filter(function (i) {
                    var d = (i.date + " " + i.time).match(/^(\d{2})\/(\d{2})\/(\d{4}) (\d{2}):(\d{2}):(\d{2})/);
                    return new Date(d[3], d[2] - 1, d[1], d[4], d[5], d[6]).getTime() > noti.lastReaded;
                }).length || 0;
            });

            me.initNoti = initFn(function (reset) {
                if (!$localStorage.eaNotifications)
                    $localStorage.eaNotifications = {};
                if (reset || !$localStorage.eaNotifications[CONFIG.ps.agent_id]) {
                    if (!$localStorage.eaNotifications[CONFIG.ps.agent_id])
                        $localStorage.eaNotifications[CONFIG.ps.agent_id] = {};
                    if (!$localStorage.eaNotifications[CONFIG.ps.agent_id].newest)
                        me.refreshNewest();
                    // Set "lastReaded" the current newest value
                    ng.extend($localStorage.eaNotifications[CONFIG.ps.agent_id], {
                        counter: 0, // new noti counter value
                        lastReaded: $localStorage.eaNotifications[CONFIG.ps.agent_id].newest
                    });
                }
            });

            // Call init noti here once (because the service is singleton),
            // on the service initialization to generate noti object in
            // local storage if needed
            me.initNoti();
        }
    ]);
})(angular);