(function (ng) {
    'use strict';

    ng.module('MobileSite.services.Form', [
    ]).service('formService', ['$q', '$route', '$location', '$pageTitle', 'EAAPI', 'utilService', 'searchService',
        function ($q, $route, $location, $pageTitle, EAAPI, utilService, searchService) {
            var me = this;

            me.onSubmit = function ($scope, isValid, method, args, formName, successNr, pathTo, errNr, field) {
                var deferred = $q.defer();

                if (isValid)
                    EAAPI.get(method, args, true)
                            .then(function (resp) {
                                field = field || 'email';

                                // Error
                                if (ng.isString(resp) && resp !== 'OK') {
                                    $scope[formName][field].$errorMsg = errNr && $scope.cfg.ERROR[errNr] || resp;

                                    deferred.reject();
                                }
                                // Success
                                else {
                                    $scope.success = true;

                                    deferred.resolve(resp);

                                    // Show toast
                                    if (successNr)
                                        setTimeout(function () {
                                            $scope.$evalAsync(function () {
                                                utilService.toast($scope.cfg.MESSAGE[successNr]);
                                            });
                                        }, 0);

                                    // Redirect
                                    if (pathTo)
                                        setTimeout(function () {
                                            if (pathTo === 'self')
                                                $route.reload();
                                            else
                                                $scope.$evalAsync(function () {
                                                    $location.path(pathTo);
                                                });
                                        }, successNr && 250 || 0);
                                }
                            });
                else
                    deferred.reject();

                return deferred.promise;
            };

            me.getPTL = function ($scope, type, valueField, modelItemName, getItems) {
                var singularType = type.slice(0, -1),
                        ucfType = type.ucfirst();

                modelItemName = modelItemName || singularType.toLowerCase();
                getItems = getItems || !ng.isDefined(getItems) && 1;

                if (!$scope.isMobile) {
                    searchService.selectDesktopMultiItems($scope[type], $scope.model[modelItemName], valueField);

                    $scope['on' + singularType.ucfirst() + 'ItemClick'] = function (data) {
                        searchService.onDesktopMultiItemClick(data, $scope[type], $scope['selected' + ucfType], valueField);
                    };
                }

                if (getItems)
                    EAAPI['get' + ucfType]()
                            .then(function (resp) {
                                $scope[type] = $scope[type].concat(ng.copy(resp));

                                if (!$scope.isMobile)
                                    searchService.selectDesktopMultiItems($scope[type], $scope.model[modelItemName], valueField);

                            });
            };

            me.AVS = function ($scope, pid) {
                var deferred = $q.defer();

                EAAPI.getProperty(pid)
                        .then(function (resp) {
                            if (!resp || !resp.entry[0]) {
                                deferred.reject();
                                return $location.path('/error');
                            }

                            var p = $scope.property = resp.entry[0];

                            utilService.formatAddress(p)
                                    .then(function (tpl) {
                                        $scope.displayAddress = tpl;
                                        $pageTitle.set('Share ' + tpl.replace(/<.*?>/g, ''), true);
                                    });

                            $scope.hBackUrl = '#' + (utilService.backTo || ('/property-for-' + ([1, 4].indexOf(parseInt(p.SR, 10)) >= 0 ? 'sale' : 'rent') + '/' + pid));
                            utilService.backTo = 0;

                            deferred.resolve();
                        });

                return deferred.promise;
            };

        }
    ]);
})(angular);