(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.Redirect', [
    ]).controller('redirectCtrl', ['$rootScope', '$routeParams', '$location', '$sessionStorage', 'CONFIG', 'EAAPI',
        function ($rootScope, $routeParams, $location, $sessionStorage, CONFIG, EAAPI) {
            $rootScope.$broadcast('loadingStarted');

            var pn = $routeParams.pathname || '',
                    doRedirectToProperty = function (pid) {
                        EAAPI.get('get_property_id', {property_id: pid}, true)
                                .then(function (resp) {
                                    if (resp && resp.pid) {
                                        EAAPI.getProperty(resp.pid)
                                                .then(function (resp2) {
                                                    if (!resp2 || !resp2.entry[0])
                                                        return doRedirectToHome();

                                                    doRedirect('/property-for-' + ([1, 4].indexOf(parseInt(resp2.entry[0].SR, 10)) >= 0 ? 'sale' : 'rent') + '/' + resp.pid);
                                                });
                                    } else
                                        doRedirectToHome();
                                });
                    },
                    doRedirectToHome = function () {
                        var method = $rootScope.isCommercial && !$rootScope.isOffice ? '/login' : '';

                        if (ng.isDefined($sessionStorage.eaLogin) && ng.isDefined($sessionStorage.eaLogin[CONFIG.ps.agent_id]))
                            method = '';

                        if ($routeParams.appurl && $routeParams.device)
                            return doRedirect(method, {
                                appurl: $routeParams.appurl,
                                device: $routeParams.device
                            });

                        doRedirect(method);
                    },
                    doRedirect = function (path, search) {
                        search = search || {};
                        $rootScope.$broadcast('loadingComplete');
                        $location.search(search).path(path);
                    };

            if (pn === 'viewsingleprop.php') {
                if ($routeParams.pid)
                    doRedirectToProperty($routeParams.pid);
                else
                    doRedirectToHome();
            } else if (pn.indexOf('news/') === 0) {
                var slug = pn.replace(/\/$/, "").split('/')[1];
                if (slug)
                    doRedirect('/news/post/' + slug);
                else
                    doRedirectToHome();
            } else {
                var pid = ($routeParams.propid || pn).split('_');
                doRedirectToProperty(pid[1] || pid[0]);
            }
        }
    ]);

})(angular); 