(function (ng, $) {
    'use strict';

    ng.module('MobileSite.controllers.LandlordProperties', [])
            .controller('llPropCtrl', ['$scope', '$window', '$location', '$timeout', '$routeParams', 'CONFIG', 'EAAPI', 'searchService', 'utilService', 'ngToast',
                function ($scope, $window, $location, $timeout, $routeParams, CONFIG, EAAPI, searchService, utilService, ngToast) {
                    var nsPath = 'landlord/',
                            ipp = CONFIG.itemPerPage,
                            pageNr = parseInt($routeParams.p, 10),
                            ppid = $routeParams.pid,
                            append = 0,
                            promises = [],
                            onGetLLPropsFail = function () {
                                ngToast.create(CONFIG.ERROR.E0019);
                                $scope.onClickLLLogout();
                            },
                            getLLProps = function () {
                                return EAAPI.getProperties($scope.model, !!ppid && !append, true)
                                        .then(function (data) {
                                            var resp = ng.copy(data);

                                            if (resp && ng.isDefined(resp.nrofprops)) {
                                                resp.entry.forEach(function (item) {
                                                    item.pImg = EAAPI.getPropImages(item, 1);
                                                });

                                                var vars = searchService.onGetPropsSuccess($scope, resp, ipp, pageNr, append, ppid);

                                                pageNr = vars.pageNr;
                                                ppid = vars.ppid || ppid;
                                            } else
                                                onGetLLPropsFail();
                                        }, onGetLLPropsFail);
                            },
                            setPageLimits = function () {
                                $scope.model.to = pageNr * ipp;
                                $scope.model.from = $scope.model.to - ipp;
                            },
                            resize = function () {
                                // Dinamically calculate the properties ct top offset
                                var $filtersMenu = $('#filters-menu'),
                                        menuHeight = $filtersMenu.height(),
                                        $welcomeItems = $filtersMenu.find('.welcome'),
                                        $firstWItem = $welcomeItems.eq(0),
                                        $sep = $filtersMenu.find('.separator');

                                if (menuHeight)
                                    $scope.ctOffset = 48 + menuHeight;

                                if (($welcomeItems.length <= 1 || $firstWItem.position().top < $welcomeItems.eq(1).position().top)) {
                                    $firstWItem.css('padding', '0 7px');
                                    $sep.hide();
                                } else {
                                    $firstWItem.css('padding', 0);
                                    $sep.show();
                                }

                                // Calc landlord properties image height
                                $scope.imgH = $window.innerWidth * 0.3;
                            };

                    // Init scope variables
                    ng.extend($scope, {
                        route: nsPath + 'property',
                        model: {}
                    });

                    // Init scope functions
                    ng.extend($scope, {
                        getPropImages: EAAPI.getPropImages,
                        scrollTo: function (last) {
                            if (last && ppid) {
                                var delay = $scope.props.length * 25;
                                utilService.scrollTo(ppid, append && 50 || (delay < 1000 && 1000 || delay));
                            }

                            if (last) {
                                append = 0;
                                ppid = 0;
                            }
                        },
                        loadMore: function () {
                            pageNr += 1;
                            $location.search('p', pageNr);
                            searchService.setPage(pageNr);
                            setPageLimits();

                            append = 1;
                            getLLProps();
                        }
                    });

                    // Controller logic
                    pageNr = ng.isNumber(pageNr) && !isNaN(pageNr) && pageNr || 1;
                    setPageLimits();

                    // Get landlord properties
                    promises.push(getLLProps());

                    // Get landlord settings to get landlord name
                    promises.push(EAAPI.getLLSettings()
                            .then(function (resp) {
                                if (resp) {
                                    var llName = [],
                                            setLLName = function (piece, join) {
                                                if (piece)
                                                    llName.push(piece + (join || ''));
                                            };

                                    setLLName(resp.LL_Title, '.');
                                    setLLName(resp.LL_FirstName);
                                    setLLName(resp.LL_Surname);

                                    $scope.llName = llName.join(' ');
                                }
                            }));

                    // When all content loaded call resize fn
                    utilService.loadAsyncData(promises)
                            .then(function () {
                                $timeout(resize, 0);
                            });

                    // Event listeners
                    $($window).on('resize', resize);

                    $scope.$on('$destroy', function () {
                        $($window).off('resize', resize);
                    });
                }
            ]);
})(angular, jQuery);