(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.Alert', [
    ]).controller('alertCtrl', ['$scope', '$sessionStorage', 'CONFIG', 'EAAPI', 'utilService', 'searchService', 'formService',
        function ($scope, $sessionStorage, CONFIG, EAAPI, utilService, searchService, formService) {
            var eaLogin = $sessionStorage.eaLogin ? $sessionStorage.eaLogin[CONFIG.ps.agent_id] : {},
                    filter = {},
                    locationValueField = 'location',
                    propTypeValueField = 'id',
                    unbind;

            if (searchService.getNeedAlertFilter()) {
                searchService.setNeedAlertFilter(false);
                filter = ng.copy(searchService.getLastFilter());
            }

            ng.extend($scope, {
                hTitle: CONFIG.getAgentAppCfg('alertsPageTitle'),
                isReqTel: CONFIG.telephoneFieldRequired.alerts === true,
                salesTypes: utilService.getSalesTypes(),
                propTypes: [{id: '0', name: 'Any'}],
                beds: [utilService.getBeds(), utilService.getBeds(true)]
            });

            $scope.model = {
                type: filter.goal || $scope.salesTypes[0].goal,
                proptype: filter.type || $scope.propTypes[0].id,
                beds: filter.bd || $scope.beds[0][0].id,
                name: eaLogin.user_fullname || '',
                email: eaLogin.user_email || '',
                tel: eaLogin.user_mobile || ''
            };

            ng.extend($scope, {
                onGoalChange: function (goal) {
                    $scope.goal = goal.type;
                    $scope.model.minprice = filter.pmin || $scope.prices[goal.type].min[0].id;
                    $scope.model.maxprice = filter.pmax || $scope.prices[goal.type].max.slice(-1).pop().id;
                    $scope.freq = goal.period;
                    filter.pmax = '';
                    $scope.isStudentRent = +($scope.model.type === 3);
                },
                onChangeEmail: function () {
                    $scope.alertF.email.$errorMsg = null;
                },
                onSubmit: function (isValid) {
                    if (isValid) {
                        $scope.model.proptype = searchService.toCommaString($scope.selectedPropTypes, propTypeValueField);
                        $scope.model.location = searchService.toCommaString($scope.selectedLocations, locationValueField);

                        formService.onSubmit($scope, isValid, 'alert', $scope.model, 'alertF', 'M0008', 'self');
                    }
                }
            });

            /************* Locations *************/

            if (!CONFIG.enablePredictiveLocationsMenu) {
                $scope.locations = [{location: '', display_name: 'All'}];
                $scope.selectedLocations = [];
                $scope.model.location = filter.location || $scope.locations[0].location;

                formService.getPTL($scope, 'locations', locationValueField);
            } else {
                $scope.pauseLocationWatcher = true;

                unbind = $scope.$watch('location', function (n) {
                    if ($scope.pauseLocationWatcher)
                        return $scope.pauseLocationWatcher = false;

                    searchService.onLocationChange($scope.model, n);
                });
            }

            /************* Prices *************/

            EAAPI.getPrices({all: true})
                    .then(function (resp) {
                        $scope.prices = resp;
                        $scope.onGoalChange($scope.salesTypes.filter(function (i) {
                            return i.goal === $scope.model.type;
                        })[0] || $scope.salesTypes[0]);
                    });

            /************* Property Types *************/

            $scope.selectedPropTypes = [];

            formService.getPTL($scope, 'propTypes', propTypeValueField);

            if (unbind)
                $scope.$on('$destroy', unbind);
        }
    ]);

})(angular); 