(function (ng) {
    'use strict';

    ng.module('MobileSite.filters.App', [
    ]).filter('formatUrlFriendlyString', [
        function () {
            return function (str, replace, delimiter) {
                if (!str)
                    return str;

                delimiter = delimiter || '-';

                str = str.stripTags();

                if (replace)
                    str = str.replace(replace, ' ');

                return str
                        .removeDiacritics()
                        .replace(/[^a-zA-Z0-9\/_|+ -]/g, '')
                        .replace(/^-|-$/g, '')
                        .replace(/[\/_|+ -]+/g, delimiter)
                        .toLowerCase();
            };
        }
    ]).filter('postDate', ['$filter',
        function($filter) {
            return function(input, format) {
                if (!input)
                    return "";

                if (!ng.isDate(input) && !ng.isNumber(input)) {
                    input = input.match(/^(\d{4})-(\d{2})-(\d{2})/);
                    input = new Date(input[1], input[2] - 1, input[3]);
                }
                var suffixes = ["th", "st", "nd", "rd"],
                        dtfilter = $filter('date')(input, format || 'd MMM yy'),
                        day = parseInt(dtfilter.slice(0, 2), 10),
                        relevantDigits = (day < 30) ? day % 20 : day % 30,
                        suffix = (relevantDigits <= 3) ? suffixes[relevantDigits] : suffixes[0];
                return day + suffix + ' ' + dtfilter.replace(/^\d{1,2}\W/, '');
            };
        }
    ]).filter('humanTime', function () {
        return function (input) {
            if (!input)
                return "";

            if (ng.isDate(input))
                input = input.getTime();
            else if (!ng.isNumber(input)) {
                input = input.match(/^(\d{4})-(\d{2})-(\d{2}) (\d{2}):(\d{2}):(\d{2})/);
                input = new Date(input[1], input[2] - 1, input[3], input[4], input[5], input[6]).getTime();

                if (isNaN(input))
                    return '';
            }

            var time = new Date().getTime() - input,
                    numberOfUnits,
                    tokens = [
                        [31536000000, 'year'],
                        [2592000000, 'month'],
                        [604800000, 'week'],
                        [86400000, 'day'],
                        [3600000, 'hour'],
                        [60000, 'minute'],
                        [1000, 'second']
                    ],
                    i = 0,
                    l = tokens.length;


            for (; i < l; ++i) {
                if (time >= tokens[i][0]) {
                    numberOfUnits = Math.floor(time / tokens[i][0]);
                    return numberOfUnits + ' ' + tokens[i][1] + (numberOfUnits > 1 ? 's' : '') + ' ago';
                }
            }
        };
    }).filter('truncate', function () {
        return function (input, width) {
            width = width || 155;
            if (input.length > width) {

                var parts = input.split(/[\s\r\n]+/),
                        c = parts.length, l = 0, last = 0;

                for (; last < c; ++last) {
                    l += parts[last].length;
                    if (l > width)
                        break
                }

                return parts.slice(0, last).join(' ').trim() + '...';
            }
            return input;
        };
    }).filter('format', function () {
        return function (input) {
            var args = Array.prototype.slice.call(arguments, 1);
            return input.replace(/{(\d+)}/g, function (match, number) {
                return ng.isDefined(args[number]) ? args[number] : match;
            });
        };
    })/*.filter('num', function () {
        return function (input) {
            return parseInt(input, 10);
        };
    })*?
            /*.filter('range', function() {
             return function(input, range) {
             if (!ng.isArray(input) || !ng.isArray(range))
             return input;
             
             range = [+range[1] && +range[0] || 0, +range[1] || +range[0]];
             
             var min = Math.min(range[0], range[1]),
             max = Math.max(range[0], range[1]),
             result = [];
             
             min = min < 0 ? 0 : min;
             max = max > input.length ? input.length : max;
             
             for (var i = min; i < max; i++)
             result.push(input[i]);
             
             return result;
             };
             })*/;
})(angular);