(function (ng) {
    'use strict';

    ng.module('ngPageTitle', [
    ]).provider('$pageTitle', function () {
        var _defaultTitle,
                _appendDefault = false;

        this.setDefault = function (value) {
            _defaultTitle = value;
        };

        this.setAppendDefault = function (value) {
            _appendDefault = value;
        };

        function PageTitleService($rootScope, $window) {
            var _currentTitle;

            function _get() {
                return $window.document.title;
            }

            function _set(value, appendDefault) {
                _currentTitle = value ? (appendDefault ? value + ' - ' + _defaultTitle : value) : _defaultTitle;
                $window.document.title = _currentTitle;
            }

            if (_defaultTitle) {
                $window.document.title = _defaultTitle;
            } else {
                _defaultTitle = $window.document.title;
            }

            // Bind to ng route system.
            $rootScope.$on('$routeChangeSuccess', function (event, route) {
                var _pageTitle;
                if (route && ng.isDefined(route.$$route)) {
                    _pageTitle = route.$$route.pageTitle || null;
                }
                _set(_pageTitle, _appendDefault);
            });

            return {
                get: _get,
                set: _set
            };
        }

        this.$get = ['$rootScope', '$window', PageTitleService];
    });

})(angular); 