(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.Register', [
    ]).controller('registerCtrl', ['$scope', 'EAAPI', 'formService',
        function ($scope, EAAPI, formService) {
            $scope.model = {};

            ng.extend($scope, {
                onChangeEmail: function () {
                    $scope.regF.email.$errorMsg = null;
                },
                onSubmit: function (isValid) {
                    formService.onSubmit($scope, isValid, 'property_register', $scope.model, 'regF', 'M0011', 'login');
                }
            });

            if ($scope.isOffice)
                EAAPI.get('property_offices')
                        .then(function (resp) {
                            $scope.model.office = resp[0] && resp[0].id;
                            $scope.offices = resp;
                        });
        }
    ]);

})(angular);