(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.About', [
    ]).controller('aboutCtrl', ['$scope', 'EAAPI', 'cUtil',
        function ($scope, EAAPI, cUtil) {
            EAAPI.getAbout()
                    .then(function (resp) {
                        ng.extend($scope, {
                            hTitle: 'About Us',
                            about: resp
                        });

                        if (resp.about_us_image) {
                            var ns = 'about_us_image',
                                    appUpdateImages = function () {
                                        cUtil.getFile(resp.about_us_image, resp.about_us_image_url + '&file=' + resp.about_us_image + '&width=350&height=350', ns, $scope.imagesDownloaded[ns], function (img, downloaded) {
                                            $scope.$evalAsync(function () {
                                                if (downloaded)
                                                    $scope.imagesDownloaded[ns] = downloaded;
                                                $scope.aboutImg = img;
                                            });
                                        });
                                    },
                                    unbind = $scope.$on('appUpdateImages', appUpdateImages);

                            appUpdateImages();
                            $scope.$on('$destroy', unbind);
                        }
                    });
        }
    ]);

})(angular); 