(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.Offline', [
    ]).controller('offlineCtrl', ['$scope', '$document', '$location',
        function ($scope, $document, $location) {
            var ev = 'online.offlineCtrl';
            $scope.hTitle = 'No Internet Connection';
            $scope.retry = function () {
                var h = $scope.history;
                $location.path(h && h.length && h[h.length - 1] && h[h.length - 1].path || '/');
            };

            $document.on(ev, $scope.retry);
            $scope.$on('$destroy', function () {
                $document.off(ev, $scope.retry);
            });
        }
    ]);

})(angular); 