(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.Login', [
    ]).controller('loginCtrl', ['$scope', '$window', '$localStorage', '$location', '$route', 'CONFIG', 'authService', 'formService', 'openFBService',
        function ($scope, $window, $localStorage, $location, $route, CONFIG, authService, formService, openFBService) {
            var nsLLPath = 'landlord/',
                    fn = 'app_login',
                    setStorages = function (data) {
                        authService.setSS(data);

                        if (!$scope.isOffline)
                            authService.setLS(data);
                    }

            $scope.login = {
                user: '',
                pass: ''
            };

            if ($scope.isLL) {
                fn = nsLLPath + 'login';

//                $scope.login.user = 'support@estateapps.co.uk';
//                $scope.login.pass = 'dkd121';
//                $scope.login.user = 'sales@estateapps.co.uk';
//                $scope.login.pass = 'dkd121';
            } else {
                $scope.GETParam = $route.current.params;
                $scope.showModal = $scope.GETParam.appurl && $scope.GETParam.device;
  
//                $scope.login.user = 'ciarakelly';
//                $scope.login.pass = 'BE1993offices2017';
//                $scope.login.user = 'sales@estateapps.co.uk';
//                $scope.login.pass = 'dkd121';

                if (($scope.hasFB = openFBService.ready())) {
                    $scope.onFbLogin = function () {
                        if (!$scope.isOffline)
                            openFBService.login({scope: 'public_profile,email'})
                                    .then(function (resp) {
                                        var obj = resp.authResponse;

                                        setStorages(obj);

                                        // Get user info
                                        openFBService.api({
                                            path: '/me',
                                            params: {
                                                fields: 'name,email'
                                            }
                                        }).then(function (resp) {
                                            obj.user_id = resp.id;
                                            obj.user_fullname = resp.name;

                                            if (resp.email)
                                                obj.user_email = resp.email;

                                            setStorages(obj);
                                        });

                                        $location.path($scope.mainPath);
                                    }, function () {
                                        authService.onLogout();
                                    });
                    };
                }
            }

            if ($window.device)
                $scope.login.uuid = $window.device.uuid;

            ng.extend($scope, {
                onChangePass: function () {
                    $scope.loginF.pass.$errorMsg = null;
                },
                onSubmit: function (isValid) {
                    if (isValid) {
                        var args = {};

                        if ($scope.isLL) {
                            args.email = $scope.login.user;
                            args.password = $scope.login.pass;
                        } else
                            args = $scope.login;

                        formService.onSubmit($scope, isValid, fn, args, 'loginF', 0, 0, 'E0020', 'pass')
                                .then(function (resp) {
                                    var aId = CONFIG.ps.agent_id,
                                            redirectPath = $scope.mainPath;

                                    if ($scope.isLL) {
                                        authService.setSS({
                                            token: resp.token
                                        });

                                        redirectPath = '/' + nsLLPath + 'properties';

                                        // Clear landlord cache
                                        var k, cache = $localStorage.eaCache[aId];

                                        for (k in cache)
                                            if (k.indexOf('getLL') >= 0)
                                                delete cache[k];
                                    } else
                                        setStorages(resp);

                                    $location.path(redirectPath);
                                });
                    }
                }
            });
        }
    ]);

})(angular);