(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.ExtUrl', [
    ]).controller('extUrlCtrl', ['$scope', '$location', '$routeParams', '$sce', '$pageTitle', 'CONFIG', 'utilService',
        function ($scope, $location, $routeParams, $sce, $pageTitle, CONFIG, utilService) {
            if ($scope.isOffline)
                return $location.path('/offline');
            else {
                var parseUrl = $sce.trustAsResourceUrl;

                if ($routeParams.type === 'report-maintenance') {
                    $scope.repair = 1;
                    $scope.hTitle = 'Repair / Maintenance';

                    if (CONFIG.ps.repairform_url)
                        $scope.siteUrl = parseUrl(CONFIG.ps.repairform_url);
                } else {
                    var url = LZString.decompressFromEncodedURIComponent($routeParams.type).split('@@@BACKURL@@@');
                    url[1] && ($scope.hBackUrl = url[1]);
                    url = url[0];

                    utilService.isUrl(url) && ($scope.siteUrl = parseUrl(url));
                }
            }

            if ($scope.hTitle)
                $pageTitle.set($scope.hTitle, true);

            if (!$scope.repair && !$scope.siteUrl)
                $location.path($scope.mainPath);
        }
    ]);
})(angular); 