(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.Testimonials', [
    ]).controller('testimonialsCtrl', ['$scope', '$window', 'EAAPI', 'utilService',
        function ($scope, $window, EAAPI, utilService) {
            $scope.hTitle = 'Testimonials';

            EAAPI.getTestimonials()
                    .then(function (resp) {
                        ng.extend($scope, {
                            embedVideo: utilService.embedVideo,
                            testimonials: resp.testimonials,
                            imgUrl: resp.image_url,
                            postVideoMaxHeight: Math.round($window.innerHeight / ($window.innerWidth <= $window.innerHeight ? 2 : 1.5)) + 'px'
                        });
                    });
        }
    ]);

})(angular); 