(function (ng) {
    'use strict';

    ng.module('MobileSite.controllers.RequestValuation', [
    ]).controller('reqCtrl', ['$scope', '$sessionStorage', 'CONFIG', 'utilService', 'formService',
        function ($scope, $sessionStorage, CONFIG, utilService, formService) {
            var eaLogin = $sessionStorage.eaLogin ? $sessionStorage.eaLogin[CONFIG.ps.agent_id] : {},
                    salesTypes = utilService.getSalesTypes();

            ng.extend($scope, {
                hTitle: CONFIG.getAgentAppCfg('requestValuationPageTitle'),
                isReqTel: CONFIG.telephoneFieldRequired.requestValuation === true,
                salesTypes: [{
                        id: 1,
                        value: 'Selling'
                    }, {
                        id: 2,
                        value: 'Letting'
                    }],
                isSalesOnly: salesTypes.length === 1 && (salesTypes[0].goal === 1 || salesTypes[0].goal === 4)
            });

            $scope.model = {
                Type: $scope.salesTypes[0].id,
                firstname: eaLogin.user_fullname || '',
                email: eaLogin.user_email || '',
                phone: eaLogin.user_mobile || ''
            };

            ng.extend($scope, {
                onChangeEmail: function () {
                    $scope.reqF.email.$errorMsg = null;
                },
                onSubmit: function (isValid) {
                    formService.onSubmit($scope, isValid, 'valuationRequest', $scope.model, 'reqF', 'M0009', 'self');
                }
            });
        }
    ]);

})(angular); 